import { combineReducers } from 'redux';
import { merge } from 'lodash'


const defaultDreportParams = {
  isFetching:false,
  dreportFetched: false,
  searchFetched: false,
  dreportDownloadFetched:false,
  createTitle: 'Create New Dreport',
  createSubmitButtonText: 'Submit',
  editTitle: 'Edit Dreport',
  editSubmitButtonText: 'Submit' 
}


function byId(state = {}, action) {
    if (action.type === 'FETCHED_DREPORT') { 
      return {...state, 
        [action.payload.type] : action.payload.data
      }
    }if(action.type ==='DOWNLOADED_DREPORT'){
      return {...state,
      [action.payload.type]:action.payload.data
      }
    }
     else {
      return state
    }
  }





  // function byId(state = {}, action) {
  //   if (action.type == 'FETCHED_DREPORT') {
  //     const DreportID = action.payload.id
  //     return {
  //       ...state,
  //       [DreportID]: action.payload
  //     }
  //   }
  // }

function params(state = defaultDreportParams, action) {
  if (action.type === 'FETCHED_DREPORT') { 
      return {...state, 
        dreportFetched: true,
        isFetching:false
      }
    }if (action.type === 'DOWNLOADED_DREPORT') { 
      return {...state, 
        dreportDownloadFetched: true,
        isFetching:false
      }
    }
      else {
    return state
  }
}

const dreport = combineReducers({
    byId,
    params
});

export default dreport;

