import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  getEmployee,
  getEmployeeParams,
  getNotification,
} from "../data/selectors";
import { createEmployee, editEmployee, setNotifyDone } from "../data/actions";
import { employeeSchema, duplicateCheckEmployeeName } from "../data/model";
import { Container, Header, Form, Button } from "semantic-ui-react";
import { Formik, Field, Form as FormikForm } from "formik";
import { FormikInputComponent, FormikSelectComponent } from "../../../utilities/formUtils";
import userACL from "../../../store/access";
import Notification from "../../../utilities/notificationUtils";
import LocationSelect from "../../location/components/locationSelect";
import v4 from "uuid";

// import UserDropdown from '../../users/components/userDropdown'

const EmployeeForm = (props) => {
  const employee = useSelector((state) => getEmployee(state, props));
  const params = useSelector((state) => getEmployeeParams(state, props));
  const dispatch = useDispatch();

  const [savedEmployeeId, setSavedEmployeeId] = useState(false);

  const saveEmployee = (values, resetForm) => {
    if (props.match.path === "/employee/create") {
      values.id = v4();
      userACL.atCreate(values);
      // values.segid = values.cid
      values.rlb_box_id = values.boxId;
      delete values.txnid;
      delete values.boxId;
      dispatch(createEmployee(values));
      setSavedEmployeeId(values.id);
      resetForm();
    }

    if (props.match.path === "/employee/edit/:id") {
      userACL.atUpdate(values);
      // values.segid = values.cid
      values.rlb_box_id = values.boxId;
      delete values.txnid;
      delete values.boxId;
      dispatch(editEmployee(values));
      setSavedEmployeeId(values.id);
      setTimeout(function () {
        props.history.push(`/employee/list`);
      }, 1000);
    }
  };

  return (
    <Container>
      <Header as="h1">{params.title}</Header>
      <Formik
        id="employeeForm"
        size="large"
        width={5}
        initialValues={employee}
        validationSchema={employeeSchema}
        onSubmit={(values, { resetForm }) => saveEmployee(values, resetForm)}
        render={({ handleSubmit, onChange, values, handleChange }) => (
          <Form
            as={FormikForm}
            size="small"
            className="CustomeForm"
            width={5}
            onSubmit={handleSubmit}
          >
            <Form.Group widths={3}>
              <Field
                name="emp_name"
                label="Name"
                component={FormikInputComponent}
                isMandatory={true}
                validate={(val) => duplicateCheckEmployeeName(val, values)}
              ></Field>
            </Form.Group>
            <Form.Group widths={3}>
              <Field
                name="emp_code"
                label="Code"
                component={FormikInputComponent}
              ></Field>
            </Form.Group>
            <Form.Group widths={3}>
              <LocationSelect
                name="loc_id"
                label="Location"
                isTxn="false"
                isSelection={true}
              />
            </Form.Group>
            <Button
              type="submit"
              size="medium"
              color="blue"
              className="CustomeBTN"
            >
              {" "}
              {params.submitButtonText}{" "}
            </Button>
            {savedEmployeeId ? (
              <Notification
                id={savedEmployeeId}
                notifySelector={getNotification}
                notifyDoneAction={setNotifyDone}
                type="save"
              />
            ) : null}
          </Form>
        )}
      />
    </Container>
  );
};

export default EmployeeForm;
