export const itemsSchema = Yup.object().shape({

    id: Yup.string()
        .required('ID is a Required Field'),

    code: Yup.string()
        .min(4, 'Too Short! Minimum 4 Characters for Code is needed')
        .max(35, 'Too Long! Maximum 35 Characters for Code is allowed')
        .required('Code is (Required)'),

    name: Yup.string()
        .min(2, 'Too Short! Minimum 2 Characters for Name is needed')
        .max(200, 'Too Long! Maximum 200 Characters for Name is allowed')
        .required('Asset Description is (Required)'),

    group: Yup.string().required('Group is (Required)'),

    uom_id: Yup.string().required('Unit is a (Required)'),

    atype_id: Yup.string().required('Asset Type is (Required)'),

    initial_value: Yup.number()
        .positive('Initital Value must be a positive'),

    purchase_date: Yup.date('Purchase Date must be a date String')
        .required('Purchase Date is Required')
        .nullable(),
    residual_value: Yup.string().nullable(),
    shift_count: Yup.string().nullable(),
    capitalisation_date: Yup.date('Put to use must be a date String')
        .required('Put to use Required')
        .when('purchase_date', (purchase_date, schema) => (purchase_date && schema.min(purchase_date)))
        .nullable(),
    // .min(Date('purchase_date'), 'Capitalisation Date should be greater than Purchase Date'),

    party_name: Yup.string().nullable(),
    op_depreciation_val: Yup.string().nullable(),
    // op_dep_dt: Yup.string().when('op_depreciation_val', {
    //     is: (value) => value,
    //     then: fieldSchema => fieldSchema.required('Accrued Depreciation Date is required'),
    // }),
    op_dep_dt: Yup.string().nullable(),

    status: Yup.number().integer().nullable(),
    segid: Yup.string().nullable(),
    cid: Yup.string().nullable(),
    txnid: Yup.string().nullable(),
    dt_create: Yup.string().nullable(),
    dt_update: Yup.string().nullable(),
    user_create: Yup.string().nullable(),

    estimated_use_life:Yup.string().nullable()

});


