import * as type from './types'


export function searchLedger(ledger) {
    return {
        type : type.SEARCH_LEDGER,
        payload : ledger
    };
}

export function storeSearchLedgerResult(ledger) {
    console.log("action ledger----", ledger)
    return {
        type : type.STORE_SEARCH_LEDGER_RESULT,
        payload : ledger
    };
}

export function setLedgerObj(ledger) {
    return {
        type : type.SET_LEDGER_OBJ,
        payload : ledger
    };
}