import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { getLedgerObj } from "../../ledger_search/data/selectors";
import {
  getVoucher,
  getVoucherParams,
  getVoucherList,
  getNotification,
  getNature,
} from "../data/selectors";
import {
  createVoucher,
  createSaleVoucher,
  editVoucher,
  editSaleVoucher,
  setVFetchFalse,
  updateVoucherList,
  setNotifyDone,
} from "../data/actions";
import VTypeSelect from "../../vtype/components/vtypeSelect";
import { getVtypeObj } from "../../vtype/data/selectors";
import { voucherSchema } from "../data/model";
import {
  Container,
  Grid,
  Header,
  Form,
  Button,
  Table,
  Label,
  Modal,
} from "semantic-ui-react";
import { Formik, Field, Form as FormikForm, FieldArray } from "formik";
import {
  FormikInputComponent,
  FormikDateComponent,
  FormikTextAreaComponent,
  FormikDisplayLabelComponent,
} from "../../../utilities/formUtils";
import { displayAmtInLakh, displayInteger } from "../../../utilities/listUtils";
import VdetailsPurchase from "../../vdetails/components/vdetailsPurchase1";
import VdetailsSale from "../../vdetails/components/vdetailsSale";
import { getVdetails } from "../../vdetails/data/selectors";
import userACL from "../../../store/access";
import Notification from "../../../utilities/notificationUtils";
import LedgerSearch from "../../ledger_search/component/ledgerSearch";
import { find, merge, sumBy } from "lodash";
import moment from "moment";
import { searchLedger } from "../../ledger_search/data/actions";
import { fetchItems, fetchItem } from "../../items/data/actions";

const VoucherForm = (props) => {
  const dispatch = useDispatch();
  // console.log(props.location.state.nature, "===============================props")
  const voucher = useSelector((state) => getVoucher(state, props));

  console.log("voucher===========", voucher);
  // const year =useSelector(state=>)
  // console.log(voucher, "=================voucherdata")
  const [vtypeId, setVtypeId] = useState(voucher ? voucher.vtype_id : 0);
  const vtypeObj = useSelector((state) => getVtypeObj(state, vtypeId));
  // console.log(vtypeObj, '====vobj')
  const [nature, setNature] = useState(vtypeObj ? vtypeObj.nature : "");
  const vdetails = useSelector((state) =>
    getVdetails(state, voucher ? voucher.id : 0)
  );
  const ledgerDetails = useSelector((state) => getLedgerObj(state));
  const params = useSelector((state) => getVoucherParams(state, props));
  const [isModalOpen, setModalOpen] = useState(false);
  const [savedVoucherId, setSavedVoucherId] = useState(false);
  const [val, setVal] = useState("");
  const [amount, setAmount] = useState("");
  const [cessAmt, setCessAmt] = useState("");

  console.log("ledgerDetails=====", ledgerDetails);

  useEffect(() => {
    setNature(vtypeObj ? vtypeObj.nature : "");
  }, [vtypeObj]);

  // useEffect(() => {
  //   let values = merge({}, userACL.atFetch());
  //   values.name = "";
  //   dispatch(fetchItems(values));
  // }, []);

  // useEffect(() => {
  //   let values = merge({}, userACL.atFetch());
  //   values.nature=voucher.nature
  //   dispatch(fetchItem(values));
  // }, []);

  const saveVoucher = async (values, resetForm, status) => {
    if (props.match.path === "/voucher/create") {
      if (nature === "Sale" || nature === "sale") {
        values.tdetails.map((vd) => {
          userACL.atCreate(vd);
          vd.date = values.date;
          vd.ledger_name = values.ledger_name;
          vd.rlb_box_id = vd.boxId;
          vd.ledger_cd = ledgerDetails.ledger_code;
          vd.uom_id = vd.id;
          delete vd.uom_name;
          delete vd.id;
          delete vd.txnid;
          delete vd.cost;
          delete vd.pl;
          delete vd.nature;
          delete vd.party_cd;
          delete vd.party_country;
          delete vd.party_gstin;
          delete vd.party_id;
          delete vd.boxId;
          delete vd.party_name;
          delete vd.user_create;
          delete vd.user_update;
          delete vd.id;
          delete vd.txn_id;
          delete vd.rate;
          delete vd.status;
          delete vd.dt_create;
          delete vd.dt_update;
          // delete vd.rlb_box_id
        });
        userACL.atCreate(values);
        values.party_name = values.ledger_name;
        values.vnum = null;
        values.rlb_box_id = values.boxId;
        values.party_state_cd = ledgerDetails.party.state_code;
        values.party_id = ledgerDetails.ledger_useEffectcode;
        values.party_cd = ledgerDetails.party.id;
        values.party_country = ledgerDetails.party.country_name;
        values.party_gstin = ledgerDetails.party.gstin_no;
        delete values.txnid;
        delete values.totalInvAmt;
        delete values.ledger_name;
        delete values.id;
        delete values.status;
        delete values.dt_create;
        delete values.dt_update;
        delete values.rlb_vid;
        delete values.nature;
        delete values.boxId;
        dispatch(createSaleVoucher(values));
        setSavedVoucherId(values.id);
        resetForm();
        setVal("");
      } else {
        values.tdetails.map((vd) => {
          userACL.atCreate(vd);
          vd.date = values.date;
          vd.ledger_name = values.ledger_name;
          vd.rlb_box_id = vd.boxId;
          vd.ledger_cd = ledgerDetails.ledger_code;
          vd.uom_id = vd.id;
          delete vd.uom_name;
          delete vd.id;
          delete vd.txnid;
          delete vd.cost;
          delete vd.pl;
          delete vd.nature;
          delete vd.party_cd;
          delete vd.party_country;
          delete vd.party_gstin;
          delete vd.party_id;
          delete vd.boxId;
          delete vd.party_name;
          delete vd.user_create;
          delete vd.user_update;
          delete vd.id;
          delete vd.txn_id;
          delete vd.rate;
          delete vd.status;
          delete vd.dt_create;
          delete vd.dt_update;
        });
        userACL.atCreate(values);
        values.party_name = values.ledger_name;
        values.vnum = null;
        values.rlb_box_id = values.boxId;
        values.party_state_cd = ledgerDetails.party.state_code;
        values.party_id = ledgerDetails.ledger_useEffectcode;
        values.party_cd = ledgerDetails.party.id;
        values.party_country = ledgerDetails.party.country_name;
        values.party_gstin = ledgerDetails.party.gstin_no;
        delete values.txnid;
        delete values.totalInvAmt;
        delete values.ledger_name;
        delete values.id;
        delete values.status;
        delete values.dt_create;
        delete values.dt_update;
        delete values.rlb_vid;
        delete values.nature;
        dispatch(createVoucher(values));
        setSavedVoucherId(values.id);
        resetForm();
        setVal("");
      }
    }
    if (props.match.path === "/voucher/edit/:id") {
      if (nature === "Sale" || nature === "sale") {
        values.tdetails.map((vd) => {
          userACL.atUpdate(vd);
          vd.date = values.date;
          vd.rlb_box_id = vd.boxId;
          vd.ledger_name = voucher.party_name;
          delete vd.boxId;
          delete vd.rank;
          delete vd.txnid;
          delete vd.uom_name;
        });
        userACL.atUpdate(values);
        values.rlb_box_id = values.boxId;
        delete values.boxId;
        delete values.txnid;
        delete values.totalInvAmt;
        dispatch(editSaleVoucher(values));
        setSavedVoucherId(values.id);
        setTimeout(function () {
          props.history.push(`/voucher/list`, { nature: "sale" });
        }, 1000);
        dispatch(setVFetchFalse(false));
      } else {
        values.tdetails.map((vd) => {
          userACL.atUpdate(vd);
          //   vd.date = values.date;
          //   delete vd.boxId;
          //   delete vd.txnid;
          //   delete vd.uom_name;
          vd.date = values.date;
          vd.rlb_box_id = vd.boxId;
          vd.ledger_name = voucher.party_name;
          delete vd.boxId;
          delete vd.rank;
          delete vd.txnid;
          delete vd.uom_name;
        });
        userACL.atUpdate(values);
        // delete values.txnid;
        // delete values.totalInvAmt;
        // values.rlb_box_id = values.boxId;
        // console.log("values.rlb_box_id===",values.rlb_box_id ,values.boxId)
        // delete values.boxId;
        delete values.txnid;
        delete values.totalInvAmt;
        dispatch(editVoucher(values));
        setSavedVoucherId(values.id);
        setTimeout(function () {
          props.history.push(`/voucher/list`, { nature: "purchase" });
        }, 1000);
        // dispatch(setVFetchFalse(false))
      }
    }
  };

  function getVdArr(values) {
    switch (nature) {
      case "purchase":
        console.log("P");
        return <FieldArray name="tdetails" component={VdetailsPurchase} />;
      case "sale":
        console.log("S");
        return <FieldArray name="tdetails" component={VdetailsSale} />;
      default:
        console.log("P");
        return <FieldArray name="tdetails" component={VdetailsPurchase} />;
    }
  }

  useEffect(() => {
    if (props.match.path === "/voucher/create") {
      let vd = vdetails[0];
      vd.txn_id = voucher.id;
      vd.date = voucher.date;
      voucher.tdetails = vdetails;
    }
  }, []);

  useEffect(() => {
    if (props.match.path === "/voucher/edit/:id") {
      // let values = {
      //   type: "rpt_ledger_all",
      //   date: moment().format("DD-MM-YYYY"),
      //   uid: 2612,
      //   q: "%25" + "%23n",
      // };
      let values = merge({}, userACL.atFetch());
      values.rlb_box_id = values.boxId;
      values.ledger_name = voucher.party_name;
      values.nature=voucher.nature
      console.log(
        "valuesNature=========",
        voucher.party_name,
        values.ledger_name
      );
      delete values.boxId;
      // dispatch(searchLedger(values));
      dispatch(fetchItem({ nature}));
    }
  }, []);

  const setLedgerDetails = (id, ledgerObj, setFieldValue) => {
    if (id) {
      setFieldValue("party_cd", ledgerObj.ledgerCode);
      setFieldValue("party_name", ledgerObj.ledgerName);
      setFieldValue("party_state_cd", ledgerObj.stateCode);
      setFieldValue("party_country", ledgerObj.countryName);
      setFieldValue("party_gstin", ledgerObj.gstinNo);
    } else {
      setFieldValue("party_cd", "");
      setFieldValue("party_name", "");
      setFieldValue("party_state_cd", "");
      setFieldValue("party_country", "");
      setFieldValue("party_gstin", "");
    }
  };

  return (
    <Container>
      <Header as="h1">{params.title}</Header>

      <Grid columns="equal">
        <Grid.Column>
          <Formik
            id="voucherForm"
            size="large"
            width={5}
            initialValues={voucher}
            initialStatus={{ itemReset: true }}
            validationSchema={voucherSchema}
            onSubmit={(values, { resetForm, status }) =>
              saveVoucher(values, resetForm, status)
            }
            render={({
              handleSubmit,
              onChange,
              values,
              handleChange,
              errors,
              handleBlur,
            }) => (
              <Form
                as={FormikForm}
                size="large"
                width={5}
                onSubmit={handleSubmit}
                onBlur={handleBlur}
              >
                <Form.Group widths={6}>
                  {/* {props.match.path === '/voucher/create' ? */}
                  <VTypeSelect
                    name="vtype_id"
                    label="Voucher Type"
                    classNameGet="minWtSet10em"
                    isTxn="false"
                    isSelection={true}
                    index={0}
                    fieldName={""}
                    fromField=""
                    focus={true}
                    passFunct={setVtypeId}
                    isMandatory={true}
                  />
                  {/* : */}
                  {/* <Field component={FormikDisplayLabelComponent} isLabel={false} label='Voucher Type' classExtra={"topExtra"} text={vtypeObj ? vtypeObj.name : ''} /> */}
                  {/* } */}
                  <Field
                    component={FormikDisplayLabelComponent}
                    isLabel={false}
                    label="Voucher Number"
                    classExtra={"topExtra"}
                    text={
                      props.match.path === "/voucher/create"
                        ? "Auto Generate"
                        : voucher.vnum
                        ? voucher.vnum
                        : ""
                    }
                  />
                  <Field
                    name="date"
                    component={FormikDateComponent}
                    placeholder="Date"
                    isMandatory={true}
                  ></Field>
                  <LedgerSearch
                    name="ledger_name"
                    sefFocus={true}
                    label="Search Ledger"
                    isMandatory={true}
                    width={5}
                    focus={false}
                    placeholder="Search Ledger"
                    isTxn="true"
                    isSelection={true}
                    getLabelClass="alignLeft paddingTopBottom5"
                    getValue={setLedgerDetails}
                  />
                  <Field
                    name="bill_num"
                    label="Bill No."
                    component={FormikInputComponent}
                    placeholder="Bill No."
                    isMandatory={true}
                  ></Field>
                  <Field
                    name="bill_date"
                    label="Bill Date"
                    component={FormikDateComponent}
                    placeholder="Bill Date"
                    isMandatory={true}
                  ></Field>
                </Form.Group>
                <Form.Group>{getVdArr(values)}</Form.Group>
                <Table basic style={{ width: "99%" }}>
                  <Table.Body>
                    <Table.Row>
                      {values
                        ? values.totalInvAmt
                          ? setVal(values.totalInvAmt)
                          : null
                        : null}

                      <Table.Cell
                        className={nature == "sale" ? "totalTdSale" : "totalTd"}
                      >
                        Totals
                      </Table.Cell>
                      <Table.Cell
                        className={
                          nature == "sale" ? "invoiceTDTable" : "invoiceTD"
                        }
                      >
                        Invoice Amt: {val}{" "}
                      </Table.Cell>
                      <Table.Cell
                        className={
                          nature == "sale"
                            ? "qtyTdTable text-righl"
                            : "qtyTd text-right"
                        }
                      >
                        {sumBy(values.tdetails, (item) => Number(item.qty))
                          ? displayInteger(
                              sumBy(values.tdetails, (item) => Number(item.qty))
                            )
                          : displayInteger(0)}
                      </Table.Cell>
                      <Table.Cell
                        className={
                          nature == "sale"
                            ? "rateTdTable text-right"
                            : "rateTd text-right"
                        }
                      ></Table.Cell>
                      <Table.Cell
                        className={
                          nature == "sale"
                            ? "amountTdTable text-right"
                            : "amountTd text-right"
                        }
                      >
                        {sumBy(values.tdetails, (item) => Number(item.amount))
                          ? displayAmtInLakh(
                              sumBy(values.tdetails, (item) =>
                                Number(item.amount)
                              )
                            )
                          : displayAmtInLakh(0)}
                      </Table.Cell>
                      <Table.Cell
                        className={
                          nature == "sale"
                            ? "costTable text-right"
                            : "gstRTd text-right"
                        }
                      >
                        {nature == "sale"
                          ? sumBy(values.tdetails, (item) => Number(item.cost))
                            ? displayAmtInLakh(
                                sumBy(values.tdetails, (item) =>
                                  Number(item.cost)
                                )
                              )
                            : displayAmtInLakh(0)
                          : null}
                      </Table.Cell>
                      <Table.Cell
                        className={
                          nature == "sale"
                            ? "plTable text-right"
                            : "gstRTddd text-righ"
                        }
                      >
                        {nature == "sale"
                          ? sumBy(values.tdetails, (item) => Number(item.pl))
                            ? displayAmtInLakh(
                                sumBy(values.tdetails, (item) =>
                                  Number(item.pl)
                                )
                              )
                            : displayAmtInLakh(0)
                          : null}
                      </Table.Cell>
                      <Table.Cell
                        className={
                          nature == "sale"
                            ? "gstATdTable text-right"
                            : "gstATd  text-right"
                        }
                      >
                        {sumBy(values.tdetails, (item) => Number(item.igst_amt))
                          ? displayAmtInLakh(
                              sumBy(values.tdetails, (item) =>
                                Number(item.igst_amt)
                              )
                            )
                          : displayAmtInLakh(0)}
                      </Table.Cell>
                      <Table.Cell
                        className={
                          nature == "sale"
                            ? "cessRTdTable text-right"
                            : "cessRTd text-right"
                        }
                      ></Table.Cell>
                      <Table.Cell
                        className={
                          nature == "sale"
                            ? "cessATdTable text-right"
                            : "cessATd text-right"
                        }
                      >
                        {sumBy(values.tdetails, (item) => Number(item.cess_amt))
                          ? displayAmtInLakh(
                              sumBy(values.tdetails, (item) =>
                                Number(item.cess_amt)
                              )
                            )
                          : displayAmtInLakh(0)}
                      </Table.Cell>
                      <Table.Cell className="restTd">&nbsp;</Table.Cell>
                    </Table.Row>
                  </Table.Body>
                </Table>
                <Form.Group widths="equal" className="VBottoomPanel">
                  <Field
                    name="narration"
                    component={FormikTextAreaComponent}
                    userProps={{
                      maxLength: 300,
                      displayCount: false,
                      height: 3,
                    }}
                    label="Narration"
                    focus={true}
                  />
                  {/* classField="relativeGet" <Form.Field name="narration" label='' control='textarea' rows='3'/> */}
                  <Button
                    type="submit"
                    floated="right"
                    color="blue"
                    size="medium"
                  >
                    {" "}
                    {params.submitButtonText}{" "}
                  </Button>
                  {savedVoucherId ? (
                    <>
                      <Notification
                        id={savedVoucherId}
                        notifySelector={getNotification}
                        notifyDoneAction={setNotifyDone}
                        type="save"
                      />
                    </>
                  ) : null}
                  {/* {console.log(errors)} */}
                </Form.Group>
              </Form>
            )}
          />
        </Grid.Column>
      </Grid>
      <div>
        <Modal open={isModalOpen} size="small">
          <Modal.Header>Error</Modal.Header>
          <Modal.Content>
            <p>
              Voucher total amount should be matched with Realbooks Voucher
              Amount
            </p>
          </Modal.Content>
          <Modal.Actions>
            <Button
              type="button"
              negative
              icon="thumbs down outline"
              content="Okay"
              labelPosition="right"
              onClick={() => {
                setModalOpen(false);
              }}
            />
          </Modal.Actions>
        </Modal>
      </div>
    </Container>
  );
};

export default VoucherForm;
