import { combineReducers } from 'redux';
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr';

// Define a assettypes schema
const assettypeSchema = new schema.Entity('assettypes',{}, { idAttribute: 'id' });
const assettypeListSchema = [assettypeSchema];

const defaultAssettypeParams = {
  assettypeFetched: false,
  isFetching:false,
  createTitle: 'Create Asset Type',
  createSubmitButtonText: 'Submit',
  editTitle: 'Edit Asset Type',
  editSubmitButtonText: 'Submit' 
}

const defaultMessages = {
  id: {
    save: {
      status: "",
      msg: "",
      isSaving: false
    },
    edit: {
      status: "",
      msg: "",
      isSaving: false
    },
    delete: {
      status: "",
      msg: "",
      isSaving: false
    }
  }
}

function byId(state = {}, action) {
    if (action.type === 'SAVED_ASSETTYPE') {
      const assettypeID = action.payload.id;
      return {...state, 
        [assettypeID]: action.payload
      }
    }
    if (action.type === 'FETCHED_ASSETTYPE') { 
        const normalizedAssettype = normalize(action.payload, assettypeListSchema);
        return merge({},state,normalizedAssettype.entities.assettypes)
    }
    if (action.type === 'DELETED_ASSETTYPE') {
      if(action.payload.msg.type === "success"){
        const assettypeID = action.payload.id;
        let finalState = {...state}
        delete finalState[assettypeID]
        return finalState
      }else{
        return state
      }
    } else {
      return state
    }
  }
function byListId(state = {}, action) {
    if (action.type === 'SAVED_ASSETTYPE') {
      const assettypeID = action.payload.id;
      return {...state, 
        [assettypeID]: action.payload
      }
    }
    if (action.type === 'FETCHED_ASSETTYPE') { 
        const normalizedAssettype = normalize(action.payload, assettypeListSchema);
        return merge({},state,normalizedAssettype.entities.assettypes)
    }
    if (action.type === 'DELETED_ASSETTYPE') {
      if(action.payload.msg.type === "success" || action.payload.msg.type === "Success"){
        const assettypeID = action.payload.id;
        let finalState = {...state}
        delete finalState[assettypeID]
        return finalState
      }else{
        return state
      }
    } else {
      return state
    }
  }

  function storeSearchedSegList(state = {}, action) {
    if (action.type === 'STORE_SEARCHED_SEG_LIST') {
        return action.payload
    } else {
        return state
    }
}

function params(state = defaultAssettypeParams, action) {
  if (action.type === 'FETCHED_ASSETTYPE') { 
      return {...state, 
        assettypeFetched: true,
        isFetching:false
      }
  }
  if(action.type === 'SET_FETCH_FALSE'){
    return {...state, 
      assettypeFetched: false,
      isFetching:false
    }
  }
   else {
    return state
  }
}



function notifications(state = {}, action) {
  if (action.type === 'SAVED_ASSETTYPE') {
    return {...state, 
      [action.payload.id]: { ...state[action.payload.id],
        save: { 
          status : action.txn === "success" ? 'success' : 'error',
          // status : action.payload.msg.msg,
          msg : action.txn
        }
      }
    }
  }
  if (action.type === 'DELETED_ASSETTYPE') { 
    return {...state, 
      [action.payload.id]: { ...state[action.payload.id],
        delete: { 
          status: (action.payload.msg.type === "success" || action.payload.msg.type === "Success") ? 'success' : 'error',
          msg : action.payload.msg
        }
      }
    }
  } 
  if(action.type === 'NOTIFICATION_DONE_ASSETTYPE'){
    const {id, type} = action.payload
    // Remove the 'id' element from state
    const { [id]: idValue, ...restOfState } = state;
    // Remove the 'type' from the 'id' element
    const { [type]: removedValue, ...restOfId } = idValue;
    // Merge back together
    const finalState = { ...restOfState, [id]: restOfId };     
    return finalState

  } else {
    return state
  }
}

const assettype = combineReducers({
    byId,
    byListId,
    notifications,
    storeSearchedSegList,
    params
});

export default assettype;

