import { combineReducers } from 'redux';
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr';

// Define a years schema
const yearSchema = new schema.Entity('years',{}, { idAttribute: 'id' });
const yearListSchema = [yearSchema];

const defaultYearParams = {
  yearFetched: false,
  isFetching:false,
  createTitle: 'Create New Year',
  createSubmitButtonText: 'Submit',
  editTitle: 'Edit Year',
  editSubmitButtonText: 'Submit' 
}

function byId(state = {}, action) {
    if (action.type === 'SAVED_YEAR') {
        const yearID = action.payload.id;
      return {...state, 
        [yearID]: action.payload
      }
    }
    if (action.type === 'FETCHED_YEAR') { 
        const normalizedYear = normalize(action.payload, yearListSchema);
        return merge({},state,normalizedYear.entities.years)
    } else {
      return state
    }
  }

function params(state = defaultYearParams, action) {
  if (action.type === 'FETCH_YEAR') { 
    return {...state, 
      yearFetched: false,
      isFetching:true
    }
}
  if (action.type === 'FETCHED_YEAR') { 
      return {...state, 
        yearFetched: true,
        isFetching:false
      }
  } else {
    return state
  }
}

function notifications(state = {}, action) {
  if (action.type === 'SAVED_YEAR') {
    return {...state, 
      [action.payload.id]: { ...state[action.payload.id],
        save: { 
          status : action.txn === "success" ? 'success' : 'error',
          msg : action.txn
        }
      }
    }
  }
  if (action.type === 'EDITED_YEAR') {
    return {...state, 
      [action.payload.id]: { ...state[action.payload.id],
        edit: { 
          status : action.txn === "success" ? 'success' : 'error',
          msg : action.txn
        }
      }
    }
  }
  if (action.type === 'DELETED_YEAR') { 
    return {...state, 
      [action.payload.id]: { ...state[action.payload.id],
        delete: { 
          status : action.payload.msg.type === ("Success" || "success" ) ? 'success' : 'error',
          msg : action.payload.msg.msg
        }
      }
    }
  } 
  if(action.type === 'NOTIFICATION_DONE_YEAR'){
    const {id, type} = action.payload
    // Remove the 'id' element from state
    const { [id]: idValue, ...restOfState } = state;
    // Remove the 'type' from the 'id' element
    const { [type]: removedValue, ...restOfId } = idValue;
    // Merge back together
    const finalState = { ...restOfState, [id]: restOfId };     
    return finalState

  } else {
    return state
  }
}

const year = combineReducers({
    byId,
    params,
    notifications
});

export default year;

