import React, { useEffect, useState } from "react";
import { FileUploader } from "react-drag-drop-files";
import { Button, Icon, Modal } from "semantic-ui-react";



function TemplateDragDrop(props) {

    const [error, setError] = useState(false);
    // const [seleted, setSelected] = useState(false);
    const [fileType, setFileType] = useState("");
    const [fileTypes, setFileTypes] = useState(["xls", "xlsx", "csv", "xml"]);
    const [uploadMsg, setUploadMsg] = useState('');
    const [statusModal, setStatusModal] = useState({open: false, msg: ''});


    useEffect(() => {
        if (props.fileType === "xl") {
            setFileTypes(["xls", "xlsx"])
            setFileType("(.xls / .xlsx)")
        }
        if (props.fileType === "xlsx,xls") {
            setFileTypes(["xls", "xlsx"])
            setFileType("(.xls / .xlsx)")
        }
        if (props.fileType === "csv") {
            setFileTypes(["csv"])
            setFileType("(.csv)")
        }
        if (props.fileType === "xml") {
            setFileTypes(["xml"])
            setFileType("(.xml)")
        }
    }, [props.fileType])


    const handleChange = (fileObj) => {
        props.setTemplateFileObj(fileObj)
        if (fileObj && fileObj.name !== null && fileObj.name !== undefined) {
            if (props.setFileSelected) {
                props.setFileSelected(true)
            }

            setUploadMsg(fileObj.name)
        } else {
            if (props.setFileSelected) {
                props.setFileSelected(false)
            }
            setUploadMsg("")
        }


    };

    useEffect(() => {
        if (props.fileSeleted === false) {
            props.setFileSelected(false)
            setUploadMsg("")
        }

    }, [props.fileSeleted])

    const typeErrorFunc = (err) => {
        setError(true)
        if (props.fileType === "xl") {
            setUploadMsg("Only XLS / XLSX Files are allowed")
        }
        if (props.fileType === "csv") {
            setUploadMsg("Only CSV Files are allowed")
        }
        if (props.fileType === "xml") {
            setUploadMsg("Only XML Files are allowed")
        }

    }
    return (
        <>
            <FileUploader
                name="file"
                handleChange={handleChange}
                types={fileTypes}
                maxSize={15}
                onSizeError={(file) => {
                    setStatusModal({open: true, msg: 'Maximum file size 15 MB'})
                }}
                onTypeError={(err) => typeErrorFunc(err)}
                children={<div className="ewqTBN"><Icon color="grey" name='file' size="large"></Icon>{props.fileSeleted ? "Selected" : `Choose/Drag Template File ${fileType}`}</div>}
                // required={true}

            />
            {
                error ? <p style={{ marginLeft: "8%", paddingTop: "10px", fontSize: "14px", color: "red" }}>{uploadMsg}</p>
                    :
                    <p style={{ marginLeft: "8%", paddingTop: "10px", fontSize: "14px", color: "green" }}>{uploadMsg}</p>

            }
            <Modal open={statusModal.open} size="small">
                <Modal.Header>
                    Size Error
                </Modal.Header>
                <Modal.Content>
                    <p>{statusModal.msg}</p>
                </Modal.Content>
                <Modal.Actions>
                    <Button
                        size="small"
                        inverted
                        color="red"
                        onClick={() => setStatusModal({open: false, msg: ''})}
                    >
                        Close
                    </Button>
                </Modal.Actions>
            </Modal>
        </>
    );
}

export default TemplateDragDrop;