import * as type from './types'


export function createVoucher(voucher) {
    return {
        type: type.CREATE_VOUCHER,
        payload: voucher,
        txn : 'initiated',
    };
}


export function createSaleVoucher(voucher) {
    return {
        type: type.CREATE_SALE_VOUCHER,
        payload: voucher,
        txn: 'initiated'
    }
}

export function savedVoucher(voucher, res) {
    return {
        type: type.SAVED_VOUCHER,
        payload: voucher,
        txn: res,
        diff: true
    }
}


export function savedSaleVoucher(voucher, res) {
    return {
        type: type.SAVED_SALE_VOUCHER,
        payload: voucher,
        txn: res,
        diff: true
    }
}

export function editVoucher(voucher) {

    return {
        type: type.EDIT_VOUCHER,
        payload: voucher,
        txn: 'initiated',
    };
}

export function editSaleVoucher(voucher) {
    return {
        type: type.EDIT_SALE_VOUCHER,
        payload: voucher,
        txn: 'initiated'
    }
}

export function fetchVoucher(param) {
    return {
        type: type.FETCH_VOUCHER,
        payload: param
    };
}

export function fetchSaleVoucher(props){
    return {
        type:type.FETCH_SALE_VOUCHER,
        payload:props
    }
}

export function fetchedVoucher(voucher) {
    return {
        type: type.FETCHED_VOUCHER,
        payload: voucher
    };
}

export function fetchedSaleVoucher(props){
    return{
        type: type.FETCHED_SALE_VOUCHER,
        payload: props
    }
}

export function deleteVoucher(voucher) {
    return {
        type: type.DELETE_VOUCHER,
        payload: voucher
    };
}

export function deletedVoucher(msg) {
    return {
        type: type.DELETED_VOUCHER,
        payload: msg
    };
}

export function editFetchVoucher(voucher) {
    return {
        type: type.EDIT_FETCH_VOUCHER,
        payload: voucher
    };
}

export function editFetchedVoucher(msg) {
    // console.log(JSON.stringify(msg))
    return {
        type: type.EDIT_FETCHED_VOUCHER,
        payload: msg
    };
}

export function setVFetchFalse(action) {
    return {
        type: type.SET_V_FETCH_STATUS,
        payload: action
    };
}
export function setNotifyDone(action) {
    return {
        type: type.NOTIFICATION_DONE_VOUCHER,
        payload: action
    };
}
export function updateVoucherList(action) {
    return {
        type: type.UPDATE_LIST_VOUCHER,
        payload: action
    };
}

export function getNature(props){
    return {
        type:type.GET_NATURE,
        payload:props
    }
}

