export const validatateVAndRlbVAmt = (rlbObject, voucherObject) => {
        let rlbAmt = parseFloat(rlbObject.cr) !== 0 ? rlbObject.cr : rlbObject.dr
        let vdetails = voucherObject.tdetails;
        let vdTotalAmt = 0;
        for (let i = 0; i < vdetails.length; i++) {
                let vdAmt = vdetails[i].amt
                vdTotalAmt = parseFloat(vdTotalAmt) + parseFloat(vdAmt)
        }
        console.log(parseFloat(rlbAmt), " === ", parseFloat(vdTotalAmt), "checking 2222")
        console.log((parseFloat(rlbAmt) === parseFloat(vdTotalAmt)), "checking")
        if (parseFloat(rlbAmt) === parseFloat(vdTotalAmt)) {
                return true;
        } else {
                return false;
        }
}

const filterDate = (rows, id, filterValue) => {
        console.log(filterValue, '=======filval')
        console.log(typeof (filterValue), '=======type')
        return rows.filter(row => {
                let rowValue = moment(row.values[id]).format("DD-MM-YYYY")
                let check = filterValue.replace(/[ ]/gi, "-").replace(/[*]/gi, "-").replace(/[.]/gi, "-").replace(/[+]/gi, "-").replace(/[/]/gi, "-").replace(/[,]/gi, "-")
                return rowValue !== undefined ?
                        String(rowValue).toLowerCase().startsWith(String(check).toLowerCase())
                        : true
        })
}

