import { combineReducers } from 'redux';
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr';

// Define a areas schema
const areaSchema = new schema.Entity('areas',{}, { idAttribute: 'id' });
const areaListSchema = [areaSchema];

const defaultAreaParams = {
  areaFetched: false,
  createTitle: 'Create New Depreciation Area',
  createSubmitButtonText: 'Submit',
  editTitle: 'Edit Depreciation Area',
  editSubmitButtonText: 'Submit' 
}

const defaultMessages = {
  id: {
    save: {
      status: "",
      msg: "",
      isSaving: false
    },
    edit: {
      status: "",
      msg: "",
      isSaving: false
    },
    delete: {
      status: "",
      msg: "",
      isSaving: false
    }
  }
}

function byId(state = {}, action) {
    if (action.type === 'SAVED_AREA') {
      const areaID = action.payload.id;
      return {...state, 
        [areaID]: action.payload
      }
    }
    if (action.type === 'FETCHED_AREA') { 
        const normalizedArea = normalize(action.payload, areaListSchema);
        return merge({},state,normalizedArea.entities.areas)
    }
    if (action.type === 'DELETED_AREA') {
      if(action.payload.msg === "Success"){
        const areaID = action.payload.id;
        let finalState = {...state}
        delete finalState[areaID]
        return finalState
      }else{
        return state
      }
    } else {
      return state
    }
  }

function params(state = defaultAreaParams, action) {
  if (action.type === 'FETCHED_AREA') { 
      return {...state, 
        areaFetched: true,
        isFetching:false
      }
  }
   else {
    return state
  }
}


function notifications(state = {}, action) {
  if (action.type === 'SAVED_AREA') {
    return {...state, 
      [action.payload.id]: { ...state[action.payload.id],
        save: { 
          status : action.txn === "success" ? 'success' : 'error',
          msg : action.txn
        }
      }
    }
  }
  if (action.type === 'DELETED_AREA') { 
    return {...state, 
      [action.payload.id]: { ...state[action.payload.id],
        delete: { 
          status : action.payload.msg === "Success" ? 'success' : 'error',
          msg : action.payload.msg
        }
      }
    }
  } 
  if(action.type === 'NOTIFICATION_DONE_AREA'){
    const {id, type} = action.payload
    // Remove the 'id' element from state
    const { [id]: idValue, ...restOfState } = state;
    // Remove the 'type' from the 'id' element
    const { [type]: removedValue, ...restOfId } = idValue;
    // Merge back together
    const finalState = { ...restOfState, [id]: restOfId };     
    return finalState

  } else {
    return state
  }
}

const area = combineReducers({
    byId,
    notifications,
    params
});

export default area;

