import React from 'react';
import { Route, withRouter } from 'react-router';
import { Container } from 'semantic-ui-react'
import VtypeMaster from './components/vtypeForm';
import VtypeList from './components/vtypeList';



const Vtype = () => {
    return (
        <Container>
            <Route path="/vtype/list" component={withRouter(VtypeList)} />
            <Route path="/vtype/edit/:id" component={VtypeMaster} />
            <Route path="/vtype/create" component={VtypeMaster} />
        </Container>
    )
};

export default Vtype;