import React from 'react';
import { Field } from 'formik'
import { FormikSelectComponent } from '../../../utilities/formUtils'


const VtypeNatureSelect = ({ name, isTxn, isLabel, label, isSelection, isMandatory, getSelectedDDVal, computeValue, index, fieldName, fromField, otherList }, props) => {

    const getSelectedValue = (values) => {
        // console.log("Type Selected", values, name)
    }

    const opt = [
        { key: "purchase", value: "purchase", text: "Purchase" },
        { key: "sale", value: "sale", text: "Sale" },
        { key: "depriciation", value: "depriciation", text: "Depriciation" }
    ]

    return (
        <Field name={name} isTxn={isTxn} isLabel={isLabel} label={label} isMandatory={isMandatory} isSelection={isSelection} component={FormikSelectComponent} computeValue={computeValue} index={index} fieldName={fieldName} fromField={fromField} otherList={otherList} userProps={{ options: opt, getValue: getSelectedDDVal ? getSelectedDDVal : getSelectedValue }}> </Field>
    )
}


export default VtypeNatureSelect;

