import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux'
// import { searchLedger } from '../data/actions'
import * as select from '../data/selectors'
import { Field } from 'formik'
import { FormikSelectComponent, FormikAsyncSelectComponent } from '../../../utilities/formUtils' //FormikAsyncSelectComponent
import { searchLedger, setLedgerObj,storeSearchLedgerResult } from '../data/actions'
import userACL from '../../../store/access'
import { find, merge } from 'lodash'
import moment from 'moment'


const LedgerSearch = ({ name, isTxn, isLabel, label, values, isMandatory, focus, isSelection, type, placeholder, appendLabel, appendLabelText, isFetching, width, getLabelClass, ledgerSearchBy, sefFocus, getValue : callback  }, props) => {

    const searchOptions = useSelector(state => select.getSearchLedgerResults(state, props))
    // const searchOptions = useSelector(state => select.selectItem(state, props))

    const option=useSelector(state=>select.getLedgerList(state))
    let options = []
    const dispatch = useDispatch()
    

    const getInputValue = (value) => {
        if(value.length > 2){
            let values = {
                ledger_name: value,
                rlb_box_id: userACL.atFetch().boxId
            }
            values = merge({}, values, userACL.atFetch())
            console.log("values--222--------------------------", values)
            dispatch(searchLedger(values))
            // console.log("searchLedger=======",values)
        }
    }

    const getValue = (value, setFieldValue) => {
        let obj = searchOptions.find((item)=>item.ledger_name === value)
        console.log("ledgerObj=====",searchOptions,value,obj)
        if (callback){
            callback(value, obj, setFieldValue)
        }
        dispatch(setLedgerObj( obj||{}))

    }
    // console.log((val)=>getValue(val),"=========getValue")
    // Loading Logic if tasks not fetched
    return (
        <Field name={name} sefFocusCalled={sefFocus} isMandatory={isMandatory} className={appendLabel ? "goRelative" : ""} isLabel={isLabel} isTxn={isTxn} label={label} isFetching={isFetching} tabindex={5} width={width} appendLabel={appendLabel} appendLabelText={appendLabelText} isSelection={isSelection} focus={focus} component={FormikAsyncSelectComponent} userProps={{ options : (options ? options : [{key: 0, value: '', text: ''}]), searchOptions, getInputValue, getValue, placeHolder: placeholder }} getLabelClass={getLabelClass}> </Field>
        //component={FormikAsyncSelectComponent}
    )
}

export default LedgerSearch;
