export const getItems = (state, props) => {
    if (props.match.path === '/items/create') {
        return items()
    }
    if (props.match.path === '/items/edit/:id') {
        let _id = props.match.params.id
        console.log('_id', _id)
        let obj = cloneDeep(state.items.byListId[_id])
        return obj
    }
}

export const getQuickItems = (state, props) => {
    let item = items()
    let itemDesc = itemsDepreciation()
    userACL.atCreate(itemDesc)
    itemDesc.items = item.id
    itemDesc.manual = 1
    itemDesc.rank = 1
    return item
}

export const getItemDepreciation = (state, itemId) => {
    let itemDescFromVid = at(state.items.ById, itemId)
    if (itemDescFromVid[0] === undefined)
        itemDescFromVid = [itemsDepreciation()]
    return itemDescFromVid
}

export const getItemsParams = (state, props) => {
    const params = {}

    if (startsWith(state.router.location.pathname, '/items/create')) {
        params.title = state.items.params.createTitle
        params.submitButtonText = state.items.params.createSubmitButtonText
    };

    if (startsWith(state.router.location.pathname, '/items/edit/')) {
        params.title = state.items.params.editTitle
        params.submitButtonText = state.items.params.editSubmitButtonText
    };

    return params
}


export const selectItem = createSelector(
    itemSelectorList,
    items => {
        const keys = Object.keys(items)
        const obj = keys.map((key) => { return { value: key, key: key, text: items[key].name, name: items[key].name, atypeId:items[key].atype_id, code: items[key].code,id:items[key].id } })
        return obj
    })

