export const selectItems = createSelector(
    itemSelectorList,
    (items) => {
        const keys = Object.keys(items)
        const obj = keys.map((key) => { return { value: key, key: key, text: items[key].code.concat(' - ', items[key].name), name: items[key].group_name, group: items[key].group} })
        return obj
    })


