import * as type from './types'


export function fetchDreport(option) {
    return {
        type : type.FETCH_DREPORT,
        payload : option
    };
}

export function fetchedDreport(dreport) {
    return {
        type : type.FETCHED_DREPORT,
        payload : dreport
    };
}

export function downloadDreport(option) {
    return {
        type : type.DOWNLOAD_DREPORT,
        payload : option
    };
}

export function downloadedDreport(dreport) {
    return {
        type : type.DOWNLOADED_DREPORT,
        payload : dreport
    };
}

