import * as type from "./types";

export function createVtype(props) {
  return {
    type: type.CREATE_VTYPE,
    payload: props,
    txn: "initiated",
  }
}

export function savedVtype(props) {
  return {
    type: type.SAVED_VTYPE,
    payload: props,
    txn: "success",
    diff: true,
  }
}

export function editVtype(props) {
  return {
    type: type.EDIT_VTYPE,
    payload: props,
    txn: "initiated",
  }
}

export function fetchVtype() {
  return {
    type: type.FETCH_VTYPE,
  }
}

export function fetchedVtype(props){
    return{
        type:type.FETCHED_VTYPE,
        payload:props,
    }
}

export function fetchRlbVtype() {
  return {
    type: type.FETCH_RLBVTYPE,
  }
}

export function fetchedRlbVtype(props){
    return{
        type:type.FETCHED_RLBVTYPE,
        payload:props,
    }
}

export function deleteVtype(props){
  return{
    type:type.DELETE_VTYPE,
    payload:props
  }
}

export function deletedVtype(msg){
  return{
    type:type.DELETED_VTYPE,
    payload:msg
  }
}

export function setNotifyDone(props){
  return{
    type: type.NOTIFICATION_DONE_VTYPE,
    payload: props
  };
}