import { call, takeEvery, takeLatest, delay, all, put, select } from 'redux-saga/effects'
import { storeSearchLedgerResult } from './actions'
import { ASSET_URL, getHeader } from '../../../store/path'
import axios from 'axios';
import { filter } from 'lodash'


function* searchLedger() {

  yield takeLatest('SEARCH_LEDGER', initiateLedgerSearch)

}



function* initiateLedgerSearch(action) {
  // yield delay(350)
  let { data } = yield call(searchLedgerAPI, action.payload)
  let getDate = getDateAfterArrange(data);
  yield put(storeSearchLedgerResult(getDate))

}
function getDateAfterArrange(data) {
  let obj = data.data
  const keys = Object.keys(obj)
  let getobj = keys.map((key) => {
      obj[key].key = obj[key].id
      obj[key].value = obj[key].ledger_name
      obj[key].text = obj[key].ledger_name

      return obj[key]
  }).sort((a) => {
    return a.ledger_category.toString().toLowerCase() === "party"
  })
  return getobj
}

function searchLedgerAPI(data) {
  // return axios.post('http://ec2-3-7-170-219.ap-south-1.compute.amazonaws.com:9001/search/ledger/byName/'+data.q, data, getHeader(), {crossDomain: true}, { withCredentials: true })
  // return axios.post('https://realbooks.in/search/ledger/byName/' + data.q, data, getHeader(), { crossDomain: true })
  return axios.post(ASSET_URL+'/asset/ledgersearch', data, getHeader(),{ crossDomain: true })
  // return axios.post('http://ec2-54-251-137-83.ap-southeast-1.compute.amazonaws.com:9003/search/ledger/byName/'+data.q, data, getHeader(), {crossDomain: true})
}

//#endregion

const sagaErrorMessage = (error, action) => {
  console.group("Saga Error:" + action.type)
  console.log(error)
  console.groupEnd()
}

export default function* ledgerSaga() {
  yield all([
    searchLedger(),
  ])
}  