import { call, takeEvery, all, put, select } from 'redux-saga/effects'
import { getIsDepartmentFetched } from './selectors'
import { savedDepartment, fetchedDepartment, deletedDepartment } from './actions'
import axios from 'axios';
import { handlError, parseError } from '../../app/serverError'
import { ASSET_URL } from '../../../store/path'
import userACL from '../../../store/access'
import { merge } from 'lodash'

// const delay = (ms) => new Promise(res => setTimeout(res, ms))

//#region Saga for Create Department 

function* createDepartment() {
  yield takeEvery('CREATE_DEPARTMENT', saveDepartment)
}

function* saveDepartment(action) {
  const { response, error } = yield call(saveDepartmentAPI, action.payload)
  console.log("Saga", action.payload)
  if (response) {
    if(response.data.type!=="error"){
      yield put(savedDepartment(action.payload))
    }
  }else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
  }
}

function saveDepartmentAPI(data) {
  // data.rlb_box_id = data.boxId
  // delete data.boxId
  return axios.post(ASSET_URL + '/department/add', data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for Edited Department 

function* editDepartment() {
  yield takeEvery('EDIT_DEPARTMENT', saveEditedDepartment)
}

function* saveEditedDepartment(action) {
  const { response, error } = yield call(editDepartmentAPI, action.payload)
  if (response) {
    if(response.type.data!=="error"){
      yield put(savedDepartment(action.payload))
    }
  }
  else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
  }
}

function editDepartmentAPI(data) {
  let user_id = data.id
  // data.rlb_box_id = data.boxId
  // delete data.boxId
  return axios.post(ASSET_URL + '/department/update', data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion

//#region Saga for Delete Department 

function* deleteDepartment() {
  yield takeEvery('DELETE_DEPARTMENT', saveDeleteDepartment)
}

function* saveDeleteDepartment(action) {
  const { response, error } = yield call(deleteDepartmentAPI, action.payload)
  if (response) yield put(deletedDepartment({ id: action.payload.id, msg: response.data }))
  // if (response) yield put(deletedDepartment( action.payload ))
  else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
  }
}

function deleteDepartmentAPI(data) {
  let departmentId = data.id
  // data.rlb_box_id = data.boxId
  // delete data.boxId
  return axios.post(ASSET_URL + '/department/delete/' + departmentId, data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for List Tasks 

function* fetchDepartment() {

  yield takeEvery('FETCH_DEPARTMENT', requestDepartment)

}

function* requestDepartment() {

  const isDepartmentFetched = yield select(getIsDepartmentFetched);
  if (!isDepartmentFetched) {
    let { data } = yield call(requestDepartmentAPI)
    yield put(fetchedDepartment(data.data))
  }

}

function requestDepartmentAPI() {
  let data = merge({}, userACL.atFetch())
  data.rlb_box_id = data.boxId
  delete data.boxId
  return axios.post(ASSET_URL + '/department/list', data, { crossDomain: true })
}

//#endregion


const sagaErrorMessage = (error, action) => {
  console.group("Saga Error:" + action.type)
  console.log(error)
  console.groupEnd()
}

export default function* departmentSaga() {
  yield all([
    createDepartment(),
    deleteDepartment(),
    fetchDepartment(),
    editDepartment()
  ])
}  