import axios from 'axios';
import { all, call, put, takeEvery } from 'redux-saga/effects';
import { ASSET_URL } from '../../../store/path';
import { handlError, parseError } from '../../app/serverError';
import { deletedDepreciationForItem, savedDepreciationForItem } from '../../items/data/actions';
import { deletedDepreciation, fetchedDepreciation, savedDepreciation } from './actions';

// const delay = (ms) => new Promise(res => setTimeout(res, ms))

//#region Saga for Create Depreciation 

function* createDepreciation() {
    yield takeEvery('CREATE_DEPRECIATION',saveDepreciation)
  }

function* saveDepreciation(action){
  const { response, error } = yield call(saveDepreciationAPI, action.payload)
  console.log("Saga",action.payload)
  if (response){ 
    yield put (savedDepreciation(action.payload))
    yield put (savedDepreciationForItem(action.payload))
  }else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function saveDepreciationAPI(data) {
    return axios.post(ASSET_URL+'/depreciation/add', data, {crossDomain: true})
      .then(response => ({ response }))
      .catch(error => ({ error }))
  }

//#endregion


//#region Saga for Edited Depreciation 

function* editDepreciation() {
  yield takeEvery('EDIT_DEPRECIATION',saveEditedDepreciation)
}

function* saveEditedDepreciation(action){
  const { response, error } = yield call(editDepreciationAPI, action.payload)  
  if (response){ 
    yield put (savedDepreciation(action.payload))
    yield put (savedDepreciationForItem(action.payload))
  }else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function editDepreciationAPI(data) {
  let user_id = data.id
  return axios.post(ASSET_URL+'/depreciation/update/'+ user_id, data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for Delete Depreciation 

function* deleteDepreciation() {
  yield takeEvery('DELETE_DEPRECIATION',saveDeleteDepreciation)
}

function* saveDeleteDepreciation(action){
  const { response, error } = yield call(deleteDepreciationAPI, action.payload)
  if (response){ 
    yield put (deletedDepreciation({id: action.payload, msg : response.data}))
    yield put (deletedDepreciationForItem({id: action.payload, msg : response.data}))
  }else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function deleteDepreciationAPI(data) {
  let depreciationId = data
  return axios.post(ASSET_URL+'/depreciation/delete/'+ depreciationId, data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for List Tasks 

function* fetchDepreciation() {
  yield takeEvery('FETCH_DEPRECIATION',requestDepreciation)
}
  
function* requestDepreciation(action){
  let { data } = yield call(requestDepreciationAPI, action.payload)
  yield put (fetchedDepreciation(data))
}

function requestDepreciationAPI(data) { 
  return axios.post(ASSET_URL+'/depreciation/list', data, {crossDomain: true})
}

//#endregion


const sagaErrorMessage = (error, action) => {
    console.group("Saga Error:"+action.type)
    console.log(error)
    console.groupEnd()
}

export default function* depreciationSaga() {
    yield all([
        createDepreciation(),
        fetchDepreciation(),
        editDepreciation(),
        deleteDepreciation()
    ])
  }  