import { combineReducers } from "redux";
import { merge } from 'lodash'
import { normalize, schema } from "normalizr";


// Define a depreciations schema
const vTypeSchema = new schema.Entity('vtypes', {}, { idAttribute: 'id' });
const vTypeListSchema = [vTypeSchema];

// const rlbvTypeSchema = new schema.Entity('vtypes', {}, { idAttribute: 'id' });
// const rlbvTypeListSchema = [rlbvTypeSchema];


const defaultVtypeParams = {
  vTypeFetched: false,
  isFetching: false,
  rlbvTypeFetched:false,
  isRlbvTypeFetching:false,
  createTitle: 'Create New Vtype',
  createSubmitButtonText: 'Submit',
  editTitle: 'Edit Vtype List',
  editSubmitButtonText: 'Submit'
}

function byId(state = {}, action) {
  if (action.type == 'SAVED_VTYPE') {
    const VtypeID = action.payload.id
    return {
      ...state,
      [VtypeID]: action.payload
    }
  }

  if (action.type === 'FETCHED_VTYPE') {
    // console.log(JSON.stringify(action.payload),'===============vtype_payload')
    const normalizedVtype = normalize(action.payload.data, vTypeListSchema);
    return merge({}, state, normalizedVtype.entities.vtypes)
  }

  if (action.type === 'DELETED_VTYPE') {
    if (action.payload.msg.type === "success") {
      const vTypeID = action.payload.id;
      let finalState = { ...state }
      delete finalState[vTypeID]
      return finalState
    } else {
      return state
    }
  } else {
    return state
  }
}

function byRlbvTypeId(state={},action){
  if (action.type === 'FETCHED_RLBVTYPE') {
    const normalizedVtype = normalize(action.payload.data, vTypeListSchema);
    return merge({}, state, normalizedVtype.entities.vtypes)
  }else{
    return state;
  }
}

function notifications(state = {}, action) {
  if (action.type === 'SAVED_VTYPE') {
    return {
      ...state,
      [action.payload.id]: {
        ...state[action.payload.id],
        save: {
          status: action.txn === "success" ? 'success' : 'error',
          msg: action.txn
        }
      }
    }
  }
  if (action.type === 'DELETED_VTYPE') {
    return {
      ...state,
      [action.payload.id]: {
        ...state[action.payload.id],
        delete: {
          status: (action.payload.msg.type === "success") ? 'success' : 'error',
          msg: action.payload.msg
        }
      }
    }
  }
  if (action.type === 'NOTIFICATION_DONE_VTYPE') {
    const { id, type } = action.payload
    // Remove the 'id' element from state
    const { [id]: idValue, ...restOfState } = state;
    // Remove the 'type' from the 'id' element
    const { [type]: removedValue, ...restOfId } = idValue;
    // Merge back together
    const finalState = { ...restOfState, [id]: restOfId };
    return finalState

  } else {
    return state
  }
}

function params(state = defaultVtypeParams, action) {
  if (action.type === 'FETCH_VTYPE') {
    return {
      ...state,
      vTypeFetched: false,
      isFetching: true
    }
  }
  if (action.type === 'FETCHED_VTYPE') {
    return {
      ...state,
      vTypeFetched: true,
      isFetching: false
    }
  } 
  if (action.type === 'FETCH_RLBVTYPE') {
    return {
      ...state,
      rlbvTypeFetched: false,
      isRlbvTypeFetching: true
    }
  }
  if (action.type === 'FETCHED_RLBVTYPE') {
    return {
      ...state,
      rlbvTypeFetched: true,
      isRlbvTypeFetching: false
    }
  }else {
    return state
  }
}


const vtype = combineReducers({
  byId,
  byRlbvTypeId,
  notifications,
  params

});

export default vtype;