import axios from 'axios';
import { merge } from 'lodash';
import { all, call, put, select, takeEvery, takeLatest } from 'redux-saga/effects';
import userACL from '../../../store/access';
import { ASSET_URL } from '../../../store/path';
// import { handlError, parseError } from '../../app/serverError';
import { fetchedLocation} from './actions';
import { getIsLocationFetched} from './selectors';


//#region Saga for List Tasks 

function* fetchLocation() {

  yield takeEvery('FETCH_LOCATION',requestLocation)

}
  
function* requestLocation(){
  console.log("can do")
  let { data } = yield call(requestLocationAPI)
  console.log("data=---------> ", data)
  console.log("data.data=---------> ", data.data)
  yield put (fetchedLocation(data.data))

}


function requestLocationAPI() {
  let data = merge({}, userACL.atFetch())  
  let locationCid=data.cid
  let boxId=data.boxId
  console.log("ASSET_URL==="+ASSET_URL)
  console.log("locationCid==="+locationCid)
  return axios.post(ASSET_URL+'/asset/get-segwise-godown/'+ locationCid +'/'+ boxId, data, {crossDomain: true})
}




const sagaErrorMessage = (error, action) => {
    console.group("Saga Error:"+action.type)
    console.log(error)
    console.groupEnd()
}

export default function* locationSaga() {
    yield all([
        fetchLocation(),

    ])
  }  