import { call, takeEvery, all, put, select } from 'redux-saga/effects'
import { getIsEmployeeFetched } from './selectors'
import { savedEmployee, fetchedEmployee, deletedEmployee } from './actions'
import axios from 'axios';
import { handlError, parseError } from '../../app/serverError'
import { ASSET_URL } from '../../../store/path'
import userACL from '../../../store/access' 
import { merge } from 'lodash' 

// const delay = (ms) => new Promise(res => setTimeout(res, ms))

//#region Saga for Create Employee 

function* createEmployee() {
    yield takeEvery('CREATE_EMPLOYEE',saveEmployee)
  }

function* saveEmployee(action){
  const { response, error } = yield call(saveEmployeeAPI, action.payload)
  console.log("Saga",action.payload)
  if (response) {
    if(response.data.type!=="error"){
    yield put (savedEmployee(action.payload))
  }
  }else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function saveEmployeeAPI(data) {
  // data.rlb_box_id=data.boxId
  //   delete data.boxId
    return axios.post(ASSET_URL+'/employee/add', data, {crossDomain: true})
      .then(response => ({ response }))
      .catch(error => ({ error }))
  }

//#endregion 



// 3BVGFH


//#region Saga for Edited Employee 

function* editEmployee() {
  yield takeEvery('EDIT_EMPLOYEE',saveEditedEmployee)
}

function* saveEditedEmployee(action){
  const { response, error } = yield call(editEmployeeAPI, action.payload)  
  if (response) {
    if(response.data.type!=="error"){
    yield put (savedEmployee(action.payload))
  }
  }else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function editEmployeeAPI(data) {
  let user_id = data.id
  // data.rlb_box_id=data.boxId
  //   delete data.boxId
  return axios.post(ASSET_URL+'/employee/update', data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion

//#region Saga for Delete Employee 

function* deleteEmployee() {
  yield takeEvery('DELETE_EMPLOYEE',saveDeleteEmployee)
}

function* saveDeleteEmployee(action){
  const { response, error } = yield call(deleteEmployeeAPI, action.payload)
  if (response) yield put (deletedEmployee({id: action.payload.id, msg : response.data}))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function deleteEmployeeAPI(data) {
  let employeeId = data.id
  data.rlb_box_id=data.boxId
    delete data.boxId
  return axios.post(ASSET_URL+'/employee/delete/'+ employeeId, data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for List Tasks 

function* fetchEmployee() {

  yield takeEvery('FETCH_EMPLOYEE',requestEmployee)

}
  
function* requestEmployee(){

  const isEmployeeFetched = yield select(getIsEmployeeFetched);
  if(!isEmployeeFetched){
    let { data } = yield call(requestEmployeeAPI)
    yield put (fetchedEmployee(data.data))
  }

}

function requestEmployeeAPI() {
  let data = merge({}, userACL.atFetch())  
  data.rlb_box_id=data.boxId
    delete data.boxId
  return axios.post(ASSET_URL+'/employee/list', data, {crossDomain: true})
}

//#endregion


const sagaErrorMessage = (error, action) => {
    console.group("Saga Error:"+action.type)
    console.log(error)
    console.groupEnd()
}

export default function* employeeSaga() {
    yield all([
        createEmployee(),
        deleteEmployee(),
        fetchEmployee(),
        editEmployee()
    ])
  }  