export const FormikAmountComponent = ({
  isLabel, label, saveOnBlur, decimal, compute, isTxn, path, placeholder, isMandatory, focus, width, fieldName, onFocus, classField, maxlength, fieldtype, readonly, tabindex,
  computeValue, fromField, otherList, 
  field, // { name, value, onChange, onBlur }
  index,
  form: { touched, errors, values, handleSubmit, handleBlur, setFieldValue, submitCount }, // also values, setXXXX, handleXXXX, dirty, isValid, status, etc.
  ...props
}) => (
    // <Form.Field error={errors[field.name] && touched[field.name]}>
    <Form.Field className={classField ? classField : null} width={width ? width : null} error={get(errors,field.name) && get(touched,field.name)}>
      {errors[field.name] && touched[field.name] && !isTxn ?
        <label>{errors[field.name]}</label> :
        <label>{isLabel ? null : label ? label: startCase(toLower(field.name))}{isMandatory ? <font color="red"> * </font> : null}</label>}
      <NumberFormat 
        thousandSeparator={true} 
        thousandsGroupStyle="lakh"
        decimalScale = {decimal ? decimal : 2 }
        placeholder={placeholder}
        name={field.name}
        value={field.value}
        maxLength={maxlength ? maxlength : 100}
        onValueChange={(val) => setFieldValue(field.name, val.floatValue)}
        onChange={(e) => {
          if(computeValue){
            computeValue(values, index, setFieldValue, field.name, fieldName, fromField, otherList, e.target.value, fieldtype)
          }
          
        }}
        onBlur={(e, val) =>{
          // e.persist()
          // handleBlur(e)
          if (compute) compute(values, index, setFieldValue, field.name, fieldName)
        }}
        autoFocus={focus ? focus : false}
        style={{textAlign: 'right'}}
        readOnly = {readonly ? readonly : false}
        tabIndex = {tabindex ? tabindex : 0}
      />
      {isTxn ? <ErrorMessage name={field.name} render= { msg => <Label size="mini" color='red' basic pointing prompt>{msg}</Label> }/>: null }
    </Form.Field>
  );

