export const vdetailsSchema = Yup.object().shape({

        id: Yup.string()
                .required('Required!'),

        date: Yup.string()
                .nullable(),

        txn_id: Yup.string()
                .nullable(),
        // .required('This is a Required Field'),

        item_id: Yup.string().nullable(),

        qty: Yup.number('Number Only!')
                .typeError('Required!')
                .positive('Positive Only!')
                .required('Required!'),

        rate: Yup.number('Number Only!')
                .typeError('Required!')
                .min(0),

        amount: Yup.number('Number Only!')
                .typeError('Required!')
                .min(0),

        status: Yup.number().integer().nullable(),
        segid: Yup.string().nullable(),
        cid: Yup.string().nullable(),
        dt_create: Yup.string().nullable(),
        dt_update: Yup.string().nullable(),
        user_create: Yup.string().nullable(),
        user_update: Yup.string().nullable()

});

export const callDataDepOnAssetType = debounce(fetchDataDepOnAssetType, 500);
function fetchDataDepOnAssetType(values, index, setFieldValue) {
        return axios
                .post(ASSET_URL + '/asset-type/' + values)
                .then(response => {
                        if (response) {
                                console.log(response.data.id, "=response.data.id")
                                if (response.data.id) {
                                        console.log("====================================call")
                                        setFieldValue('tdetails[' + index + '].ledger_cd', response.data.purc_ldgr_cd)
                                        setFieldValue('tdetails[' + index + '].ledger_name', response.data.purc_ldgr_name)
                                } else {
                                        setFieldValue('tdetails[' + index + '].ledger_cd', "")
                                        setFieldValue('tdetails[' + index + '].ledger_name', "")
                                }
                        } else {
                                setFieldValue('tdetails[' + index + '].ledger_cd', "")
                                setFieldValue('tdetails[' + index + '].ledger_name', "")
                        }
                })
                .catch(error => "")
}

export const fetchCost = debounce(fetchCostFromItem, 800);
function fetchCostFromItem(data, index, setFieldValue) {
        let cid = userACL.atFetch().cid,
        segid = userACL.atFetch().segid
        data.cid = cid
        data.segid = segid
        return axios.post(ASSET_URL + '/items/get-depreciation-by-itemi-id/' + data.id, data )
                .then(response => {
                        if (response) {
                                console.log(response.data, "=responsecost=---=-")
                                if (response.data.id) {
                                        setFieldValue('tdetails[' + index + '].cost', response.data.depreciation_amount)
                                } else {
                                        setFieldValue('tdetails[' + index + '].cost', '')
                                }
                        } else {
                                setFieldValue('tdetails[' + index + '].cost', '')
                        }
                }).catch(error => "")
}
