import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { fetchYear } from '../data/actions'
import * as select from '../data/selectors'
import { Field } from 'formik'
import { FormikSelectComponent } from '../../../utilities/formUtils'


const YearSelect = ({ name, isTxn, isLabel, isMandatory, placeholder, label, isSelection, computeValue, index, fieldName, fromField, otherList }, props) => {

    // const year = useSelector(state => state.year.byId) 
    // const selectedYear = useSelector(state => state.year.params.selected) 
    const yearFetched = useSelector(state => select.getIsYearFetched(state, props))
    const opt = useSelector(state => select.selectYear(state, props))
    const dispatch = useDispatch();

    // console.log("yearFetched============",yearFetched)

    useEffect(() => {
        if (!yearFetched)
            dispatch(fetchYear());
    },[]);


    const getSelectedValue = (values) => {
        // console.log("Year Selected", values, name)
    }


    if (!yearFetched && opt.length > 0) {
        return <p>Loading</p>
    } else {
        return (
            <Field name={name} isLabel={isLabel} placeholder={placeholder}isMandatory={isMandatory} isTxn={isTxn} label={label} isSelection={isSelection} component={FormikSelectComponent} computeValue={computeValue} index={index} fieldName={fieldName} fromField={fromField} otherList={otherList} userProps={{ options: opt, getValue: getSelectedValue }}> </Field>
        )
    }


}


export default YearSelect;
