import React from 'react';
import { Route, withRouter } from 'react-router';
import YearList from "./components/yearList";
import YearForm from "./components/yearForm";
import { Container } from 'semantic-ui-react'



const Year = () => {
    return (
        <Container>
            <Route path="/year/list" component={withRouter(YearList)} />
            <Route path="/year/edit/:id" component={YearForm} />
            <Route path="/year/create" component={YearForm} />
        </Container>
    )
};

export default Year;