import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { unitMasterSchema } from "../data/model";
import {
  createUnitMaster,
  editUnitMaster,
  fetchUnitMaster,
  setNotifyDone,
} from "../data/actions";
import { Button, Container, Header, Form } from "semantic-ui-react";
import { Formik, Field, Form as FormikForm } from "formik";
import userACL from "../../../store/access";
import { FormikInputComponent } from "../../../utilities/formUtils";
import {
  getUnitMaster,
  getUnitMasterParams,
  getNotification,
  selectUnit,
} from "../data/selectors";
import Notification from "../../../utilities/notificationUtils";
import v4 from "uuid";

const UnitMasterForm = (props) => {
  const dispatch = useDispatch();

  const unit = useSelector((state) => getUnitMaster(state, props));
  const unitList = useSelector((state) => state.unitmaster.byId);
  const params = useSelector((state) => getUnitMasterParams(state, props));
  const [savedUnitmasterId, setSavedUnitmasterId] = useState(false);

  useEffect(() => {
    dispatch(fetchUnitMaster());
  }, []);

  let x = Object.keys(unitList).map((item) => {
    return unitList[item].name;
  });

  const checkIfValueInUse = (valueToCheck) => {
    const existingValues = x;
    return existingValues.includes(valueToCheck);
  };

  const saveUnitMaster = (values, resetForm) => {
    if (props.match.path === "/unitmaster/create") {
      values.id = v4();
      userACL.atCreate(values);
      // values.segid = values.cid;
      values.rlb_box_id = values.boxId;
      delete values.txnid;
      delete values.boxId;
      dispatch(createUnitMaster(values));
      setSavedUnitmasterId(values.id);
      resetForm();
    }

    if (props.match.path === "/unitmaster/edit/:id") {
      userACL.atUpdate(values);
      // values.segid = values.cid;
      values.rlb_box_id = values.boxId;
      delete values.txnid;
      delete values.boxId;
      dispatch(editUnitMaster(values));
      setSavedUnitmasterId(values.id);
      setTimeout(function () {
        props.history.push(`/unitmaster/list`);
      }, 1000);
    }
  };

  const validate = (values) => {
    const errors = {};
    if (checkIfValueInUse(values.name)) {
      errors.name = "This Name Already Existing ";
    }
    return errors;
  };

  return (
    <Container>
      <Header as="h1">{params.title}</Header>
      <Formik
        initialValues={unit}
        validationSchema={unitMasterSchema}
        validate={validate}
        onSubmit={(values, { resetForm }) => saveUnitMaster(values, resetForm)}
        render={({
          handleSubmit,
          errors,
          onChange,
          values,
          handleChange,
          setFieldValue,
        }) => (
          <Form as={FormikForm} onSubmit={handleSubmit} className="CustomeForm">
            <Form.Group widths={3}>
              <Field
                name="name"
                label="Unit Name"
                component={FormikInputComponent}
                onChange={handleChange}
                isMandatory={true}
              />
            </Form.Group>
            <Form.Group widths={3}>
              <Field
                name="sname"
                label="Short Name"
                component={FormikInputComponent}
                isMandatory={true}
              />
            </Form.Group>

            <Form.Group widths={3}>
              <Field
                name="display_name"
                label="Display Name"
                component={FormikInputComponent}
                isMandatory={true}
              />
            </Form.Group>

            <Form.Group widths={3}>
              <Field
                name="decimal"
                label="Decimal"
                component={FormikInputComponent}
                isMandatory={true}
              />
            </Form.Group>

            <Button
              type="submit"
              size="medium"
              color="blue"
              className="CustomeBTN"
            >
              {" "}
              {params.submitButtonText}{" "}
            </Button>
            {savedUnitmasterId ? (
              <Notification
                id={savedUnitmasterId}
                notifySelector={getNotification}
                notifyDoneAction={setNotifyDone}
                type="save"
              />
            ) : null}
          </Form>
        )}
      />
    </Container>
  );
};

export default UnitMasterForm;
