import { ConnectedRouter } from 'connected-react-router';
import React from 'react';
import { Provider } from 'react-redux';
import {Route} from 'react-router';
import { history } from '../../configureStore';
import SideBar from './sidebar';
import Year from '../year/index';
import Assettype from '../assettype/index';
import Itemgroup from '../itemgroup/index'
import UnitMaster from '../unitmaster/index'
import Vtype from '../vtype/index';
import Employee from '../employee/index'
import Department from '../department/index'
import Items from '../items/index'
import Voucher from '../voucher/index'
import Dreport from '../dreport/index'
import ImportForm from '../importCreate/components/importForm';



import 'semantic-ui-less/semantic.less';
import { Container } from 'semantic-ui-react';
import importList from '../importCreate/components/importList';

// import "bootstrap/dist/css/bootstrap.min.css";

const App = ({ store }) => (
    <Provider store = {store}>
      <ConnectedRouter history={history} >
        <Container className="bodyHolder">
          <SideBar>
          <Route path="/year" component={Year} />
          <Route path="/assettype" component={Assettype} />
          <Route path="/itemgroup" component={Itemgroup} />
          <Route path="/unitmaster" component={UnitMaster} />
          <Route path='/vtype' component={Vtype} /> 
          <Route path="/employee" component={Employee} />
          <Route path="/department" component={Department} />
          <Route path="/items" component={Items} />
          <Route path="/voucher" component={Voucher} />
          <Route path="/dreport" component={Dreport} />
          <Route path="/import/create" component={ImportForm}/>
          <Route path="/import/list" component={importList}/>
          </SideBar>
        </Container>       
      </ConnectedRouter>
     </Provider>
    );


export default App;

