function checkAssetType(value, values) {
  const data = merge({ type: "check", id: values.id }, userACL.atFetch());
  // data.cid=data.segid
  data.rlb_box_id = data.boxId;
  let name = values.tname;
  delete data.boxId;
  if (value && value.length > 2)
    return axios
      .post(ASSET_URL + "/assettype/check/" + name, data)
      .then((response) => {
        if (response.data === "duplicate") {
          console.log("response", response.data);
          return "Duplicate";
        }
      })
      .catch((error) => "");
}

