  function* createFile() {
    yield takeEvery('CREATE_FILE', requestUploadProcess)
}

function* requestUploadProcess(action) {
    let { response, error } = yield call(requestUploadProcessAPI, action.payload);
    if (response) {
        yield put(savedFile(response.data))
        let getData = response.data
        if (getData.type === "error") {
            try {
                // const response1 = axios.post(CUSTOM_URL + "/exvspy/RecordError", getData, { crossDomain: true })
                // yield put(repostProcessListRes(response.data))
                yield put(savedFile(response.data))
                // return response
            }
            catch (error) {
                return ({ error })
            }
        }
    } else {
        console.log(error)
        yield put(savedFile({ "type": "error", "msg": "Error occured" }))
    }
}

async function requestUploadProcessAPI(data) {
    try {
        let response = await axios.post(ASSET_URL + "/excelimport/add", data, { crossDomain: true })
        return ({ response })
    }
    catch (error) {
        return ({ error })
    }

}

//file upload list reigion

function* fetchFileList() {
  yield takeEvery('FETCH_FILE_LIST', requestFileList)
}

function* requestFileList(action) {
  let { response, error } = yield call(requestFileListAPI, action.payload);
  if (response) {
      yield put(fetchedFileList(response.data.data))
  } else {
      console.log(error)
      yield put(fetchedFileList({ "type": "error", "msg": "Error occured" }))
  }
}

async function requestFileListAPI(data) {
  data = merge({}, data, userACL.atFetch())
  // formData.append("cid", data.cid);
  // formData.append("segid", data.segid);
  // formData.append("rlb_box_id", data.rlb_box_id);
  // formData.append("from_dt", data.from_dt);
  // formData.append("to_dt", data.to_dt);
  try {
      const response = await axios.post(ASSET_URL + "/excelimport/list",data,  { crossDomain: true })
      return ({ response })
  }
  catch (error) {
      return ({ error })
  }

}

//download file

function* downloadFile() {
    yield takeEvery('DOWNLOAD_FILE',requestDownloadFile)
  }
  function* requestDownloadFile(action){
      let { response } = yield call(requestDownloadFileAPI, action.payload)
      console.log("action=====",action)
    // let { data } = yield call(downloadFileApi, action.payload)
    if (response) {
      console.log("response download",action.payload)
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement("a");
      link.href = url;
      link.setAttribute("download",action.payload+".xlsx");
      // link.setAttribute("download", action.payload.depreciation+action.payload.fy_id+".xlsx");
      document.body.appendChild(link);
      link.click();
      yield put(downloadedFile(response))
  }  
  }
  
  function requestDownloadFileAPI(data) {
//   data=merge({}, data, userACL.atFetch())
    console.log(data)
    const request_config = {
      method: "GET",
      url: ASSET_URL + '/excelimport/DownloadFile/'+data,
      data: data,
      responseType: "blob",
      headers: { "Content-Type": "application/json" },
  };
  return axios(request_config, { crossDomain: true })
  .then(response => ({ response }),
  (error) => {console.log(error);}
  );
  }


