import { call, takeEvery, all, put, select } from 'redux-saga/effects'
import { getIsAssettypeFetched } from './selectors'
import { savedAssettype, fetchedAssettype, deletedAssettype, storeSearchedSegList } from './actions'
import axios from 'axios';
import { handlError, parseError } from '../../app/serverError'
import { ASSET_URL } from '../../../store/path'
import userACL from '../../../store/access'
import { merge } from 'lodash'

// const delay = (ms) => new Promise(res => setTimeout(res, ms))

//#region Saga for Create Assettype 

function* createAssettype() {
  yield takeEvery('CREATE_ASSETTYPE', saveAssettype)
}

function* saveAssettype(action) {
  const { response, error } = yield call(saveAssettypeAPI, action.payload)
  console.log("Saga", action.payload)
  if (response){
    if(response.data.type!=="error")
     yield put(savedAssettype(action.payload))}
  else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
  }
}

function saveAssettypeAPI(data) {
  return axios.post(ASSET_URL + '/assettype/add', data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for Edited Assettype 

function* editAssettype() {
  yield takeEvery('EDIT_ASSETTYPE', saveEditedAssettype)
}

function* saveEditedAssettype(action) {
  const { response, error } = yield call(editAssettypeAPI, action.payload)
  if (response) {
    if(response.data.type!=="error"){
    yield put(savedAssettype(action.payload))
    console.log("sagaErrorMessage========",response.data.msg)
  }}
  else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
  }
}

function editAssettypeAPI(data) {
  
  let user_id = data.id
  // data.rlb_box_id=data.boxId
  // delete data.boxId
  return axios.post(ASSET_URL + '/assettype/update/' + user_id, data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion

//#region Saga for Delete Assettype 

function* deleteAssettype() {
  yield takeEvery('DELETE_ASSETTYPE', saveDeleteAssettype)
}

function* saveDeleteAssettype(action) {
  const { response, error } = yield call(deleteAssettypeAPI, action.payload)
  if (response){
    if(response.data.type!=="error")
     yield put(deletedAssettype({ id: action.payload.id, msg: response.data }))
     console.log("sagaErrorMessage========",response.data.msg)
  }else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
    
  }
}

function deleteAssettypeAPI(data) {
  let assettypeId = data.id
  // data.rlb_box_id=data.boxId
  // delete data.txnId
  // delete data.boxId
  return axios.post(ASSET_URL + '/assettype/delete/' + assettypeId, data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for List Tasks 

function* fetchAssettype() {

  yield takeEvery('FETCH_ASSETTYPE', requestAssettype)

}

function* requestAssettype(action) {

  const isAssettypeFetched = yield select(getIsAssettypeFetched);
  if (!isAssettypeFetched) {
    let { data } = yield call(requestAssettypeAPI)
    yield put(fetchedAssettype(data.data))
  }

}

function requestAssettypeAPI() {
  // let data = merge({}, userACL.atFetch())
  // let data = {}
  // data.cid = userACL.atFetch().cid
  let data = merge({}, userACL.atFetch())
  // data.segid = data.cid
  data.rlb_box_id=data.boxId
  delete data.boxId
  return axios.post(ASSET_URL + '/assettype/list', data, { crossDomain: true })
}

//#endregion

function* searchSegList() {
  yield takeEvery('SEARCH_SEG_LIST', initiateSegListSearch)
}

function* initiateSegListSearch(action) {
  let { data } = yield call(searchSegListAPI)
  // console.log(data.data, '------------seglist-payload')
  yield put(storeSearchedSegList(data.data))
}

function searchSegListAPI() {
  let data = merge({}, userACL.atFetch()) 
  return axios.post(ASSET_URL + '/get-seglist', data, { crossDomain: true })
}


const sagaErrorMessage = (error, action) => {
  console.group("Saga Error:" + action.type)
  console.log(error)
  console.groupEnd()
}

export default function* assettypeSaga() {
  yield all([
    createAssettype(),
    deleteAssettype(),
    fetchAssettype(),
    editAssettype(),
    searchSegList()
  ])
}  