import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { requestOptionsItemgroup } from '../data/actions'
import * as select from '../data/selectors'
import { Field } from 'formik'
import { FormikAsyncSelectComponent } from '../../../utilities/formUtils'
import { filter, includes } from 'lodash'


const ItemgroupSelectAsync = ({ name, isTxn, isLabel, label, values, isSelection, placeholder, getValue : callback  }, props) => {

    const itemgroupFetched = useSelector(state => state.itemgroup.params.itemgroupFetched)
    const serachOptions = useSelector(state => select.selectItemgroupOptions(state, props))
    const options = useSelector(state => select.selectItemgroupParent(state, props, values.id))
    const dispatch = useDispatch()
    const [loadoptions, setloadoptions] = useState(()=>'')

    // useEffect(()=>{
    //     if (serachOptions.length>0) {
    //         const res = filter(serachOptions, (n) => { return !includes(n.path,values.id) })
    //         setloadoptions(res)
    //     }
    // },[]);  

    const getInputValue = (value, id) => {
        dispatch(requestOptionsItemgroup({str: value, id: id}));
    }

    const getValue = (value) => {
        callback(value)
    }

    // Loading Logic if tasks not fetched
    // if (!itemgroupFetched && options.length > 0 ){
    //     return <p>Loading</p>
    // }  else {        
        return (
            <Field name={name} isLabel={isLabel} isTxn={isTxn} label={label} isSelection={isSelection} component={FormikAsyncSelectComponent} userProps={{ options, searchOptions: serachOptions, getInputValue, getValue }} placeholder={placeholder}> </Field>
        )
    }
// }

export default ItemgroupSelectAsync;
