import * as type from './types'

export function addVdetails(vdetails) {
    return {
        type : type.CREATE_VDETAILS,
        payload : vdetails,
        txn : 'initiated',
    };
}

export function savedVdetails(vdetails) {
    return {
        type : type.SAVED_VDETAILS,
        payload : vdetails,
        txn : 'draft',
        // diff : true
    };
}

export function editVdetails(vdetails) {
    return {
        type : type.EDIT_VDETAILS,
        payload : vdetails,
        txn : 'initiated',
    };
}

export function fetchVdetails() {
    return {
        type : type.FETCH_VDETAILS,
    };
}

export function fetchedVdetails(vdetails) {
    return {
        type : type.FETCHED_VDETAILS,
        payload : vdetails
    };
}