import { combineReducers } from 'redux';
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr';

// Define a locations schema
const locationSchema = new schema.Entity('locations',{}, { idAttribute: 'segement_id' });
const locationListSchema = [locationSchema];

const defaultLocationParams = {
  locationFetched: false,
  segmentFetched: false,
  locationDepsOnItemFetched: false,
  searchFetched: false,
  createTitle: 'Create New Location',
  createSubmitButtonText: 'Submit',
  editTitle: 'Edit Location',
  editSubmitButtonText: 'Submit' 
}


function byId(state = {}, action) {
    if (action.type === 'SAVED_LOCATION') {
      const locationID = action.payload.id;
      return {...state, 
        [locationID]: action.payload
      }
    }
    if (action.type === 'FETCHED_LOCATION') { 
        const normalizedLocation = normalize(action.payload, locationListSchema);
        return merge({},state,normalizedLocation.entities.locations)
    }
    if (action.type === 'DELETED_LOCATION') { 
      if(action.payload.msg === "Success"){
        const locationID = action.payload.id;
        let finalState = {...state}
        delete finalState[locationID]
        return finalState
      }else{
        return state
      }
    } else {
      return state
    }
  }

function searchResults(state = {}, action) {
  if (action.type === 'STORE_SEARCH_LOCATION_RESULT') { 
    const normalizedLocation = normalize(action.payload, locationListSchema);
    return merge({},normalizedLocation.entities.locations)
} else {
    return state
  }
}
    

function params(state = defaultLocationParams, action) {
  if (action.type === 'FETCHED_LOCATION') { 
      return {...state, 
        locationFetched: true,
        isFetching:false
      }
  } if (action.type === 'SEARCH_LOCATION') { 
    return {...state, 
      searchFetched: false,
      isSearching: true
    }
  } if (action.type === 'FETCHED_SEGMENT_LIST') { 
    return {...state, 
      segmentFetched: true
    }
  } if(action.type === 'FETCHED_LOCATION_DEPS_ON_ITEM'){
    return {...state, 
      locationDepsOnItemFetched: true
    }

  }
  if (action.type === 'STORE_SEARCH_LOCATION_RESULT') { 
    return {...state, 
      searchFetched: true,
      isSearching : false
    }
  }else {
    return state
  }
}

function segmentList(state = {}, action) {
  if (action.type === 'FETCHED_SEGMENT_LIST') { 
    const normalizedLocation = normalize(action.payload, locationListSchema);
    return merge({},state,normalizedLocation.entities.locations)
  }else{
    return state
  }
}

function locationSegmentList(state = {}, action) {
  if (action.type === 'FETCHED_LOCATION_DEPS_ON_ITEM') { 
    const normalizedLocation = normalize(action.payload, locationListSchema);
    return merge({},state,normalizedLocation.entities.locations)
  }else{
    return state
  }
}

function notifications(state = {}, action) {
  if (action.type === 'SAVED_LOCATION') { 
    return {...state, 
      [action.payload.id]: { ...state[action.payload.id],
        save: { 
          status : action.txn === "success" ? 'success' : 'error',
          msg : action.txn
        }
      }
    }
  }
  if (action.type === 'DELETED_LOCATION') {
    return {...state, 
      [action.payload.id]: { ...state[action.payload.id],
        delete: { 
          status : action.payload.msg === "Success" ? 'success' : 'error',
          msg : action.payload.msg
        }
      }
    }
  } 
  if(action.type === 'NOTIFICATION_DONE_LOCATION'){
    const {id, type} = action.payload
    // Remove the 'id' element from state
    const { [id]: idValue, ...restOfState } = state;
    // Remove the 'type' from the 'id' element
    const { [type]: removedValue, ...restOfId } = idValue;
    // Merge back together
    const finalState = { ...restOfState, [id]: restOfId };     
    return finalState

  } else {
    return state
  }
}

function locationData(state = {}, action){
  if (action.type === 'FETCHED_LOCATION') { 
      return {...action.payload}
  }else{
    return state
  }
} 


const location = combineReducers({
    byId,
    notifications,
    searchResults,
    segmentList,
    locationSegmentList,
    params,
    locationData,
});

export default location;

