import { combineReducers } from 'redux';

// Define a ledger schema


const defaultLedgerParams = {
    searchFetched: false,
    isSearching: true
  }

  
function searchLedgerResults(state = {}, action) {
    if (action.type === 'STORE_SEARCH_LEDGER_RESULT') { 
      // console.log("action.payload================> ", action.payload)
      return action.payload
  } else {
      return state
    }
  }

// function byLedgerId(){
//   if(action.type)
// }

function ledgerObj(state = {}, action) {
    if (action.type === 'SET_LEDGER_OBJ') { 
      return action.payload
  } else {
      return state
    }
  }
  
function params(state = defaultLedgerParams, action) {
    if (action.type === 'STORE_SEARCH_LEDGER_RESULT') { 
      return {...state, 
        searchFetched: true,
        isSearching : false,
      }
    } else {
      return state
    }
  }

  const ledgers = combineReducers({
        searchLedgerResults,
        params,
        ledgerObj
  });
  
  export default ledgers;
  