import { combineReducers } from 'redux';
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr';

// Define a depreciations schema
const depreciationSchema = new schema.Entity('depreciations',{}, { idAttribute: 'id' });
const depreciationListSchema = [depreciationSchema];

const defaultDepreciationParams = {
  depreciationFetched: false,
  createTitle: 'Create New Depreciation',
  createSubmitButtonText: 'Submit',
  editTitle: 'Edit Depreciation',
  editSubmitButtonText: 'Submit' 
}


function byId(state = {}, action) {
    if (action.type === 'SAVED_DEPRECIATION') {
      const depreciationID = action.payload.id;
      return {...state, 
        [depreciationID]: action.payload
      }
    }
    if (action.type === 'FETCHED_DEPRECIATION') { 
        const normalizedDepreciation = normalize(action.payload, depreciationListSchema);
        return merge({},state,normalizedDepreciation.entities.depreciations)
    }
    if (action.type === 'DELETED_DEPRECIATION') {
      if(action.payload.msg === "Success"){ 
        const depreciationID = action.payload.id;
        let finalState = {...state}
        delete finalState[depreciationID]
        return finalState
      }else{
        return state
      }
    }else {
      return state
    }
  }

function params(state = defaultDepreciationParams, action) {
  if (action.type === 'FETCHED_DEPRECIATION') { 
      return {...state, 
        depreciationFetched: true,
        isFetching:false
      }
  } else {
    return state
  }
}


function notifications(state = {}, action) {
  if (action.type === 'SAVED_DEPRECIATION') {
    return {...state, 
      [action.payload.id]: { ...state[action.payload.id],
        save: { 
          status : action.txn === "success" ? 'success' : 'error',
          msg : action.txn
        }
      }
    }
  }
  if (action.type === 'DELETED_DEPRECIATION') { 
    return {...state, 
      [action.payload.id]: { ...state[action.payload.id],
        delete: { 
          status : action.payload.msg === "Success" ? 'success' : 'error',
          msg : action.payload.msg
        }
      }
    }
  } 
  if(action.type === 'NOTIFICATION_DONE_DEPRECIATION'){
    const {id, type} = action.payload
    // Remove the 'id' element from state
    const { [id]: idValue, ...restOfState } = state;
    // Remove the 'type' from the 'id' element
    const { [type]: removedValue, ...restOfId } = idValue;
    // Merge back together
    const finalState = { ...restOfState, [id]: restOfId };     
    return finalState

  } else {
    return state
  }
}

const depreciation = combineReducers({
    byId,
    notifications,
    params
});

export default depreciation;

