import { createSelector } from 'reselect'
import { startsWith, at, cloneDeep } from 'lodash';
import { newVdetails } from './model'
export const getIsFetchingVdetails = (state, props) => state.vdetails.params.isFetching;
export const getIsVdetailsFetched = (state, props) => state.vdetails.params.vdetailsFetched;
export const getVdetailsList = (state, prop) => state.vdetails.byId
// const orgSelected = (state) => state.org.params.selected;

export const getVdetails = (state, vid) => {
    let vdetailsFromVid = at(state.vdetails.ById, vid)
    if (vdetailsFromVid[0] === undefined)
        vdetailsFromVid = [newVdetails()]
    return vdetailsFromVid
}

export const getVdetailsParams = (state, props) => {
    const params = {}

    if (startsWith(state.router.location.pathname, '/voucher/create')) {
        params.title = state.vdetails.params.createTitle
        params.submitButtonText = state.vdetails.params.createSubmitButtonText
    };

    if (startsWith(state.router.location.pathname, '/voucher/edit/')) {
        params.title = state.vdetails.params.editTitle
        params.submitButtonText = state.vdetails.params.editSubmitButtonText
    };

    return params
}

export const selectVdetails = createSelector(
    getVdetailsList,
    vdetails => {
        const keys = Object.keys(vdetails)
        const obj = keys.map((key) => { return { value: key, label: vdetails[key].name } })
        return obj
    })

export const getCost = (state) => state.voucher.searchResults