import { createSelector } from 'reselect'
import { startsWith, cloneDeep } from 'lodash';
import { employee } from './model'


export const getIsFetchingEmployee = (state, props) => state.employee.params.isFetching;
export const getIsEmployeeFetched = (state, props) => state.employee.params.employeeFetched;
export const getEmployeeList = (state, prop) => state.employee.byId

export const getNotification = (state, id) => state.employee.notifications[id]

const orgSelected = (state) => state.org.params.selected;

export const getEmployee = (state, props) => {
    if(props.match.path==='/employee/create'){
        return employee()
    }
    if(props.match.path==='/employee/edit/:id'){
        let _id = props.match.params.id
        let obj = cloneDeep(state.employee.byId[_id]);
        return obj        
    }
}

export const getEmployeeParams = (state, props) => {
    const params =  {}

    if(startsWith(state.router.location.pathname,'/employee/create')){ 
        params.title = state.employee.params.createTitle
        params.submitButtonText = state.employee.params.createSubmitButtonText        
    };

    if(startsWith(state.router.location.pathname,'/employee/edit/')){ 
        params.title = state.employee.params.editTitle
        params.submitButtonText = state.employee.params.editSubmitButtonText        
    };

    return params
}

export const selectEmployee = createSelector(
    getEmployeeList,
    employee => { 
        const keys = Object.keys(employee)
        // const obj = keys.map( (key) => { return { value : key, label : employee[key].name }})
        const obj = keys.map( (key) => { return { key : key,  value : key, text : employee[key].emp_name }})
        return obj
    })
