Yup.addMethod(Yup.date, 'format', parseDateFromFormats);
export const locationSchema = Yup.object().shape({

    id: Yup.string()
            .required('ID is a Required Field'),

    map_segid: Yup.string()
            .required('Segement Required')
            .nullable(),

    sname: Yup.string()
            .min(2, 'Minimum 2 Characters for Short Name is Needed')
            .max(6, 'Maximum 6 Characters for Short Name is allowed')
            .required('Short Name Required'),

    location: Yup.string()
            .min(2, 'Minimum 2 Characters for Location is needed')
            .max(50, 'Maximum 50 Characters for Location is allowed')
            .required('Location Required'),

    city: Yup.string()
            .required('City Required'),

//     country: Yup.string()
//             .required('Country is a Required Field'),

    status: Yup.number().integer().nullable(),
    segid: Yup.string().nullable(),
    cid: Yup.string().nullable(),
    txnid : Yup.string().nullable(),
    dt_create: Yup.string().nullable(),
    dt_update: Yup.string().nullable(),
    user_create: Yup.string().nullable(),
    user_update: Yup.string().nullable()

  });


