import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'
import { fetchEmployee, deleteEmployee, setNotifyDone } from '../data/actions'
import * as select from '../data/selectors'
import { Container, Table, TableHeader, TableBody, Header, Button, Modal } from 'semantic-ui-react'
import userACL from '../../../store/access'
import Notification from '../../../utilities/notificationUtils'



const Employee = props => {
    const dispatch = useDispatch();
    const employeeFetched = useSelector(state => select.getIsEmployeeFetched(state, props))

    const isNegative = props.employee.status === 1 ? false : true;
    const [isModalOpen, setModalOpen] = useState(false)
    const location = useSelector(state => state.location.byId)

    const [deleteEmployeeId, setDeleteEmployeeId] = useState(false);

    // useEffect(() => {
    //     if (!employeeFetched)
    //         dispatch(fetchEmployee())
    // }, [])


    const deleteEmployeeFromList = (values) => {
        const getVlaue = { id: values }
        userACL.atUpdate(getVlaue)
        dispatch(deleteEmployee(getVlaue))
        setDeleteEmployeeId(values.id)
        setModalOpen(false)
    }

    const closeModal = () => {
        setModalOpen(false)
    }

    return (
        <Table.Row negative={isNegative}>
            <Table.Cell>{props.employee.emp_name}</Table.Cell>
            <Table.Cell>{props.employee.emp_code}</Table.Cell>
            <Table.Cell>{props.employee.loc_id !== null && props.employee.loc_id !== undefined && props.employee.loc_id !== "" ? location[props.employee.loc_id] ? location[props.employee.loc_id].segement_name : "" : ""}</Table.Cell>
            {console.log(location)}
            <Table.Cell>
                {isNegative === false ?
                    <>
                        <Link to={"/employee/edit/" + props.employee.id}>Edit</Link><span>&nbsp;&nbsp;</span>
                        <span style={{ cursor: 'pointer', color: 'red' }} onClick={() => { setModalOpen(true) }}>Delete</span>
                    </>
                    :
                    <span>Deleted</span>
                }
                <>
                    {
                        deleteEmployeeId ?
                            <Notification id={deleteEmployeeId} notifySelector={select.getNotification} notifyDoneAction={setNotifyDone} type='delete' />
                            :
                            null
                    }
                </>
                <Modal open={isModalOpen} size="mini">
                    <Modal.Header>Employee List</Modal.Header>
                    <Modal.Content>
                        <h4>Do you want to delete this row??</h4>
                    </Modal.Content>
                    <Modal.Actions>
                        <Button type="button" positive onClick={() => closeModal()}>No</Button>
                        <Button type="button" color='red' onClick={() => deleteEmployeeFromList(props.employee.id)}>Yes</Button>

                    </Modal.Actions>
                </Modal>
            </Table.Cell>
        </Table.Row>
    )
}

const EmployeeList = (props) => {

    const employee = useSelector(state => state.employee.byId)
    const employeeFetched = useSelector(state => select.getIsEmployeeFetched(state, props))
    const dispatch = useDispatch();

    useEffect(() => {
        if (!employeeFetched)
            dispatch(fetchEmployee());
    });

    const tableStyle = {
        header: {
            position: "fixed",
            width: "100%",
            backgroundColor: "#fff"
        },
        tableHead: {
            overflowY: "auto",
            height: '580px',
            backgroundColor: "#fff"
        },
        table: {
            borderCollapse: "collapse",
            width: '100%',
            marginTop:"40px"
        },
        th: {
            position: "sticky",
            backgroundColor: "#fff",
            top: "28px",
            zIndex: "1",
            padding: "25px 0"
        }
    }

    // Loading Logic if tasks not fetched
    if (!employeeFetched) {
        return <p>Loading ... </p>
    } else {
        return (
            <Container style={tableStyle.tableHead}>
                <Header as='h1' align='left' style={tableStyle.header}>Employee List</Header>
                <Table selectable basic='very' style={tableStyle.table}>
                    <TableHeader>
                        <Table.Row>
                            <Table.HeaderCell style={tableStyle.th}>Employee Name</Table.HeaderCell>
                            <Table.HeaderCell style={tableStyle.th}>Employee Code</Table.HeaderCell>
                            <Table.HeaderCell style={tableStyle.th}>Location</Table.HeaderCell>
                            <Table.HeaderCell style={tableStyle.th}>Action</Table.HeaderCell>
                        </Table.Row>
                    </TableHeader>
                    <TableBody>
                        {Object.keys(employee).map((key) =>
                            <Employee employee={employee[key]} key={employee[key].id} />)}
                    </TableBody>
                </Table>
            </Container>
        )
    }
}

export default EmployeeList;
