import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'
import { fetchYear,fetchedYear } from '../data/actions'
import * as select from '../data/selectors'
import { Container, Table, TableHeader, TableBody, Header, Loader } from 'semantic-ui-react'
import moment from 'moment'
import { List } from 'react-virtualized';

// Year List
const Year = props => (
    <Table.Row>
        {console.log(props.year.name)}
        <Table.Cell>{props.year.name}</Table.Cell>
        <Table.Cell>{props.year.sname}</Table.Cell>
        <Table.Cell>{props.year.book_dep}</Table.Cell>
        <Table.Cell>{props.year.it_dep}</Table.Cell>
        <Table.Cell>{moment(props.year.from_dt).isValid() ? moment(props.year.from_dt).format("DD-MM-YYYY") : ""}</Table.Cell>
        <Table.Cell>{moment(props.year.to_dt).isValid() ? moment(props.year.to_dt).format("DD-MM-YYYY") : ""}</Table.Cell>
        <Table.Cell>
            <Link to={"/year/edit/" + props.year.id}>Edit</Link>
        </Table.Cell>
    </Table.Row>
)

const YearList = (props) => {


    const tableStyle = {
        header: {
            position: "fixed",
            width: "100%",
            backgroundColor: "#fff"
        },
        tableHead: {
            overflowY: "auto",
            height: '580px', 
            backgroundColor: "#fff",
        },
        
        table: {
            borderCollapse: "collapse",
            width: '100%',
            marginTop:"40px"
        },
        th: {
            position: "sticky",
            backgroundColor: "#fff",
            top: "28px",
            zIndex: "1",
            padding: "25px 0"
        },
      
    }


    const year = useSelector((state) => state.year.byId)
    const yearFetched = useSelector(state => select.getIsYearFetched(state, props))
    // const fetchedYear=useSelector(state=>state.year.params.isFetching)
    const dispatch = useDispatch();

    useEffect(() => {
        if(!yearFetched)
        dispatch(fetchYear());
    });

    // Loading Logic if tasks not fetched
    if (!yearFetched) {
        return <Loader active inline='centered' style={{marginTop:"150px"}}>Loading ... </Loader>
    } else {
        return (
            <Container style={tableStyle.tableHead}>
                {console.log(year)}
                <Header as='h1' align='left' style={tableStyle.header}>Year List</Header>
                <Table selectable basic='very' style={tableStyle.table}>
                    <TableHeader>

                        <Table.Row>
                            <Table.HeaderCell style={tableStyle.th}>Name</Table.HeaderCell>
                            <Table.HeaderCell style={tableStyle.th}>Short Name</Table.HeaderCell>
                            <Table.HeaderCell style={tableStyle.th}>Book Depreciation</Table.HeaderCell>
                            <Table.HeaderCell style={tableStyle.th}>IT Depreciation</Table.HeaderCell>
                            <Table.HeaderCell style={tableStyle.th}>From Date</Table.HeaderCell>
                            <Table.HeaderCell style={tableStyle.th}>To Date</Table.HeaderCell>
                            <Table.HeaderCell style={tableStyle.th}>Action</Table.HeaderCell>
                        </Table.Row>

                    </TableHeader>
                    <TableBody>
                        {Object.keys(year).map((key) =>
                            <Year year={year[key]} key={year[key].id} />)}
                    </TableBody>
                </Table>
            </Container>
        )
    }
}

export default YearList;