import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux'
import { Field } from 'formik';
import { Grid, GridColumn, Button, Form, Table, Icon, Modal } from 'semantic-ui-react';
import "react-datepicker/dist/react-datepicker.css";
import { FormikInputComponent, FormikAmountComponent } from '../../../utilities/formUtils';
import { assetTypeNumbering } from '../data/model';
import userACL from '../../../store/access'
import SegmentSearch from './segmentSelect';



let rank = 1;
const AssetTypeNumberingArray = ({
  name, move, swap, push, insert, unshift, remove, pop,  // { name, value, onChange, onBlur }
  form: { initialValues, values, errors }, // also values, setXXXX, handleXXXX, dirty, isValid, status, etc.
  ...props
}) => {

  const [isModalOpen, setIsModalOpen] = useState({ status: false, msg: '' })

  let arrKeys = [];
  const fieldName = name;
  if (values[fieldName] && values[fieldName].length) {
    arrKeys = Object.keys(values[fieldName]);
  }
  const [rank, setRank] = useState(1)

  const getNewLineItem = () => {
    let assetNum = assetTypeNumbering()
    userACL.atCreate(assetNum)
    assetNum.atype_id = initialValues.id
    assetNum.segid = ""
    push(assetNum)
    setRank(rank + 1)
  }

  const callToRemove = (index, remove) => {
    if (values[fieldName].length > 1) {
      remove(index);
    } else {
      setIsModalOpen({ status: true, msg: 'You are not allowed to delete remaining last row' })
    }
  }

  return (
    <>
      <Table basic='very'>
        <Table.Header className="TableHeader">
          <Table.Row key="header" className="stickyHead">
            <Table.HeaderCell width={1}>Segment</Table.HeaderCell>
            <Table.HeaderCell width={1}>Number Prefix</Table.HeaderCell>
            <Table.HeaderCell width={1}>Number Suffix</Table.HeaderCell>
            <Table.HeaderCell width={1}>Next Number</Table.HeaderCell>
            <Table.HeaderCell width={1}>Number Count</Table.HeaderCell>
            <Table.HeaderCell width={1}></Table.HeaderCell>
          </Table.Row>
        </Table.Header>
        <Table.Body>


          {values[fieldName] && values[fieldName].length > 0 ? (
            arrKeys.map((index) => (
              <Table.Row key={`${fieldName}[${index}]`} className="VInputPanel ItemVInputPanel">
                <Table.Cell width={3} className="border-bottom">
                  <SegmentSearch name={`${fieldName}[${index}].segid`} isLabel='false' isTxn='false' isSelection={true} className="paddingTop" />
                </Table.Cell>
                <Table.Cell width={2}>
                  <Field name={`${fieldName}[${index}].number_prefix`} component={FormikInputComponent} isLabel='false' isTxn='true' />
                </Table.Cell>

                <Table.Cell width={2}>
                  <Field name={`${fieldName}[${index}].number_suffix`} component={FormikInputComponent} isLabel='false' isTxn='true' />
                </Table.Cell>

                <Table.Cell width={2}>
                  <Field name={`${fieldName}[${index}].next_number`} component={FormikAmountComponent} isLabel='false' isTxn='true' />
                </Table.Cell>

                <Table.Cell width={2}>
                  <Field name={`${fieldName}[${index}].number_count`} component={FormikAmountComponent} isLabel='false' isTxn='true' />
                </Table.Cell>

                <Table.Cell className="btnpanel" width={1}>
                  <Icon title='Add Row' className="fontSizeAddDeleteIcon AddIcon commonBtnIcon" name='add' color='green' onClick={() => getNewLineItem()} style={{ cursor: 'pointer' }} />
                  <Icon title='Delete Row' className="fontSizeAddDeleteIcon commonBtnIcon" name='cancel' color='red' onClick={(e) => callToRemove(index, remove)} style={{ cursor: 'pointer' }} />
                </Table.Cell>
              </Table.Row>
            ))) : null}
        </Table.Body>
      </Table>
      <Modal open={isModalOpen.status} size="mini">
        <Modal.Header>Error</Modal.Header>
        <Modal.Content>
          <p>{isModalOpen.msg}</p>
        </Modal.Content>
        <Modal.Actions>
          <Button type="button" negative icon='thumbs down outline' content='Okay' labelPosition='right' onClick={() => { setIsModalOpen({ status: false, msg: '' }) }} />
        </Modal.Actions>
      </Modal>
    </>
  )
}

export default AssetTypeNumberingArray;


