import { createSelector } from 'reselect'
import { startsWith, cloneDeep } from 'lodash';
import { vType } from './model'

export const getIsFetchingVtype = (state, props) => state.vtype.params.isFetching;
export const getIsVtypeFetched = (state, props) => state.vtype.params.vTypeFetched;
export const getIsRlbVtypeFetched = (state, props) => state.vtype.params.rlbvTypeFetched;
export const getVtypeList = (state, props) => state.vtype.byId
export const getVtypeObj = (state, id) => state.vtype.byId[id]
export const getrlbVtypeList = (state, props) => state.vtype.byRlbvTypeId
export const getrlbVtypeObj = (state, id) => state.vtype.byRlbvTypeId[id]
export const getNotification = (state, id) => state.vtype.notifications[id]

export const getVtype = (state, props) => {
    if(props.match.path==='/vtype/create'){
        return vType()
    }
    if(props.match.path==='/vtype/edit/:id'){
        let _id = props.match.params.id
        let obj = cloneDeep(state.vtype.byId[_id]);
        return obj    
    }
}



export const getVtypeParams = (state, props) => {
    const params =  {}
    
    if(startsWith(state.router.location.pathname,'/vtype/create')){ 
        params.title = state.vtype.params.createTitle
        params.submitButtonText = state.vtype.params.createSubmitButtonText        
    };
        
    if(startsWith(state.router.location.pathname,'/vtype/edit/')){ 
        params.title = state.vtype.params.editTitle
        params.submitButtonText = state.vtype.params.editSubmitButtonText        
    };

    return params
}

export const selectVType = (state, props ) => {
    const vtype = state.vtype.byId
    const keys = Object.keys(vtype)
    const obj = keys.map( (key) => { 
        return { key : key,  value : key, text : vtype[key].name, nature: vtype[key].nature}
    });
    return obj
            
}

export const selectRlbVType=(state,props)=>{
    const vtype = state.vtype.byRlbvTypeId
    const keys = Object.keys(vtype)
    const obj = keys.map( (key) => { 
        return { key : key,  value :vtype[key].nature, text : vtype[key].nature, rlb_vtype: vtype[key].nature}
    });
    return obj
}
