import { createSelector } from 'reselect'
import { startsWith, cloneDeep, toLower,at } from 'lodash';
import { itemgroup } from './model'

export const getIsFetchingItemgroup = (state, props) => state.itemgroup.params.isFetching;
export const getIsItemgroupFetched = (state, props) => state.itemgroup.params.itemgroupFetched;
export const getIsFetchingOptions = (state, props) => state.itemgroup.params.isOptionsFetching;
export const getIsOptionsFetched = (state, props) => state.itemgroup.params.optionsFetched;
export const getItemgroupList = (state, prop) => state.itemgroup.byId
export const itemSelectorList = (state, prop) => state.items.byListId;
export const getOptions = (state, prop) => state.itemgroup.options
export const getNotification = (state, id) => state.itemgroup.notifications[id]
// const orgSelected = (state) => state.org.params.selected;

export const getItemgroup = (state, props) => {
    if(props.match.path==='/itemgroup/create'){
        return itemgroup()
    }
    if(props.match.path==='/itemgroup/edit/:id'){
        let _id = props.match.params.id
        let obj = cloneDeep(state.itemgroup.byId[_id]);
        return obj
    }
}

export const getItemgroupParams = (state, props) => {
    const params =  {}

    if(startsWith(state.router.location.pathname,'/itemgroup/create')){ 
        params.title = state.itemgroup.params.createTitle
        params.submitButtonText = state.itemgroup.params.createSubmitButtonText        
    };

    if(startsWith(state.router.location.pathname,'/itemgroup/edit/')){ 
        params.title = state.itemgroup.params.editTitle
        params.submitButtonText = state.itemgroup.params.editSubmitButtonText        
    };

    return params
}


export const selectItemgroup = (state, props, onlySelect ) => {
    console.log("onlySelect-----------",onlySelect)
    if(onlySelect !== undefined && onlySelect !== null && onlySelect !== ''){
        const itemgroup = state.itemgroup.byId     
        const keys = Object.keys(itemgroup)
        // const obj = keys.map( (key) => { return { value : key, label : itemgroup[key].name, path: itemgroup[key].path }})
        const obj = keys.map( (key) => { 
            return { key : key,  value : key, text : itemgroup[key].name, path: itemgroup[key].path }
        }).filter(function (item) {
            return (item.key !== '0' && (toLower(onlySelect).indexOf(toLower('<'+item.text+'>')) >= 0));
        });
        return obj
    }else{
        const itemgroup = state.itemgroup.byId     
        const keys = Object.keys(itemgroup)
        // const obj = keys.map( (key) => { return { value : key, label : itemgroup[key].name, path: itemgroup[key].path }})
        const obj = keys.map( (key) => { 
            return { key : key,  value : key, text : itemgroup[key].name, path: itemgroup[key].path }
        }).filter(function (item) {
            return item.key !== '0';
        });
        return obj
    }
            
}


export const selectItemgroupParent = (state, props, id ) => {
    const itemgroup = state.itemgroup.byId     
    const keys = Object.keys(itemgroup)
    // const obj = keys.map( (key) => { return { value : key, label : itemgroup[key].name, path: itemgroup[key].path }})
    const obj = keys.map( (key) => { 
        return { key : key,  value : key, text : itemgroup[key].name, path: itemgroup[key].path }
    }).filter(function (item) {
        return item.key !== id;
    });
    return obj        
}

