import { createSelector } from 'reselect'
import { startsWith, cloneDeep } from 'lodash';
import { department } from './model'


export const getIsFetchingDepartment = (state, props) => state.department.params.isFetching;
export const getIsDepartmentFetched = (state, props) => state.department.params.departmentFetched;
export const getDepartmentList = (state, prop) => state.department.byId

export const getNotification = (state, id) => state.department.notifications[id]

const orgSelected = (state) => state.org.params.selected;

export const getDepartment = (state, props) => {
    if(props.match.path==='/department/create'){
        return department()
    }
    if(props.match.path==='/department/edit/:id'){
        let _id = props.match.params.id
        let obj = cloneDeep(state.department.byId[_id]);
        return obj        
    }
}

export const getDepartmentParams = (state, props) => {
    const params =  {}

    if(startsWith(state.router.location.pathname,'/department/create')){ 
        params.title = state.department.params.createTitle
        params.submitButtonText = state.department.params.createSubmitButtonText        
    };

    if(startsWith(state.router.location.pathname,'/department/edit/')){ 
        params.title = state.department.params.editTitle
        params.submitButtonText = state.department.params.editSubmitButtonText        
    };

    return params
}

export const getFilteredDepartment=createSelector(
    getDepartmentList,
    
        (items)=>{
            let filterDepartment= Object.keys(items).map(function(key){
                return items[key]
            })
            return filterDepartment
        }
    
)

export const selectDepartment = createSelector(
    getDepartmentList,
    department => { 
        const keys = Object.keys(department)
        // const obj = keys.map( (key) => { return { value : key, label : department[key].name }})
        const obj = keys.map( (key) => { return { key : key,  value : key, text : department[key].dept_name }})
        return obj
    })
