import * as Yup from 'yup';
import React from 'react';
import v4 from 'uuid';
import { parseDateFromFormats } from '../../../utilities/parseDate';
import moment from 'moment'
import axios from 'axios';
import { handlError, parseError } from '../../app/serverError'
import { ASSET_URL } from '../../../store/path'
import { debounce, merge } from 'lodash'
import userACL from '../../../store/access' 


export const department = () => ({
    id: v4(),
    dept_name: "",
    segid: "",
    cid: "",
    rlb_box_id:"",
    status: 1,
    dt_create: "",
    dt_update: "",
    user_create: "",
    user_update: ""
})


Yup.addMethod(Yup.date, 'format', parseDateFromFormats);
export const departmentSchema = Yup.object().shape({

    id: Yup.string()
                .required('ID is a Required Field'),


    dept_name: Yup.string()
                .required('Name is a Required Field'),

    status: Yup.number().integer().nullable(),
    segid: Yup.string().nullable(),
    cid: Yup.string().nullable(),
    txnid : Yup.string().nullable(),
    dt_create: Yup.string().nullable(),
    dt_update: Yup.string().nullable(),
    user_create: Yup.string().nullable(),
    user_update: Yup.string().nullable()

  });


export const duplicateCheckDepartmentName = debounce(checkDepartmentName, 500);
function checkDepartmentName ( value, values )  {
    const data = merge({type: 'exist_chk', id: values.id}, userACL.atFetch()) 
    data.rlb_box_id=data.boxId
    delete data.boxId
    if (value.length > 2)
        return axios.post(ASSET_URL+'/department/check/'+ value, data)
            .then(response => {
                if (response.data==='duplicate') {
                    console.log("response",response.data)
                    return "Duplicate"
                }
              }) 
            .catch(error => "")
}

  
export const searchColumns=[
    {
        Header: <div>Department Name</div>,
        accessor: 'dept_name',
        Cell: row => <div style={{ textAlign: "left", paddingLeft: "5px" }}>{(row.value && row.value !== undefined && row.value !== null && row.value !== 'NULL' && row.value !== "0" && row.value !== 0) ? row.value : ""}</div>,
        classNameGet: 'vcitms2'
    }
]