import { createSelector } from 'reselect'
import { startsWith, cloneDeep } from 'lodash';
import { unitMaster } from './model'

export const getIsFetchingunitMaster = (state, props) => state.unitmaster.params.isFetching;
export const getIsUnitMasterFetched = (state, props) => state.unitmaster.params.unitMasterFetched;
export const getunitMasterList = (state, props) => state.unitmaster.byId
export const getNotification = (state, id) => state.unitmaster.notifications[id]


export const getUnitMaster = (state, props) => {
    if(props.match.path==='/unitmaster/create'){
        return unitMaster()
    }
    if(props.match.path==='/unitmaster/edit/:id'){
        let _id = props.match.params.id
        let obj = cloneDeep(state.unitmaster.byId[_id]);
        return obj    }
}



export const getUnitMasterParams = (state, props) => {
    const params =  {}
    
    if(startsWith(state.router.location.pathname,'/unitmaster/create')){ 
        params.title = state.unitmaster.params.createTitle
        params.submitButtonText = state.unitmaster.params.createSubmitButtonText        
    };
        
    if(startsWith(state.router.location.pathname,'/unitmaster/edit/')){ 
        params.title = state.unitmaster.params.editTitle
        params.submitButtonText = state.unitmaster.params.editSubmitButtonText        
    };

    return params
}


export const selectUnit = (state, props ) => {
    const unit = state.unitmaster.byId
    const keys = Object.keys(unit)
    const obj = keys.map( (key) => { 
        return { key : key,  value : key, text : unit[key].name }
    });
    return obj
            
}
