import axios from 'axios';
import { merge } from 'lodash';
import { all, call, put, select, takeEvery, takeLatest } from 'redux-saga/effects';
import userACL from '../../../store/access';
import { ASSET_URL } from '../../../store/path';
import { handlError, parseError } from '../../app/serverError';
import { deletedItemGroup, fetchedItemgroup, receivedOptionsItemgroup, savedItemgroup } from './actions';
import { getIsItemgroupFetched } from './selectors';

// const delay = (ms) => new Promise(res => setTimeout(res, ms))

//#region Saga for Create Itemgroup 

function* createItemgroup() {
    yield takeEvery('CREATE_ITEMGROUP',saveItemgroup)
  }

function* saveItemgroup(action){
  const { response, error } = yield call(saveItemgroupAPI, action.payload)
  console.log("Saga",action.payload)
  if(response){
  if (response.data.type!=="error") yield put (savedItemgroup(action.payload))
  }
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function saveItemgroupAPI(data) {
  // data.rlb_box_id=data.boxId
  // delete data.boxId
    return axios.post(ASSET_URL+'/assetgroup/add', data, {crossDomain: true})
      .then(response => ({ response }))
      .catch(error => ({ error }))
  }

//#endregion

//#region Saga for Edited Itemgroup 

function* editItemgroup() {
  yield takeEvery('EDIT_ITEMGROUP',saveEditedItemgroup)
}

function* saveEditedItemgroup(action){
  const { response, error } = yield call(editItemgroupAPI, action.payload)  
  if (response) {
    if(response.data.type!=="error")
   { yield put (savedItemgroup(action.payload))}
  }
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function editItemgroupAPI(data) {
  return axios.post(ASSET_URL+'/assetgroup/update', data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion



//#region Saga for Delete Itemgroup 

function* deleteItemGroup() {
  yield takeEvery('DELETE_ITEMGROUP',saveDeleteItemGroup)
}

function* saveDeleteItemGroup(action){
  const { response, error } = yield call(deleteItemGroupAPI, action.payload)
  if (response){
    if(response.data.type!=="error"){
     yield put (deletedItemGroup({id: action.payload.id, msg : response.data}))
    }
    console.log("response Itemgroup========",response.data.msg)
    }
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function deleteItemGroupAPI(data) {
  let itemGroupId = data.id
  // data.rlb_box_id=data.boxId
  // delete data.boxId
  return axios.post(ASSET_URL+'/assetgroup/delete/'+ itemGroupId, data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion



//#region Saga for List Tasks

function* fetchItemgroup() {

  yield takeEvery('FETCH_ITEMGROUP',requestItemgroup)

}
  
function* requestItemgroup(){

  const isItemgroupFetched = yield select(getIsItemgroupFetched);
  if(!isItemgroupFetched){
    let { data } = yield call(requestItemgroupAPI)
    yield put (fetchedItemgroup(data.data))
  }

}

function requestItemgroupAPI() {
  let data = merge({}, userACL.atFetch())  
  // data.segid=data.cid
  data.rlb_box_id=data.boxId
  delete data.boxId
  return axios.post(ASSET_URL+'/assetgroup/list', data, {crossDomain: true})
}

//#endregion


//#region Saga for List Tasks 

function* fetchOptions() {

  yield takeLatest('OPTIONS_REQUESTED_ITEMGROUP',requestOptionsItemgroup)

}
  
function* requestOptionsItemgroup(action){

  let { data } = yield call(requestOptionsAPI, action.payload)
  yield put (receivedOptionsItemgroup(data))

}

function requestOptionsAPI(data) {
  data = merge({}, data , userACL.atFetch())
  return axios.post(ASSET_URL+'/itemgroup/search/'+ data.str, data, {crossDomain: true})
}

//#endregion


const sagaErrorMessage = (error, action) => {
    console.group("Saga Error:"+action.type)
    console.log(error)
    console.groupEnd()
}

export default function* itemgroupSaga() {
    yield all([
        createItemgroup(),
        fetchItemgroup(),
        editItemgroup(),
        deleteItemGroup(),
        fetchOptions()
    ])
  }  