import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'
import { fetchItemgroup, deleteItemGroup, setNotifyDone } from '../data/actions'
import * as select from '../data/selectors'
import { getIsItemgroupFetched } from '../data/selectors';
import { Container, Table, TableHeader, TableBody, Header, Button, Modal } from 'semantic-ui-react'
import userACL from '../../../store/access'
import Notification from '../../../utilities/notificationUtils'


const Itemgroup = (props) => {
    const dispatch = useDispatch();
    const [isModalOpen, setModalOpen] = useState(false)
    const itemgroup = useSelector(state => state.itemgroup.byId[props.itemgroup.parent_id])
    const isNegative = props.itemgroup.status === 1 ? false : true;
    const [itemgroupId,setItemgroupId]=useState("")
    const [deleteItemGroupId, setDeleteItemGroupId] = useState(false);
    const itemgroupFetched = useSelector(state => select.getIsItemgroupFetched(state, props))

    // useEffect(() => {
    //     if (!itemgroupFetched)
    //         dispatch(fetchItemgroup())
    // }, [])

    const deleteItemGroupFromList = (values) => {
        const getVlaue = { id: values }
        userACL.atUpdate(getVlaue)
        dispatch(deleteItemGroup(getVlaue))
        setDeleteItemGroupId(getVlaue.id)
        setModalOpen(false)
    }
    
    // const deleteItemGroupFromList = (values) => {
    //     // values.rlb_box_id=values.boxId
    //     // delete values.boxId
    //     // const getVlaue = { id: values }
    //     userACL.atUpdate(values)
    //     values.rlb_box_id=values.boxId
    //     delete values.boxId
    //     delete values.txnid
    //     dispatch(deleteItemGroup(values))
    //     setDeleteItemGroupId(values)
    //     setModalOpen(false)
    // }


    const closeModal = () => {
        setModalOpen(false)
    }

    console.log("itemgroupId==========",itemgroupId)

    if (props.itemgroup.id !== 0)
        return (       
            <Table.Row negative={isNegative}>
                <Table.Cell>{props.itemgroup.name}</Table.Cell>
                <Table.Cell>{itemgroup ? itemgroup.name : ''}</Table.Cell>
                <Table.Cell>
                    {isNegative === false ?
                        <>
                            <Link to={"/itemgroup/edit/" + props.itemgroup.id}>Edit</Link><span>&nbsp;&nbsp;</span>
                            <span style={{ cursor: 'pointer', color: 'red' }} onClick={() => {
                                setModalOpen(true)
                                setItemgroupId(props.itemgroup.id)
                                }}>Delete</span>
                        </>
                        :
                        <span>Deleted</span>
                    }
                    <>
                        {
                            deleteItemGroupId ?
                                <Notification id={deleteItemGroupId} notifySelector={select.getNotification} notifyDoneAction={setNotifyDone} type="delete" />
                                :
                                null
                        }
                    </>
                    <Modal open={isModalOpen} size="mini">
                        <Modal.Header>Itemgroup List</Modal.Header>
                        <Modal.Content>
                            <h4>Do you want to delete this row??</h4>
                        </Modal.Content>
                        <Modal.Actions>
                            <Button type="button" positive onClick={() => closeModal()}>No</Button>
                            <Button type="button" color='red' onClick={() => deleteItemGroupFromList(itemgroupId)}>Yes</Button>

                        </Modal.Actions>
                    </Modal>
                </Table.Cell>
            </Table.Row>
        )
    else
        return null
}

const ItemgroupList = (props) => {

    const itemgroup = useSelector(state => state.itemgroup.byId)
    const itemgroupFetched = useSelector(state => select.getIsItemgroupFetched(state, props))
    const dispatch = useDispatch();

    useEffect(() => {
        if(!itemgroupFetched)
            dispatch(fetchItemgroup())
    },[])

    const tableStyle = {
        header: {
            position: "fixed",
            width: "100%",
            backgroundColor: "#fff"
        },
        tableHead: {
            overflowY: "auto",
            height: '580px',
            backgroundColor: "#fff"
        },
        table: {
            borderCollapse: "collapse",
            width: '100%',
            marginTop:"40px"
        },
        th: {
            position: "sticky",
            backgroundColor: "#fff",
            top: "28px",
            zIndex: "1",
            margin: "25px 0"
        }
    }
    

    // Loading Logic if tasks not fetched
    if (!itemgroupFetched) {
        return <p>Loading ... </p>
    } else {
        return (
            <Container style={tableStyle.tableHead}>
                <Header as='h1' align='left' style={tableStyle.header}>Asset Group List</Header>
                <Table selectable basic='very' style={tableStyle.table}>
                    <TableHeader>
                        <Table.Row>
                            <Table.HeaderCell style={tableStyle.th}>Name</Table.HeaderCell>
                            <Table.HeaderCell style={tableStyle.th}>Parent group</Table.HeaderCell>
                            <Table.HeaderCell style={tableStyle.th}>Action</Table.HeaderCell>
                        </Table.Row>
                    </TableHeader>
                    <TableBody>
                        {Object.keys(itemgroup).map((key) =>
                            <Itemgroup itemgroup={itemgroup[key]} key={itemgroup[key].id} />)}
                    </TableBody>
                </Table>
            </Container>
        )
    }
}

export default ItemgroupList;
