
export const computeAmt = (values, index, setFieldValue) => {
    let name = 'tdetails[' + index + '].amount',
        qty = values.tdetails[index].qty === null ? 0 : (values.tdetails[index].qty === "" ? 0 : values.tdetails[index].qty),
        rate = values.tdetails[index].rate === null ? 0 : (values.tdetails[index].rate === "" ? 0 : values.tdetails[index].rate),
        amount = 0;
    if (parseFloat(qty) !== 0 && parseFloat(rate) !== 0) {
        amount = (parseFloat(qty) * parseFloat(rate))
    }
    setFieldValue(name, amount)
    console.log(amount, '---------totalAmount')
    callGstCalculation(values, index, setFieldValue);
}



export const computeRate = (values, index, setFieldValue) => {
    let name = 'tdetails[' + index + '].rate',
        qty = values.tdetails[index].qty === null ? 0 : (values.tdetails[index].qty === "" ? 0 : values.tdetails[index].qty),
        amt = values.tdetails[index].amount === null ? 0 : (values.tdetails[index].amount === "" ? 0 : values.tdetails[index].amount),
        rate = 0;
    if (parseFloat(qty) !== 0 && parseFloat(amt) !== 0) {
        rate = (parseFloat(amt) / parseFloat(qty));
    }
    setFieldValue(name, rate)
    callGstCalculation(values, index, setFieldValue);
}

export const callGstCalculation = (values, index, setFieldValue) => {
    let amt = values.tdetails[index].amount === null ? 0 : (values.tdetails[index].amount === "" ? 0 : values.tdetails[index].amount),
        gstRate = values.tdetails[index].gst_prcnt === null ? 0 : (values.tdetails[index].gst_prcnt === "" ? 0 : values.tdetails[index].gst_prcnt),
        cessRate = values.tdetails[index].cess_prcnt === null ? 0 : (values.tdetails[index].cess_prcnt === "" ? 0 : values.tdetails[index].cess_prcnt),
        costVal = values.tdetails[index].cost === null ? 0 : (values.tdetails[index].cost === "" ? 0 : values.tdetails[index].cost),
        
        profitLoss = (parseFloat(amt) - parseFloat(costVal))
        setFieldValue('tdetails[' + index + '].pl', profitLoss)

    if (parseFloat(profitLoss) >= 0) {
        let igstAmt = 0,
            csgstAmt = 0,
            cessAmt = 0
        if (parseFloat(gstRate) !== 0 && parseFloat(amt) !== 0) {
            igstAmt = (parseFloat(amt) * parseFloat(gstRate)) / 100
            csgstAmt = parseFloat(igstAmt) / 2
        }
        if (parseFloat(cessRate) !== 0 && parseFloat(amt) !== 0) {
            cessAmt = (parseFloat(amt) * parseFloat(cessRate)) / 100
        }
        setFieldValue('tdetails[' + index + '].igst_amt', igstAmt)
        setFieldValue('tdetails[' + index + '].cgst_amt', csgstAmt)
        setFieldValue('tdetails[' + index + '].sgst_amt', csgstAmt)
        setFieldValue('tdetails[' + index + '].cess_amt', cessAmt)
    } else {
        setFieldValue('tdetails[' + index + '].igst_amt', "0")
        setFieldValue('tdetails[' + index + '].cgst_amt', "0")
        setFieldValue('tdetails[' + index + '].sgst_amt', "0")
        setFieldValue('tdetails[' + index + '].cess_amt', "0")
    }
    totalInvoice(values, index, setFieldValue)
}

export const validateAsset = (values, index, setFieldValue, name, fieldName, fromField, otherList, value) => {
    let flag = false
    let itemObj = otherList[values.tdetails[index].item]
    let assetQty = itemObj ? itemObj.asset_qty : 0
    if (parseFloat(value) > parseFloat(assetQty)) {
        flag = false
        setFieldValue(name, parseFloat(assetQty).toFixed(3))
    } else {
        flag = true
    }
    return flag
}

export const totalInvoice = (values, index, setFieldValue, name) => {
    let totalInvoice = 0;

    for (var i = 0; i < values.tdetails.length; i++) {
        let amount = values.tdetails[i].amount
        let gstAmt = values.tdetails[i].igst_amt
        let cessAmt = values.tdetails[i].cess_amt

        totalInvoice += (parseFloat(amount) + parseFloat(gstAmt) + parseFloat(cessAmt));

    }
    console.log(totalInvoice, '---------totalInvoice')
    setFieldValue("totalInvAmt", totalInvoice)

}

