import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { getAssettype, getAssettypeList, getAssetTypeNumbering, getAssettypeParams, getNotification, getSearchSegListResults } from '../data/selectors';
import { createAssettype, editAssettype, setNotifyDone, fetchAssettype, setFetchFalse } from '../data/actions';
import { assettypeSchema, assettypeSchemaNumber, duplicateCheckAssetType,assettypeNameExistSchema} from '../data/model'
import { Container, Header, Form, Button } from 'semantic-ui-react';
import { Formik, Field, Form as FormikForm, FieldArray } from 'formik';
import { FormikDateComponent, FormikAmountComponent, FormikInputComponent } from '../../../utilities/formUtils';
import userACL from '../../../store/access'
import Notification from '../../../utilities/notificationUtils'
import NumberTypeSelect from '../../assettype/components/numberTypeSelect'
import ATNatureSelect from '../../assettype/components/atNatureSelect'
import EditableSelect from '../../assettype/components/editableSelect'
import v4 from 'uuid'
import moment from 'moment'
import RLBAssetNameSelect from './rlbAssetNameSelect';
import RLBDepNameSelect from './rlbDepNameSelect';
import RLBAccuNameSelect from './rlbAccuNameSelect';
import RLBSaleNameSelect from './rlbSaleNameSelect';
import RLBLossNameSelect from './rlbLossNameSelect';
import RLBProfitNameSelect from './rlbProfitNameSelect';
import AssetTypeNumberingArray from './assetTypeNumArray';

// import UserDropdown from '../../users/components/userDropdown'


const AssettypeForm = (props) => {

    const assettype = useSelector(state => getAssettype(state, props))
    const assettypeNumbering = useSelector(state => getAssetTypeNumbering(state, assettype ? assettype.id : 0))
    const params = useSelector(state => getAssettypeParams(state, props))
    const assetList = useSelector(state => getAssettypeList(state, props))
    const segment = useSelector(state => getSearchSegListResults(state, props))
    // console.log(segment, '====segment')
    const dispatch = useDispatch()
    const [savedAssettypeId, setSavedAssettypeId] = useState(false)
    const [natureOnCall, setNatureOnCall] = useState(assettype ? assettype.numbering : '')

    const saveAssettype = (values, resetForm) => {
        //console.log("props.match.path-----------------"+props.match.path)
        if (props.match.path === '/assettype/create') {
            values.numDetails.map((nd) => {
                delete nd.txnid
                delete nd.boxId
            })
            userACL.atCreate(values)
            // values.segid = values.cid
            values.rlb_box_id=values.boxId
            delete values.txnid
            delete values.boxId
            dispatch(createAssettype(values))
            setSavedAssettypeId(values.id)
            resetForm()
        }
        if (props.match.path === '/assettype/edit/:id') {
            values.numDetails.map((nd) => {
                delete nd.txnid
                delete nd.boxId
            })
            userACL.atUpdate(values)
            // values.segid = values.cid
            values.rlb_box_id=values.boxId
            delete values.txnid
            delete values.boxId
            dispatch(editAssettype(values))
            // dispatch(setFetchFalse(false))
            setSavedAssettypeId(values.id)
            setTimeout(function () {
                props.history.push(`/assettype/list`);
                setTimeout(function () {
                    dispatch(fetchAssettype())
                }, 100)
            }, 2000)
        }
    }

    const setAssetName = (value, values, setFieldValue) => {
        // console.log(asssetList[value])
        if (value != '') {
            setFieldValue("purc_ldgr_cd", assetList[value].purc_ldgr_cd)
        } else {
            setFieldValue("purc_ldgr_cd", "")
        }

    }

    const setDepName = (value, values, setFieldValue) => {

        if (value != '') {
            setFieldValue("depre_ldgr_cd", assetList[value].purc_ldgr_cd)
        } else {
            setFieldValue("depre_ldgr_cd", "")
        }
    }

    const setAccuName = (value, values, setFieldValue) => {

        if (value != '') {
            setFieldValue("accu_ldgr_cd", assetList[value].accu_ldgr_cd)
        } else {
            setFieldValue("accu_ldgr_cd", "")
        }
    }

    const setSaleName = (value, values, setFieldValue) => {

        if (value != '') {
            setFieldValue("sale_ldgr_cd", assetList[value].sale_ldgr_cd)
        } else {
            setFieldValue("sale_ldgr_cd", "")
        }
    }

    const setProfitName = (value, values, setFieldValue) => {

        if (value != '') {
            setFieldValue("profit_ldgr_cd", assetList[value].profit_ldgr_cd)
        } else {
            setFieldValue("profit_ldgr_cd", "")
        }
    }

    const setLossName = (value, values, setFieldValue) => {

        if (value != '') {
            setFieldValue("loss_ldgr_cd", assetList[value].loss_ldgr_cd)
        } else {
            setFieldValue("loss_ldgr_cd", "")
        }
    }

    
    useEffect(() => {
        if (props.match.path === '/assettype/create') {
            let assetNum = assettypeNumbering[0]
            userACL.atCreate(assetNum)
            assetNum.segid = ""
            assetNum.atype_id = assettype.id
            assettype.numDetails = assettypeNumbering
        }
    },[])

    
    return (
        <Container>
            <Header as='h1' id="headerHeight">{params.title}</Header>
            <Formik id="assettypeForm" size="large" width={5}
                initialValues={assettype}
       
                validationSchema={assettypeSchema}
            
                onSubmit={(values, { resetForm }) => saveAssettype(values, resetForm)}
                render={({ handleSubmit, onChange, values, handleChange }) => (
                    <div style={{ width: '100%' }}>
                            
                        <Form as={FormikForm} size="small" className="CustomeForm" width={12} onSubmit={handleSubmit}>
                            <Form.Group id="headContent" widths={4}>
                                {
                                    props.match.path === "/assettype/create" ?
                                        <Field name="tname" label="Asset Type Name" component={FormikInputComponent} validate={(val) => duplicateCheckAssetType(val, values)} focus={true} maxlength={100}></Field>
                                        :
                                        <Field name="tname" label="Asset Type Name" component={FormikInputComponent} focus={true} maxlength={100}></Field>
                                }

                                <ATNatureSelect name='nature' isTxn={false} isSelection='true' label='Type' />

                            </Form.Group>

                            <Form.Group id="headContent" widths={4}>
                                <Field name="purc_ldgr_cd" label='RLB Asset Ledger Code' component={FormikInputComponent} maxlength={100}></Field>
                                <Field name="purc_ldgr_name" label='RLB Asset Ledger Name' component={FormikInputComponent} maxlength={300}></Field>
                                {/* <RLBAssetNameSelect name="purc_ldgr_name" label='RLB Asset Ledger Name' isTxn={false} isSelection='true' setData={setAssetName} /> */}
                                <Field name="depre_ldgr_cd" label='RLB Dep. Ledger Code' component={FormikInputComponent} maxlength={100}></Field>
                                <Field name="depre_ldgr_name" label='RLB Dep. Ledger Name' component={FormikInputComponent} maxlength={300}></Field>
                                {/* <RLBDepNameSelect name="depre_ldgr_name" label='RLB Dep. Ledger Name' isTxn={false} isSelection='true' setData={setDepName} /> */}
                            </Form.Group>
                            <Form.Group id="headContent" widths={4}>
                                <Field name="accu_ldgr_cd" label='RLB Accu. Dep. Ledger Code' component={FormikInputComponent} maxlength={100}></Field>
                                <Field name="accu_ldgr_name" label='RLB Accu. Dep. Ledger Name' component={FormikInputComponent} maxlength={300}></Field>
                                {/* <RLBAccuNameSelect name="accu_ldgr_name" label='RLB Accu. Dep. Ledger Name' isTxn={false} isSelection='true' setData={setAccuName} /> */}
                                <Field name="sale_ldgr_cd" label='RLB Sale Ledger Code' component={FormikInputComponent} maxlength={100}></Field>
                                <Field name="sale_ldgr_name" label='RLB Sale Ledger Name' component={FormikInputComponent} maxlength={300}></Field>
                                {/* <RLBSaleNameSelect name="sale_ldgr_name" label='RLB Sale Ledger Name' isTxn={false} isSelection='true' setData={setSaleName} /> */}
                            </Form.Group>
                            <Form.Group id="headContent" widths={4}>
                                <Field name="profit_ldgr_cd" label='RLB Profit Ledger Code' component={FormikInputComponent} maxlength={100}></Field>
                                <Field name="profit_ldgr_name" label='RLB Profit Ledger Name' component={FormikInputComponent} maxlength={300}></Field>
                                {/* <RLBProfitNameSelect name="profit_ldgr_name" label='RLB Profit Ledger Name' isTxn={false} isSelection='true' setData={setProfitName} /> */}
                                <Field name="loss_ldgr_cd" label='RLB Loss Ledger Code' component={FormikInputComponent} maxlength={100}></Field>
                                <Field name="loss_ldgr_name" label='RLB Loss Ledger Name' component={FormikInputComponent} maxlength={300}></Field>
                                {/* <RLBLossNameSelect name="loss_ldgr_name" label='RLB Loss Ledger Name' isTxn={false} isSelection='true' setData={setLossName} /> */}
                            </Form.Group>

                            <Form.Group widths={4}>
                                <NumberTypeSelect name='numbering' isTxn={false} isSelection='true' label='Numbering' setOnCall={setNatureOnCall} />
                                {natureOnCall === 'auto' ?
                                    <EditableSelect name='is_editable' isTxn={false} isSelection='true' label='Is Editable' />
                                    : null}
                            </Form.Group>

                            {natureOnCall === 'auto' ?
                                <Form.Group widths={3}>
                                    <FieldArray name='numDetails' component={AssetTypeNumberingArray} />
                                </Form.Group>
                                : null}

                            <Button id="buttonHolder" type="submit" size="medium" color='blue' className="CustomeBTN"> {params.submitButtonText} </Button>
                            {savedAssettypeId ?
                                <Notification id={savedAssettypeId} notifySelector={getNotification} notifyDoneAction={setNotifyDone} type='save' />
                                :
                                null}
                        </Form>
                    </div>
                )}
            />
        </Container>
    )
}

export default AssettypeForm;