import * as type from './types'


export function createEmployee(employee) {
    return {
        type : type.CREATE_EMPLOYEE,
        payload : employee,
        txn : 'initiated',
    };
}

export function savedEmployee(employee) {
    return {
        type : type.SAVED_EMPLOYEE,
        payload : employee,
        txn : 'success',
        diff : true
    };
}

export function editEmployee(employee) {
    return {
        type : type.EDIT_EMPLOYEE,
        payload : employee,
        txn : 'initiated',
    };
}

export function fetchEmployee() {
    return {
        type : type.FETCH_EMPLOYEE,
    };
}

export function fetchedEmployee(employee) {
    return {
        type : type.FETCHED_EMPLOYEE,
        payload : employee
    };
}


export function deleteEmployee(employee) {
    return {
        type : type.DELETE_EMPLOYEE,
        payload: employee
    };
}

export function deletedEmployee(msg) {
    return {
        type : type.DELETED_EMPLOYEE,
        payload : msg
    };
}

export function setNotifyDone(props) {
    return {
        type : type.NOTIFICATION_DONE_EMPLOYEE,
        payload: props
    };
}