import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  getItemgroup,
  getItemgroupParams,
  getNotification,
} from "../data/selectors";
import {
  createItemgroup,
  savedItemgroup,
  editItemgroup,
  setNotifyDone,
  fetchItemgroup,
} from "../data/actions";
import {
  itemgroupSchema,
  duplicateCheckItemgroupName,
  itemNameExistSchema,
} from "../data/model";
import { Container, Header, Form, Button } from "semantic-ui-react";
import { Formik, Field, Form as FormikForm } from "formik";
import { FormikInputComponent } from "../../../utilities/formUtils";
import ItemgroupSelectAsync from "../../itemgroup/components/itemgroupSelectAsync";
import ItemgroupSelect from "./itemgroupSelect";
import userACL from "../../../store/access";
import Notification from "../../../utilities/notificationUtils";

const ItemgroupForm = (props) => {
  const itemgroup = useSelector((state) => getItemgroup(state, props));
  const params = useSelector((state) => getItemgroupParams(state, props));
  const itemGroupList = useSelector((state) => state.itemgroup.byId);

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetchItemgroup());
  }, []);

  let x = Object.keys(itemGroupList).map((item) => {
    return itemGroupList[item].name;
  });

  const checkIfValueInUse = (valueToCheck) => {
    const existingValues = x;
    return existingValues.includes(valueToCheck);
  };

  const validate = (values) => {
    const errors = {};
    if (checkIfValueInUse(values.name)) {
      errors.name = "This Name Already Existing";
    }
    return errors;
  };

  console.log(itemGroupList, "============itemGroupList");
  const [pid, setPid] = useState();
  const [savedItemGroupId, setSavedItemGroupId] = useState(false);
  const [editedItemGroupId, setEditedItemGroupId] = useState(false);

  const parentItemGroup = useSelector((state) => state.itemgroup.byId[pid]);

  const getParentValue = (values) => {
    setPid(values);
  };

  const saveItemgroup = (values, resetForm) => {
    if (props.match.path === "/itemgroup/create") {
      userACL.atCreate(values);
      // values.segid = values.cid
      values.rlb_box_id = values.boxId;
      delete values.boxId;
      delete values.txnid;
      dispatch(createItemgroup(values));
      setSavedItemGroupId(values.id);
      resetForm();
    }
    if (props.match.path === "/itemgroup/edit/:id") {
      values.path = parentItemGroup
        ? parentItemGroup.path
          ? parentItemGroup.path + values.id
          : values.id + ","
        : values.id + ",";
      userACL.atUpdate(values);
      // values.segid = values.cid
      values.rlb_box_id = values.boxId;
      // values.path = ""
      delete values.boxId;
      delete values.txnid;
      dispatch(editItemgroup(values));
      setEditedItemGroupId(values.id);
      setTimeout(function () {
        props.history.push(`/itemgroup/list`);
      }, 1000);
    }
  };

  return (
    <Container>
      <Header as="h1">{params.title}</Header>
      <Formik
        id="itemgroupForm"
        size="large"
        width={5}
        initialValues={itemgroup}
        validationSchema={itemgroupSchema}
        validate={validate}
        onSubmit={(values, { resetForm }) => saveItemgroup(values, resetForm)}
        render={({ handleSubmit, onChange, values, handleChange }) => (
          <Form
            as={FormikForm}
            size="small"
            className="CustomeForm"
            width={5}
            onSubmit={handleSubmit}
          >
            <Form.Group widths={3}>
              <Field
                name="name"
                label="Asset Group Name"
                isMandatory={true}
                component={FormikInputComponent}
                validate={(val) => duplicateCheckItemgroupName(val, values)}
                onChange={handleChange}
                focus={true}
              ></Field>
            </Form.Group>
            <Form.Group widths={3}>
              {/* <ItemgroupSelect name="parent_id" label='Parent Asset Group' isTxn='false' isSelection={true} values={values} getValue={getParentValue} placeholder="Primary" /> */}
              <ItemgroupSelectAsync
                name="parent_id"
                label="Parent Asset Group"
                isTxn="false"
                isSelection={true}
                values={values}
                getValue={getParentValue}
                placeholder="Primary"
              />
              {/* <Field name="parent_id" label='Parent Group' component={FormikInputComponent} placeholder="Short Name"></Field>   */}
            </Form.Group>
            <Button
              type="submit"
              color="blue"
              size="medium"
              className="CustomeBTN"
            >
              {" "}
              {params.submitButtonText}{" "}
            </Button>
            {savedItemGroupId ? (
              <Notification
                id={savedItemGroupId}
                notifySelector={getNotification}
                notifyDoneAction={setNotifyDone}
                type="save"
              />
            ) : null}
          </Form>
        )}
      />
    </Container>
  );
};

export default ItemgroupForm;
