import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { fetchEmployee } from '../data/actions'
import * as select from '../data/selectors'
import { Field } from 'formik'
import { FormikSelectComponent } from '../../../utilities/formUtils'


const EmployeeSelect = ({ name, isTxn, isLabel, label, isSelection, setState, getValue : callback }, props) => {

    const employeeFetched = useSelector(state => select.getIsEmployeeFetched(state, props))
    const options = useSelector(state => select.selectEmployee(state, props))
    const dispatch = useDispatch()

    useEffect(()=>{
        if (!employeeFetched)
            dispatch(fetchEmployee());
    },[]);  


    const getValue = (values) => {
        console.log("Employee Selected", values, name)
        if(setState){
            setState(values)
        }
    }


    if (!employeeFetched && options.length > 0 ){
        return <p>Loading</p>
    }  else {        
        // console.log(options)
        return (
            <Field name={name} isLabel={isLabel} isTxn={isTxn} label={label} isSelection={isSelection} component={FormikSelectComponent} userProps={{ options, getValue }}> </Field>
        )
    }

}


export default EmployeeSelect;
