import * as Yup from 'yup';
import v4 from 'uuid';
import { parseDateFromFormats } from '../../../utilities/parseDate';
import moment from 'moment'
import axios from 'axios';
import { handlError, parseError } from '../../app/serverError'
import { ASSET_URL } from '../../../store/path'
import { debounce, merge } from 'lodash'
import userACL from '../../../store/access'


export const area = () => ({
    id: v4(),
    code: "",
    name: "",
    segid: "",
    cid: "",
    status: 1,
    dt_create: "",
    dt_update: "",
    user_create: "",
    user_update: ""
})


Yup.addMethod(Yup.date, 'format', parseDateFromFormats);
export const areaSchema = Yup.object().shape({

    id: Yup.string()
        .required('ID is a Required Field'),

    code: Yup.string()
        .required('Code is a Required Field'),

    name: Yup.string()
        .required('Name is a Required Field'),

    status: Yup.number().integer().nullable(),
    segid: Yup.string().nullable(),
    cid: Yup.string().nullable(),
    txnid: Yup.string().nullable(),
    dt_create: Yup.string().nullable(),
    dt_update: Yup.string().nullable(),
    user_create: Yup.string().nullable(),
    user_update: Yup.string().nullable()

});


export const duplicateCheckAreaCode = debounce(checkAreaCode, 500);
function checkAreaCode(value, values) {
    const data = merge({ type: 'checkCode', id: values.id }, userACL.atFetch())
    if (value.length > 0)
        return axios.post(ASSET_URL + '/area/exists/' + value, data)
            .then(response => {
                if (response.data === 'duplicate') {
                    // console.log("response",response.data)
                    return "Duplicate"
                }
            })
            .catch(error => "")
}


export const duplicateCheckAreaName = debounce(checkAreaName, 500);
function checkAreaName(value, values) {
    const data = merge({ type: 'checkName', id: values.id }, userACL.atFetch())
    if (value.length > 2)
        return axios.post(ASSET_URL + '/area/exists/' + value, data)
            .then(response => {
                if (response.data === 'duplicate') {
                    // console.log("response",response.data)
                    return "Duplicate"
                }
            })
            .catch(error => "")
}
