
export const heightSet = (setHeight) => {

    let menuHeight = document.getElementById("menuContent")?document.getElementById("menuContent").clientHeight:0
    let headDiv = document.getElementById("headContent") ? document.getElementById("headContent").clientHeight : 0
    let subheadDiv = document.getElementById("subheadContent") ? document.getElementById("subheadContent").clientHeight : 0
    let getHeight = parseFloat(headDiv)
    let getSubHeight = parseFloat(subheadDiv)
    let windowHeight = window.innerHeight
    let setHeightDiv = parseFloat(windowHeight) - (parseFloat(getHeight) + parseFloat(menuHeight) + parseFloat(getSubHeight))
    setHeight(setHeightDiv)
}

export const widthSet = (setWidth) => {

    let menuContent = document.getElementById("mainContainer").clientWidth;
    setWidth(menuContent);
}

export const innerHeightSet = (setInnerHeight) => {

    let menuHeight = document.getElementById("menuContent")?document.getElementById("menuContent").clientHeight:0
    let headDiv = document.getElementById("headContent") ? document.getElementById("headContent").clientHeight : 0
    let subheadDiv = document.getElementById("subheadContent") ? document.getElementById("subheadContent").clientHeight : 0
    let getHeight = parseFloat(headDiv) + parseFloat(menuHeight) + parseFloat(subheadDiv) + 20
    let windowHeight = window.innerHeight
    let setHeightDiv = parseFloat(windowHeight) - (parseFloat(getHeight))
    setInnerHeight(setHeightDiv)
}

export const rowBodyHeightSet = (setInnerHeight) => {

    let menuHeight = document.getElementById("menuContent")?document.getElementById("menuContent").clientHeight:0
    let headDiv = document.getElementById("headContent") ? document.getElementById("headContent").clientHeight : 0
    let subheadDiv = document.getElementById("subheadContent") ? document.getElementById("subheadContent").clientHeight : 0
    let innerHeadDiv= document.getElementById("headerRowObj") ? document.getElementById("headerRowObj").clientHeight : 0
    let innerFootDiv= document.getElementById("footerRowObj") ? document.getElementById("footerRowObj").clientHeight : 0
    let getHeight = parseFloat(headDiv) + parseFloat(subheadDiv) + parseFloat(innerHeadDiv) + parseFloat(innerFootDiv) + parseFloat(menuHeight) + 10
    let windowHeight = window.innerHeight
    let setHeightDiv = parseFloat(windowHeight) - (parseFloat(getHeight))
    setInnerHeight(setHeightDiv)
}





export const heightSetModal = (setHeight) => {

    let headDiv = document.getElementById("modalHeaderHt") ? document.getElementById("modalHeaderHt").clientHeight : 0
    let getHeight = parseFloat(headDiv)
    let modalHeight = document.getElementById("modalIGHt").clientHeight ? (document.getElementById("modalIGHt").clientHeight === null ? 0 : document.getElementById("modalIGHt").clientHeight) : 0
    let setHeightDiv = parseFloat(modalHeight) - (parseFloat(getHeight))
    setHeight(setHeightDiv)
}

export const innerHeightSetModal = (setInnerHeight) => {
    let modalHeadDiv = document.getElementById("modalHeaderHt") ? document.getElementById("modalHeaderHt").clientHeight : 0
    let getHeight = parseFloat(modalHeadDiv) + 20
    let modalHeight = document.getElementById("modalIGHt").clientHeight ? document.getElementById("modalIGHt").clientHeight : 0
    let setHeightDiv = parseFloat(modalHeight) - (parseFloat(getHeight))
    setInnerHeight(setHeightDiv)
}

export const rowBodyHeightSetModal = (setInnerHeight) => {

    let menuHeight = document.getElementById("menuContent").clientHeight
    let innerHeadDiv= document.getElementById("headerRowObj") ? document.getElementById("headerRowObj").clientHeight : 0
    let innerFootDiv= document.getElementById("footerRowObj") ? document.getElementById("footerRowObj").clientHeight : 0
    let getHeight = parseFloat(innerHeadDiv) + parseFloat(innerFootDiv) + parseFloat(menuHeight) + 10
    let modalHeight = document.getElementById("modalIGHt").clientHeight ? document.getElementById("modalIGHt").clientHeight : 0
    let setHeightDiv = parseFloat(modalHeight) - (parseFloat(getHeight))
    setInnerHeight(setHeightDiv)
}