import React, { useEffect, useState } from 'react';
import { Link } from "react-router-dom";
import { Icon, Popup } from 'semantic-ui-react';
import { rowBodyHeightSetMenu } from './heightCalculator';
import { menu } from './menu';

import "./sidebar.css";
import { useDispatch } from 'react-redux';
import { fetchLocation } from '../location/data/actions';


const SideBar = ({ children }) => {

    const [isOpen, setIsOpen] = useState(true);
    const [isOpenContent, setIsOpenContent] = useState(true);
    const [menuClicked, setMenuClicked] = useState(false);
    const [heightSegment, setHeightSegment] = useState(500);
    const [heightMenu, setHeightMenu] = useState(500);

    const dispatch = useDispatch()
    useEffect(() => {
        dispatch(fetchLocation())
    }, [])    

    const toggle = () => {
        setIsOpen(!isOpen);
        if (isOpenContent === false) {
            setTimeout(() => {
                setIsOpenContent(!isOpenContent)
            }, 300);
        } else {
            setIsOpenContent(!isOpenContent)
        }
    }
    const style = {
        borderRadius: '5px',
        opacity: 0.7,
        padding: '10px',
        fontWeight: 'normal',
        backgroundColor: '#0070C2',
        fontWeight: 'bold',
        fontSize: '11px'
    }

    const styleSmallMenu = (id) => {
        let left = 0
        if (id == "segmap_holder") {
            left = 100
        } else if (id == "formtype_holder") {
            left = 69
        } else if (id == "saletype_holder") {
            left = 63
        } else if (id == "leadsource_holder") {
            left = 80
        } else if (id == "mtax_holder") {
            left = 82
        } else if (id == "salesmanager_holder") {
            left = 90
        } else if (id == "teamleader_holder") {
            left = 81
        } else if (id == "salesconsultant_holder") {
            left = 103
        } else if (id == "financebank_holder") {
            left = 86
        } else if (id == "financeconsultant_holder") {
            left = 117
        } else if (id == "vtnum_holder") {
            left = 86
        } else if (id == "ciform_holder") {
            left = 56
        } else if (id == "msciform_holder") {
            left = 56
        }
        return ({
            borderRadius: '5px',
            opacity: 0.7,
            padding: '10px',
            fontWeight: 'normal',
            backgroundColor: '#0070C2',
            fontWeight: 'bold',
            fontSize: '11px',
            left: left + 'px'
        })
    }
    useEffect(() => {
        callHeightFunc();
        window.addEventListener("resize", function () {
            callHeightFunc();
        }, false);
    }, []);
    const callHeightFunc = () => {
        rowBodyHeightSetMenu(setHeightSegment)
        rowBodyHeightSetMenu(setHeightMenu, isOpenContent);
    };

    return (
        <div className='main_container'>
            <div className='sidebar' style={{ width: isOpen ? "15vw" : "3vw", overflowX: 'hidden', overflowY: 'hidden' }}>
                <div id="logoIcon" className='top_section'>
                    <table width={'100%'} cellPadding='0' cellSpacing='0'>
                        {isOpenContent ?
                            <tr>
                                <td width={'20%'} style={{ textAlign: 'left', paddingLeft: '10px', paddingTop: '5px', paddingBottom: '5px', verticalAlign: 'middle', cursor: 'pointer' }}>
                                    <Icon name='list' style={{ fontSize: '18px' }} onClick={toggle} />
                                </td>
                                <td width={'65%'} style={{ textAlign: 'left', verticalAlign: 'middle' }}>
                                    <Link to={'/'} style={{ fontSize: '14px' }}><div>{(menu.length > 0 ? menu[0].project : "") + " Menu"}</div></Link>
                                </td>
                                <td width={'15%'} style={{ textAlign: 'center', verticalAlign: 'middle', cursor: 'pointer' }}>
                                    <Icon className="icon" style={{ fontSize: '18px' }} name="angle double left" title="Close" onClick={toggle}></Icon>
                                </td>
                            </tr>
                            :
                            <tr>
                                <td width={'100%'} style={{ textAlign: 'left', paddingLeft: '10px', paddingTop: '5px', paddingBottom: '5px', verticalAlign: 'middle', cursor: 'pointer' }}>
                                    <Icon name='list' style={{ fontSize: '18px' }} onClick={toggle} />
                                </td>
                            </tr>
                        }
                    </table>
                </div>
                <div style={{ height: heightMenu + 'px', backgroundColor: '#F2F7FC', overflowX: 'hidden', overflowY: 'auto' }}>
                    <section className='components'>
                        {menu.map((m, index) => {
                            return (
                                <Link
                                    id={m.id + "_holder"}
                                    to={m.listPath}
                                    key={index}
                                    className={"link"}
                                    activeClassName="nav_active"
                                    onClick={() => setMenuClicked(true)}
                                >
                                    <table width={'100%'} cellPadding='0' cellSpacing='0'>
                                        {isOpenContent ?
                                            <tr id={m.id}>
                                                <td width={'20%'} style={{ textAlign: 'left', paddingLeft: '10px', paddingTop: '5px', paddingBottom: '5px', verticalAlign: 'middle' }}>
                                                    {
                                                        m.listPath ?
                                                            <Popup
                                                                trigger={<Link to={m.listPath} style={{ fontSize: '18px' }}><Icon name={m.icon} /></Link>}
                                                                content={m.text + " List"}
                                                                style={style}
                                                                inverted
                                                                position='bottom left'
                                                                className='listIcon leftLink linkMenu'
                                                            />
                                                            :
                                                            <Icon name={m.icon} style={{ fontSize: '18px', cursor: 'pointer' }} />
                                                    }
                                                </td>
                                                <td width={'65%'} style={{ fontSize: '13px', textAlign: 'left', verticalAlign: 'middle', color: '#656665' }}>
                                                    {
                                                        m.listPath ?
                                                            <Popup
                                                                trigger={<Link to={m.listPath} style={{ cursor: 'pointer' }} className="hoverText">{m.text}</Link>}
                                                                content={m.text + " List"}
                                                                style={style}
                                                                inverted
                                                                position='bottom left'
                                                                className='listText centerLink linkMenu'
                                                            />
                                                            :
                                                            <div style={{ cursor: 'pointer' }} className="hoverText">{m.text}</div>
                                                    }
                                                </td>
                                                <td width={'15%'} style={{ textAlign: 'center', verticalAlign: 'middle', cursor: 'pointer' }}>
                                                    <center>
                                                        {
                                                            m.createPath ?
                                                                <Popup
                                                                    trigger={<Link to={m.createPath} style={{ fontSize: '12px' }}><Icon name="add" /></Link>}
                                                                    content={"Create " + m.text}
                                                                    style={style}
                                                                    inverted
                                                                    position='bottom right'
                                                                    className='createIcon rightLink linkMenu'
                                                                />
                                                                :
                                                                null
                                                        }
                                                    </center>
                                                </td>
                                            </tr>
                                            :
                                            <tr id={m.id}>
                                                <td width={'100%'} style={{ textAlign: 'left', paddingLeft: '10px', paddingTop: '5px', paddingBottom: '5px', verticalAlign: 'middle' }}>
                                                    {
                                                        m.listPath ?
                                                            <Popup
                                                                trigger={<Link to={m.listPath} style={{ fontSize: '18px' }}><Icon name={m.icon} /></Link>}
                                                                content={m.text + " List"}
                                                                style={styleSmallMenu(m.id + "_holder")}
                                                                inverted
                                                                position='bottom left'
                                                                className='listIcon leftLink linkMenuSmall'
                                                            />
                                                            :
                                                            <Icon name={m.icon} style={{ fontSize: '18px', cursor: 'pointer' }} />
                                                    }
                                                </td>
                                            </tr>
                                        }
                                    </table>
                                </Link>
                            )
                        })}
                    </section>
                </div>
            </div>
            {/* <main className='contentUI' style={{ width: isOpen ? "85vw" : "97vw", height: heightSegment + 'px' }}>{children}</main> */}
            <main className='contentUI'>{children}</main>
            

        </div>
    )
}

export default SideBar