const ItemsList = (props) => {
  const items = useSelector((state) => select.getFilteredItems(state));
  const itemsFetched = useSelector((state) => state.items.params.itemsFetched);
  const isFetching = useSelector((state) => state.items.params.isFetching);
  const itemsId = useSelector((state) => state.items.byListId);
  const [assetId, setAssetId] = useState("");
  const [deleteItemId, setDeleteItemId] = useState(false);
  const dispatch = useDispatch();
  const [isModalOpen, setModalOpen] = useState(false);
  const [heights, setHeight] = useState("");
  const [getBodyHeight, setGetBodyHeight] = useState("");
  // const [noDataComponent, setDataComponent] = useState(false)

  console.log(Object.keys(items), "=================assetId");

  useEffect(() => {
    callHeightFunc();
    window.addEventListener(
      "resize",
      function () {
        callHeightFunc();
      },
      false
    );
  }, []);

  useEffect(()=>{
    let values = merge({}, userACL.atFetch());
    values.name = "";
    dispatch(fetchItems(values))
  },[])

  const fetchList = (values) => {
    // if (!values.name) {
    //   values.name = "";
    // }
    if(values.name){
    dispatch(fetchItems(values));}
  };

  const deleteItemFromList = (values) => {
    const getVlaue = { id: values };
    userACL.atUpdate(getVlaue);
    dispatch(deleteItems(getVlaue));
    setDeleteItemId(values.id);
    setModalOpen(false);
  };
  const closeModal = () => {
    setModalOpen(false);
  };

  const itemsListActions = ({ object: itemsobj }) => {
    return (
      <>
        <Link to={"/items/edit/" + itemsobj.id}>Edit</Link>
        <span>&nbsp;&nbsp;</span>
        <span
          style={{ cursor: "pointer", color: "red" }}
          onClick={() => {
            setModalOpen(true);
            setAssetId(itemsobj.id);
          }}
        >
          Delete
        </span>
      </>
    );
  };

  const NoDataComponent = () => {
    return (
      <center>
        <h1 style={{ color: "red", marginTop: "150px" }}>No Data Found</h1>
      </center>
    );
  };

  const callHeightFunc = () => {
    heightSet(setHeight);
    rowBodyHeightSet(setGetBodyHeight);
  };

  // Loading Logic if tasks not fetched
  // if (!itemsFetched){
  //     return <p>Loading ... </p>
  // }  else {
  return (
    <Container>
      <Grid id="headContent" columns="equal">
        <Grid.Row>
          <Grid.Column width={7}>
            <Header as="h1" className="HeaderVerticalAlign" align="left">
              Assets List
            </Header>
          </Grid.Column>
          <Grid.Column width={4} floated="right">
            <Formik
              id="itemListSearch"
              onSubmit={(values) => {
                fetchList(values);
              }}
              // initialValues={searchParams?searchParams:null}
              render={({
                handleSubmit,
                errors,
                onChange,
                values,
                handleChange,
              }) => (
                <Form
                  as={FormikForm}
                  size="small"
                  width={5}
                  onSubmit={handleSubmit}
                >
                  <Form.Group>
                    <Field
                      name="name"
                      label=" "
                      component={FormikInputComponent}
                      placeholder="Asset Name"
                      focus={true}
                    ></Field>
                    <Button type="submit" size="small" color="blue">
                      {" "}
                      Search{" "}
                    </Button>
                  </Form.Group>
                </Form>
              )}
            />
          </Grid.Column>
        </Grid.Row>
      </Grid>
      <br />

      <div className="" style={{ height: heights + "px", width: "100%" }}>
      <StickyTable className='tableLayout' stickyHeaderCount={1} stickyColumnCount={0} style={{ width: '100%' }}>
        <TableListDiv
          columns={searchColumns}
          data={items}
          itemsFetched={itemsFetched}
          isFetching={isFetching}
          Actions={itemsListActions}
          setHeight={parseFloat(heights) - 70}
          getIndex={6}
          filterBoxNone={""}
          rightBox={" 4, 5, 6, 7,"}
          centerBox={" 2,"}
          callHeightFunc={callHeightFunc}
          paddingShort={2}
          getWidth={"101%"}
          actionClass={"actionTdDR"}
          NoDataComponent={NoDataComponent}
        />
        </StickyTable>
      </div>

      <>
        {deleteItemId ? (
          <Notification
            id={deleteItemId}
            notifySelector={select.getNotification}
            notifyDoneAction={setNotifyDone}
            type="delete"
          />
        ) : null}
      </>

      <Modal open={isModalOpen} size="mini">
        <Modal.Header>Asset List</Modal.Header>
        <Modal.Content>
          <h4>Do you want to delete this row??</h4>
        </Modal.Content>
        <Modal.Actions>
          <Button type="button" positive onClick={() => closeModal()}>
            No
          </Button>
          <Button
            type="button"
            color="red"
            onClick={() => {
              deleteItemFromList(assetId);
            }}
          >
            Yes
          </Button>
        </Modal.Actions>
      </Modal>

      {/* <Table selectable basic='very'>
                    <TableHeader>
                        <Table.Row>
                            <Table.HeaderCell>Item Code</Table.HeaderCell>
                            <Table.HeaderCell>Asset Description</Table.HeaderCell>
                            <Table.HeaderCell>Group</Table.HeaderCell>
                            <Table.HeaderCell>Party</Table.HeaderCell>
                            <Table.HeaderCell>Purchase Date</Table.HeaderCell>
                            <Table.HeaderCell>Purchase Amt</Table.HeaderCell>
                            <Table.HeaderCell>Action</Table.HeaderCell>
                        </Table.Row>
                    </TableHeader>
                    <TableBody>
                        <Table.Row>
                            <FilterCell type='text' filterAction={filterItems} col='code'/>
                            <FilterCell type='text' filterAction={filterItems} col='name'/>
                            <FilterCell type='text' filterAction={filterItems} col='group_name'/>
                            <FilterCell type='text' filterAction={filterItems} col='party_name'/>
                            <FilterCell type='text' filterAction={filterItems} col='purchase_date'/>
                            <FilterCell type='text' filterAction={filterItems} col='initial_value'/>
                            <Table.Cell></Table.Cell>
                        </Table.Row>
                        { items.map((itm) => 
                        <Items items={itm} key={itm.id} /> )}
                    </TableBody>
                </Table> */}
    </Container>
  );
  // }
};

