import * as type from './types'


export function createArea(area) {
    return {
        type : type.CREATE_AREA,
        payload : area,
        txn : 'initiated',
    };
}

export function savedArea(area) {
    return {
        type : type.SAVED_AREA,
        payload : area,
        txn : 'success',
        diff : true
    };
}

export function editArea(area) {
    return {
        type : type.EDIT_AREA,
        payload : area,
        txn : 'initiated',
    };
}

export function fetchArea() {
    return {
        type : type.FETCH_AREA,
    };
}

export function fetchedArea(area) {
    return {
        type : type.FETCHED_AREA,
        payload : area
    };
}


export function deleteArea(area) {
    return {
        type : type.DELETE_AREA,
        payload: area
    };
}

export function deletedArea(msg) {
    return {
        type : type.DELETED_AREA,
        payload : msg
    };
}

export function setNotifyDone(props) {
    return {
        type : type.NOTIFICATION_DONE_AREA,
        payload: props
    };
}