import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { fetchAssettype } from "../data/actions";
import { fetchItems, fetchedItems, filterItems,fetchedemptyItems } from "../../items/data/actions";
import { selectItem } from "../../items/data/selectors";
import * as select from "../data/selectors";
import { Field } from "formik";
import { FormikSelectComponent } from "../../../utilities/formUtils";
import userACL from "../../../store/access";
import { fill, merge } from "lodash";

const AssetTypeSelect = (
  {
    name,
    isTxn,
    isLabel,
    label,
    isSelection,
    focus,
    placeholder,
    validation,
    onSelectOf,
    computeValue,
    index,
    fieldName,
    fromField,
    otherList,
    passFunct,
    getValue: callback,
  },
  props
) => {

  const [itemValue, setItemValue] = useState();
  const assetTypeFetched = useSelector((state) =>
    select.getIsAssettypeFetched(state, props)
  );
  const options = useSelector((state) => select.selectAssetType(state, props, onSelectOf));
  const selectItemOptions = useSelector((state) => selectItem(state, props));
  const dispatch = useDispatch();

  useEffect(() => {
    if (!assetTypeFetched) dispatch(fetchAssettype());
  }, []);

  useEffect(()=>{
    if(itemValue && selectItemOptions.length){
     const filteredOptions = selectItemOptions.filter((item)=>item.atypeId === itemValue);
     if(filteredOptions.length>0 ){
       dispatch(fetchedItems(filteredOptions))
       console.log("filteredOptions============",filteredOptions)
     }
    }
},[selectItemOptions,itemValue])

//   const getValue = (value,setFieldValue) => {
//     if (value) {
//         let data = merge({}, userACL.atFetch());
//         data.name = "";
//         dispatch(fetchItems(data));
//         setItemValue(value) 
//         setFieldValue("name","")
//       }
//   };

const getValue = (value, values, setFieldValue) => {
    // values=merge({},userACL.atFetch())
    console.log("value5======", value);
    if (value) {
      let data = merge({}, userACL.atFetch());
      data.name = "";
      dispatch(fetchItems(data));
      setItemValue(value) 
      setFieldValue("name","")
    }
  };

  // Loading Logic if tasks not fetched
  if (!assetTypeFetched && options.length > 0) {
    return <p>Loading</p>;
  } else {
    return (
      <Field
        name={name}
        isLabel={isLabel}
        isTxn={isTxn}
        label={label}
        validation={validation}
        isSelection={isSelection}
        component={FormikSelectComponent}
        computeValue={computeValue}
        index={index}
        fieldName={fieldName}
        fromField={fromField}
        otherList={otherList}
        userProps={{ options, getValue }}
        focus={focus}
        placeholder={placeholder}
      >
        {" "}
      </Field>
    );
  }
};

export default AssetTypeSelect;
