import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { fetchDepartment } from '../data/actions'
import * as select from '../data/selectors'
import { Field } from 'formik'
import { FormikSelectComponent } from '../../../utilities/formUtils'


const DepartmentSelect = ({ name, isTxn, isLabel, label, isSelection, setState, getValue : callback }, props) => {

    const departmentFetched = useSelector(state => select.getIsDepartmentFetched(state, props))
    const options = useSelector(state => select.selectDepartment(state, props))
    const dispatch = useDispatch()

    useEffect(()=>{
        if (!departmentFetched)
            dispatch(fetchDepartment());
    },[]);  


    const getValue = (values) => {
        console.log("Department Selected", values, name)
        if(setState){
            setState(values)
        }
    }


    if (!departmentFetched && options.length > 0 ){
        return <p>Loading</p>
    }  else {        
        // console.log(options)
        return (
            <Field name={name} isLabel={isLabel} isTxn={isTxn} label={label} isSelection={isSelection} component={FormikSelectComponent} userProps={{ options, getValue }}> </Field>
        )
    }

}


export default DepartmentSelect;
