import { combineReducers } from 'redux';
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr';

// Define a itemgroups schema
const itemgroupSchema = new schema.Entity('itemgroups', {}, { idAttribute: 'id' });
const itemgroupListSchema = [itemgroupSchema];

const defaultItemgroupParams = {
  itemgroupFetched: false,
  optionsFetched: false,
  isFetching:false,
  isOptionsFetching:false,
  createTitle: 'Create New Asset Group',
  createSubmitButtonText: 'Submit',
  editTitle: 'Edit Asset Group',
  editSubmitButtonText: 'Submit'
}

const primaryItemGroup = {
  0: {
    id: 0,
    name: 'Primary',
    path: ''
  }
}


function byId(state = primaryItemGroup, action) {
  if (action.type === 'SAVED_ITEMGROUP') {
    const itemgroupID = action.payload.id;
    return {
      ...state,
      [itemgroupID]: action.payload
    }
  }
  if (action.type === 'FETCHED_ITEMGROUP') {
    const normalizedItemgroup = normalize(action.payload, itemgroupListSchema);
    return merge({}, state, normalizedItemgroup.entities.itemgroups)
  } 
  if (action.type === 'DELETED_ITEMGROUP') {
    if (action.payload.msg.type === "success") {
      const itemgroupID = action.payload.id;
      let finalState = { ...state }
      delete finalState[itemgroupID]
      return finalState
    } else {
      return state
    }
  } else {
    return state
  }
}


function options(state = {}, action) {
  if (action.type === 'OPTIONS_RECEIVED_ITEMGROUP') {
    // return { options: action.payload }
    const normalizedItemgroup = normalize(action.payload, itemgroupListSchema);
    return merge({}, primaryItemGroup, normalizedItemgroup.entities.itemgroups)
  } else {
    return state
  }
}


function params(state = defaultItemgroupParams, action) {
  if (action.type === 'FETCH_ITEMGROUP') {
    return {
      ...state,
      itemgroupFetched: false,
      isFetching: true
    }
  }
  if (action.type === 'FETCHED_ITEMGROUP') {
    return {
      ...state,
      itemgroupFetched: true,
      isFetching: false
    }
  }
  if (action.type === 'OPTIONS_REQUESTED_ITEMGROUP') {
    return {
      ...state,
      optionsFetched: false,
      isOptionsFetching: true
    }
  }
  if (action.type === 'OPTIONS_RECEIVED_ITEMGROUP') {
    return {
      ...state,
      optionsFetched: true,
      isOptionsFetching: false
    }
  } else {
    return state
  }
}


function notifications(state = {}, action) {
  if (action.type === 'SAVE_ITEMGROUP') {
    return {
      ...state,
      [action.payload.id]: {
        ...state[action.payload.id],
        save: {
          status: action.txn === "success" ? 'success' : 'error',
          // status: action.txn,

          msg: action.txn
        }
      }
    }
  }
  if (action.type === 'DELETED_ITEMGROUP') {
    return {
      ...state,
      [action.payload.id]: {
        ...state[action.payload.id],
        delete: {
          status: action.payload.msg === "success" ? 'success' : 'error',
          msg: action.payload.msg
        }
      }
    }
  }
  if (action.type === 'NOTIFICATION_DONE_ITEMGROUP') {
    const { id, type } = action.payload
    // Remove the 'id' element from state
    const { [id]: idValue, ...restOfState } = state;
    // Remove the 'type' from the 'id' element
    const { [type]: removedValue, ...restOfId } = idValue;
    // Merge back together
    const finalState = { ...restOfState, [id]: restOfId };
    return finalState

  } else {
    return state
  }
}


const itemgroup = combineReducers({
  byId,
  options,
  notifications,
  params
});

export default itemgroup;

