export const employee = () => ({
    id: v4(),
    loc_id: "",
    emp_name: "",
    emp_code:"",
    segid: "",
    cid: "",
    status: 1,
    dt_create: "",
    dt_update: "",
    user_create: "",
    user_update: "",
    rlb_box_id:""
})


Yup.addMethod(Yup.date, 'format', parseDateFromFormats);
export const employeeSchema = Yup.object().shape({

    id: Yup.string()
                .required('ID is a Required Field'),

    loc_id: Yup.string().nullable(),

    emp_name: Yup.string()
                .required('Name Required'),
    emp_code: Yup.string()
                .required('Name Required'),
    status: Yup.number().integer().nullable(),
    segid: Yup.string().nullable(),
    cid: Yup.string().nullable(),
    dt_create: Yup.string().nullable(),
    dt_update: Yup.string().nullable(),
    user_create: Yup.string().nullable(),
    user_update: Yup.string().nullable()

  });

export const duplicateCheckEmployeeName = debounce(checkEmployeeName, 500);
function checkEmployeeName ( value, values )  {
    const data = merge({type: 'check', id: values.id}, userACL.atFetch()) 
    // data.cid=data.segid
    data.rlb_box_id=data.boxId
    delete data.txnid
    delete data.boxId
    if (value.length > 2)
        return axios.post(ASSET_URL+'/employee/check/'+ value, data)
            .then(response => {
                  if (response.data==='duplicate') {
                      console.log("response",response.data)
                      return "Duplicate"
                  }
              }) 
            .catch(error => "")
}
  