import * as type from './types'

export function createUnitMaster(unit){
    return{
        type:type.CREATE_UNIT_MASTER,
        payload:unit,
        txn: 'initiated'
    }
}

export function savedUnitMaster(unit){
    return{
        type:type.SAVED_UNIT_MASTER,
        payload:unit,
        txn:'success',
        diff:true
    }
}

export function editUnitMaster(unit){
    return{
        type:type.EDIT_UNIT_MASTER,
        payload:unit,
        txn:'initiated'
    }
}

export function fetchUnitMaster(){
    return{
        type:type.FETCH_UNIT_MASTER
    }
}

export function fetchedUnitMaster(unit){
    return{
        type:type.FETCHED_UNIT_MASTER,
        payload:unit
    }
}


export function deleteUnitMaster(unit) {
    return {
        type : type.DELETE_UNIT_MASTER,
        payload: unit
    };
}

export function deletedUnitMaster(msg) {
    return {
        type : type.DELETED_UNIT_MASTER,
        payload : msg
    };
}

export function setNotifyDone(props) {
    return {
        type : type.NOTIFICATION_DONE_UNIT_MASTER,
        payload: props
    };
}