import { createSelector } from 'reselect'
// import { startsWith, cloneDeep } from 'lodash';
import { location } from './model'

export const getIsLocationFetched = (state, props) => state.location.params.locationFetched;
export const getIsLocationDepsOnItemListFetched = (state, props) => state.location.params.locationDepsOnItemFetched;
export const getLocationList = (state, props) => state.location.byId

export const selectLocation = createSelector(
    getLocationList,
    location => {
        const keys = Object.keys(location)
        const obj = keys.map((key,ind) => {
            
            return {
                key: ind, value: location[key].segement_id, text: location[key].segement_name, subLocation:location[key].godown_list
            }
        })
        return obj
    })

