import { createSelector } from 'reselect'
import { toLower } from 'lodash';

export const getSearchLedgerResults = (state, prop) => state.ledgers.searchLedgerResults
export const getLedgerList = (state, prop) => state.ledgers.byLedgerId
export const getLedgerObj = (state, prop) => state.ledgers.ledgerObj
export const getSearchFetched = (state, prop) => state.ledgers.params.searchFetched
export const getSelectedLedgers = (state, prop) => state.ledgers.ledgerObj

export const selectItem = createSelector(
    getLedgerList,
    items => {
        const keys = Object.keys(items)
        const obj = keys.map((key) => { return { value: key, key: key, text: items[key].ledgerName, name: items[key].ledgerName, path: items[key].path } })
        return obj
    })


export const selectLedger = (state, props, type) => { 
    const ledgers = getLedgerList(state);
    const keys = Object.keys(ledgers)
    const obj = keys.map( (key) => { 
            return { key : key, value : key, text : ledgers[key].ledgerName }
    }).filter((obj)=> toLower(obj.type) === toLower(type))
    return obj
}

export const selectLedgerSearched = createSelector(
    getSearchLedgerResults,
    ledger => { 
        const keys = Object.keys(ledger)
        const obj = keys.map( (key) => { return { key : key, value : key, text : ledger[key].ledgerName }})
        return obj
    })