import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { fetchItem, fetchItems } from "../data/actions";
import * as select from "../data/selectors";
import { getIsItemgroupFetched } from "../../itemgroup/data/selectors";
import { Field } from "formik";
import { FormikSelectComponent } from "../../../utilities/formUtils";
import userACL from "../../../store/access";
import { merge } from "lodash";

const ItemSelect = (
  {
    name,
    isTxn,
    isLabel,
    label,
    isSelection,
    focus,
    placeholder,
    validation,
    computeValue,
    index,
    fieldName,
    fromField,
    otherList,
    passFunct,
    getValue: callback,
    nature
  },
  props
) => {
  const itemsFetched = useSelector((state) =>
    select.getIsItemFetched(state, props)
  );
  const itemgroupFetched = useSelector((state) =>
    getIsItemgroupFetched(state, props)
  );
  // const options = useSelector((state) => select.selectItems(state, props));
  const options = useSelector((state) => select.selectItem(state, props));
  

  // const filteredOptions = options.filter((item)=>item.atypeId === itemValue);
  const dispatch = useDispatch();

  console.log("options=========", options);

  useEffect(() => {
    let values = merge({}, userACL.atFetch());
    values.nature=nature||""
    dispatch(fetchItem(values));
  }, []);

  const getSelectedValue = (value,values) => {
    // let opt=options.filter((item)=>{
    //   // console.log("asset Selected======", values,item)
    //   return value === item.key
    // })
  };

  // Loading Logic if tasks not fetched
  if (!itemsFetched  && options.length > 0) {
    return <p>Loading</p>;
  } else {
    return (
      <Field
        name={name}
        isLabel={isLabel}
        isTxn={isTxn}
        label={label}
        validation={validation}
        isSelection={isSelection}
        component={FormikSelectComponent}
        computeValue={computeValue}
        index={index}
        fieldName={fieldName}
        fromField={fromField}
        otherList={otherList}
        userProps={{ options, getValue: getSelectedValue }}
        focus={focus}
        placeholder={placeholder}
      >
        {" "}
      </Field>
    );
  }
};

export default ItemSelect;
