import * as type from './types'


export function createItemgroup(itemgroup) {
    return {
        type : type.CREATE_ITEMGROUP,
        payload : itemgroup,
        txn : 'initiated',
    };
}

export function savedItemgroup(itemgroup) {
    return {
        type : type.SAVED_ITEMGROUP,
        payload : itemgroup,
        txn : 'success',
        diff : true
    };
}

export function editItemgroup(itemgroup) {
    return {
        type : type.EDIT_ITEMGROUP,
        payload : itemgroup,
        txn : 'initiated',
    };
}

export function fetchItemgroup() {
    return {
        type : type.FETCH_ITEMGROUP,
    };
}

export function fetchedItemgroup(itemgroup) {
    return {
        type : type.FETCHED_ITEMGROUP,
        payload : itemgroup
    };
}

export function requestOptionsItemgroup(itemgroup) {
    return {
        type : type.OPTIONS_REQUESTED_ITEMGROUP,
        payload : itemgroup
    };
}

export function receivedOptionsItemgroup(itemgroup) {
    return {
        type : type.OPTIONS_RECEIVED_ITEMGROUP,
        payload : itemgroup
    };
}


export function deleteItemGroup(itemgroup) {
    return {
        type : type.DELETE_ITEMGROUP,
        payload: itemgroup,
    };
}

export function deletedItemGroup(msg) {
    return {
        type : type.DELETED_ITEMGROUP,
        payload : msg
    };
}

export function setNotifyDone(props) {
    return {
        type : type.NOTIFICATION_DONE_ITEMGROUP,
        payload: props
    };
}