import * as Yup from 'yup';
import v4 from 'uuid';
import { parseDateFromFormats } from '../../../utilities/parseDate';
import moment from 'moment'
import axios from 'axios';
import { handlError, parseError } from '../../app/serverError'
import { ASSET_URL } from '../../../store/path'
import { debounce, merge } from 'lodash'
import userACL from '../../../store/access' 

export const itemgroup = () => ({
    id: v4(),
    name: "",
    parent_id: "",
    path: "",
    segid: "",
    cid: "",
    rlb_box_id:"",
    status: 1,
    dt_create: "",
    dt_update: "",
    user_create: "",
    user_update: ""
})

Yup.addMethod(Yup.date, 'format', parseDateFromFormats);
export const itemgroupSchema = Yup.object().shape({

    id: Yup.string()
            .required('ID is a Required Field'),

    name: Yup.string()
            .min(2, 'Too Short! Minimum 2 Characters for Name is needed')
            .max(50, 'Too Long! Maximum 50 Characters for Name is allowed')
            .required('Name is a Required Field'),

    parent_id: Yup.string().required(),

    path: Yup.string().nullable(),

    status: Yup.number().integer().nullable(),
    segid: Yup.string().nullable(),
    cid: Yup.string().nullable(),
    rlb_box_id: Yup.string().nullable(),
    txnid : Yup.string().nullable(),
    dt_create: Yup.string().nullable(),
    dt_update: Yup.string().nullable(),
    user_create: Yup.string().nullable(),
    user_update: Yup.string().nullable()

  });

export const itemNameExistSchema=Yup.object().shape({
    name: Yup.string().required('Already exist')
})


export const duplicateCheckItemgroupName = debounce(checkItemGroupName, 500);
function checkItemGroupName ( value, values )  {
	const data = merge({type: 'check', id: values.id}, userACL.atFetch()) 
    // data.cid=data.segid
    data.rlb_box_id=data.boxId
    delete data.boxId
    if (value && value.length > 2)
        return axios.post(ASSET_URL+'/assetgroup/check/'+ value, data)
            .then(response => {
                        if (response.data==='duplicate') {
                            console.log("response",response.data)
                            return "Duplicate"
                        }
                    }) 
            .catch(error => "")
}
  