import React from 'react';
import { Route, withRouter } from 'react-router';
import UnitMaterList from "./components/unitMasterList";
import UnitMasterForm from "./components/unitMasterForm";
import { Container } from 'semantic-ui-react'


const UnitMaster = () => {
    return (
        <Container>
            <Route path="/unitmaster/list" component={withRouter(UnitMaterList)} />
            <Route path="/unitmaster/edit/:id" component={UnitMasterForm} />
            <Route path="/unitmaster/create" component={UnitMasterForm} />
        </Container>
    )
};

export default UnitMaster;