import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { fetchItemgroup } from "../data/actions";
import { fetchItems, fetchedItems, filterItems } from "../../items/data/actions";
import * as select from "../data/selectors";
import { Field } from "formik";
import { FormikSelectComponent } from "../../../utilities/formUtils";
import { selectItem } from "../../items/data/selectors";
import userACL from "../../../store/access";
import { merge } from "lodash";

const ItemgroupSelect = (
  {
    name,
    isTxn,
    isLabel,
    label,
    isMandatory,
    isSelection,
    focus,
    placeholder,
    validation,
    onSelectOf,
    computeValue,
    index,
    fieldName,
    fromField,
    otherList,
    getValue: callback,
  },
  props
) => {
  const [itemValue, setItemValue] = useState()
  const itemgroupFetched = useSelector((state) =>
    select.getIsItemgroupFetched(state, props)
  );
  const options = useSelector((state) =>
    select.selectItemgroup(state, props, onSelectOf)
  );
  const selectItemOptions = useSelector((state) => selectItem(state, props));
  const dispatch = useDispatch();

  console.log("options======", options);
  console.log("selectItemOptions======", selectItemOptions);

  useEffect(() => {
    if (!itemgroupFetched) {
      dispatch(fetchItemgroup());
    }
  }, []);

  useEffect(()=>{
       if(itemValue && selectItemOptions.length){
        const filteredOptions = selectItemOptions.filter((item)=>item.groupId === itemValue);
        if(filteredOptions.length){
          dispatch(fetchedItems([...filteredOptions]))
        }
       }
  },[selectItemOptions,itemValue])


  const getValue = (value, values, setFieldValue) => {
    // values=merge({},userACL.atFetch())
    console.log("value5======", value);
    if (value) {
      let data = merge({}, userACL.atFetch());
      data.name = "";
      dispatch(fetchItems(data));
      setItemValue(value) 
      setFieldValue("name","")
    }
  };

  // Loading Logic if tasks not fetched
  if (!itemgroupFetched && options.length > 0) {
    return <p>Loading</p>;
  } else {
    return (
      <Field
        name={name}
        isLabel={isLabel}
        isTxn={isTxn}
        isMandatory={isMandatory}
        label={label}
        validation={validation}
        isSelection={isSelection}
        component={FormikSelectComponent}
        computeValue={computeValue}
        index={index}
        fieldName={fieldName}
        fromField={fromField}
        otherList={otherList}
        userProps={{ options, getValue }}
        focus={focus}
        placeholder={placeholder}
      >
        {" "}
      </Field>
    );
  }
};

export default ItemgroupSelect;
