import React from 'react';
import { Route, withRouter } from 'react-router';
import ItemgroupList from "./components/itemgroupList";
import ItemgroupForm from "./components/itemgroupForm";
import { Container } from 'semantic-ui-react'


const Itemgroup = () => {
    return (
        <Container>
            <Route path="/itemgroup/list" component={withRouter(ItemgroupList)} />
            <Route path="/itemgroup/edit/:id" component={ItemgroupForm} />
            <Route path="/itemgroup/create" component={ItemgroupForm} />
        </Container>
    )
};

export default Itemgroup;

