import { combineReducers } from 'redux';
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr';

// Define a vdetailss schema
const vdetailsSchema = new schema.Entity('vdetailss',{}, { idAttribute: 'id' });
const vdetailsListSchema = [vdetailsSchema];

const defaultVdetailsParams = {
  vdetailsFetched: false,
  createTitle: 'Create New Vdetails',
  createSubmitButtonText: 'Submit',
  editTitle: 'Edit Vdetails',
  editSubmitButtonText: 'Submit' 
}


function byId(state = {}, action) {
    if (action.type === 'SAVED_VDETAILS') {
        const vdetailsID = action.payload.id;
      return {...state, 
        [vdetailsID]: action.payload
      }
    }
    if (action.type === 'FETCHED_VDETAILS') { 
        const normalizedVdetails = normalize(action.payload, vdetailsListSchema);
        return merge({},state,normalizedVdetails.entities.vdetailss)
    } else {
      return state
    }
  }

function params(state = defaultVdetailsParams, action) {
  if (action.type === 'FETCHED_VDETAILS') { 
      return {...state, 
        vdetailsFetched: true,
        isFetching:false
      }
  } else {
    return state
  }
}

const vdetails = combineReducers({
    byId,
    params
});

export default vdetails;

