export const getVoucher = (state, props) => {
    // console.log(props.match.path, "=====props.match.path")
    if (props.match.path === '/voucher/create') {
        let object = newVoucher()
        // let propsGet = cloneDeep(props)
        // object.props = propsGet
        return object
    }else if (props.match.path === '/voucher/edit/:id') {
        let _id = props.match.params.id
        console.log(_id, "===============================id")
        let obj = cloneDeep((props.location.state.nature === "Sale" || props.location.state.nature === "sale") ? state.voucher.byListIdSale[_id] : state.voucher.byListId[_id]);
        let totalInvoice = 0;

        for (var i = 0; i < obj.tdetails.length; i++) {
            let amount = obj.tdetails[i].amount
            let gstAmt = obj.tdetails[i].igst_amt
            let cessAmt = obj.tdetails[i].cess_amt

            totalInvoice += parseFloat(amount) + parseFloat(gstAmt) + parseFloat(cessAmt);

        }
        obj.totalInvAmt = totalInvoice

        return obj
    }
}

export const getVdetails = (state, props) => {
    if (props.match.path === '/voucher/create') {
        let vdetailsFromVid = [newVdetails()]
        return vdetailsFromVid
    }
}

export const getVoucherSearchList = (state, props) => {
    return voucherSearchList()
}

export const getVoucherParams = (state, props) => {
    const params = {}

    if (startsWith(state.router.location.pathname, '/voucher/create')) {
        params.title = state.voucher.params.createTitle
        params.submitButtonText = state.voucher.params.createSubmitButtonText
    };

    if (startsWith(state.router.location.pathname, '/voucher/edit/')) {
        params.title = state.voucher.params.editTitle
        params.submitButtonText = state.voucher.params.editSubmitButtonText
    };

    return params
}

