export const getFilteredVoucher = createSelector(
    getVoucherList,getVtypeList,
    (voucher, vtype)=>{
        // console.log('vtype....', vtype)
        let filteredVoucher= Object.keys(voucher).map(function(key){
            return voucher[key];
        })
        return filteredVoucher;
    }
)

export const getFilteredSaleVoucher = createSelector(
    getVoucherListSale,getVtypeList,
    (voucher, vtype)=>{
        let filteredVoucher= Object.keys(voucher).map(function(key){
            return voucher[key];
        })

        return filteredVoucher;
    }
)

