import { call, takeEvery, all, put, select } from 'redux-saga/effects'
import { getIsVdetailsFetched } from './selectors'
import {savedVdetails,fetchedVdetails} from './actions'
import axios from 'axios';
import { handlError, parseError } from '../../app/serverError'
import { ASSET_URL } from '../../../store/path'

// const delay = (ms) => new Promise(res => setTimeout(res, ms))

//#region Saga for Create Vdetails 

function* createVdetails() {
    yield takeEvery('-',saveVdetails)
    // yield takeEvery('CREATE_VDETAILS',saveVdetails)
  }

function* saveVdetails(action){
  const { response, error } = yield call(saveVdetailsAPI, action.payload)
  console.log("Saga",action.payload)
  if (response) yield put (savedVdetails(action.payload))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function saveVdetailsAPI(data) {
    return axios.post(ASSET_URL+'/vdetails/add', data)
      .then(response => ({ response }))
      .catch(error => ({ error }))
  }

//#endregion


//#region Saga for Edited Vdetails 

function* editVdetails() {
  yield takeEvery('-',saveEditedVdetails)
  // yield takeEvery('EDIT_VDETAILS',saveEditedVdetails)
}

function* saveEditedVdetails(action){
  const { response, error } = yield call(editVdetailsAPI, action.payload)  
  if (response) yield put (savedVdetails(action.payload))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function editVdetailsAPI(data) {
  let user_id = data.id
  return axios.post(ASSET_URL+'/vdetails/update/'+ user_id, data)
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for List Tasks 

function* fetchVdetails() {

  yield takeEvery('FETCH_VDETAILS',requestVdetails)

}
  
function* requestVdetails(){

  const isVdetailsFetched = yield select(getIsVdetailsFetched);
  if(!isVdetailsFetched){
    let { data } = yield call(requestVdetailsAPI)
    yield put (fetchedVdetails(data))
  }

}

function requestVdetailsAPI(data) {
  return axios.post(ASSET_URL+'/vdetails', data)
}

//#endregion


const sagaErrorMessage = (error, action) => {
    console.group("Saga Error:"+action.type)
    console.log(error)
    console.groupEnd()
}

export default function* vdetailsSaga() {
    yield all([
        createVdetails(),
        fetchVdetails(),
        editVdetails()
    ])
  }  