import { combineReducers } from 'redux';
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr';

// Define a employees schema
const employeeSchema = new schema.Entity('employees',{}, { idAttribute: 'id' });
const employeeListSchema = [employeeSchema];

const defaultEmployeeParams = {
  employeeFetched: false,
  isFetching:false,
  createTitle: 'Create New Employee',
  createSubmitButtonText: 'Submit',
  editTitle: 'Edit Employee',
  editSubmitButtonText: 'Submit' 
}

const defaultMessages = {
  id: {
    save: {
      status: "",
      msg: "",
      isSaving: false
    },
    edit: {
      status: "",
      msg: "",
      isSaving: false
    },
    delete: {
      status: "",
      msg: "",
      isSaving: false
    }
  }
}

function byId(state = {}, action) {
    if (action.type === 'SAVED_EMPLOYEE') {
      const employeeID = action.payload.id;
      return {...state, 
        [employeeID]: action.payload
      }
    }
    if (action.type === 'FETCHED_EMPLOYEE') { 
        const normalizedEmployee = normalize(action.payload, employeeListSchema);
        return merge({},state,normalizedEmployee.entities.employees)
    }
    if (action.type === 'DELETED_EMPLOYEE') {
      if(action.payload.msg.type === "success"){
        const employeeID = action.payload.id;
        let finalState = {...state}
        delete finalState[employeeID]
        return finalState
      }else{
        return state
      }
    } else {
      return state
    }
  }

function params(state = defaultEmployeeParams, action) {
  if (action.type === 'FETCH_EMPLOYEE') { 
    return {...state, 
      employeeFetched: false,
      isFetching:true
    }
}
  if (action.type === 'FETCHED_EMPLOYEE') { 
      return {...state, 
        employeeFetched: true,
        isFetching:false
      }
  }
   else {
    return state
  }
}


function notifications(state = {}, action) {
  if (action.type === 'SAVED_EMPLOYEE') {
    return {...state, 
      [action.payload.id]: { ...state[action.payload.id],
        save: { 
          status : action.txn === "success" ? 'success' : 'error',
          msg : action.txn
        }
      }
    }
  }
  if (action.type === 'DELETED_EMPLOYEE') { 
    return {...state, 
      [action.payload.id]: { ...state[action.payload.id],
        delete: { 
          status : action.payload.msg === "Success" ? 'success' : 'error',
          msg : action.payload.msg
        }
      }
    }
  } 
  if(action.type === 'NOTIFICATION_DONE_EMPLOYEE'){
    const {id, type} = action.payload
    // Remove the 'id' element from state
    const { [id]: idValue, ...restOfState } = state;
    // Remove the 'type' from the 'id' element
    const { [type]: removedValue, ...restOfId } = idValue;
    // Merge back together
    const finalState = { ...restOfState, [id]: restOfId };     
    return finalState

  } else {
    return state
  }
}

const employee = combineReducers({
    byId,
    notifications,
    params
});

export default employee;

