import { combineReducers } from 'redux';
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr';

// Define a vouchers schema
const voucherSchema = new schema.Entity('vouchers', {}, { idAttribute: 'id' });
const voucherListSchema = [voucherSchema];


const defaultVoucherParams = {
  voucherFetched: false,
  searchFetched: false,
  voucherEditFetched: false,
  fromVoucherList: false,
  isFetching:false,
  isSearching:false,
  getVid: 0,
  createTitle: 'Create New Voucher',
  createSubmitButtonText: 'Submit',
  editTitle: 'Edit Voucher',
  editSubmitButtonText: 'Submit'
}


function byId(state = {}, action) {
  // console.log(action.payload, "=======================")
  if (action.type === 'SAVED_VOUCHER') {
    const voucherID = action.payload.id;
    return {
      ...state,
      [voucherID]: action.payload
    }
  }
  if (action.type === 'EDIT_FETCHED_VOUCHER') {
    const voucherID = action.payload.id;
    return {
      ...state,
      [voucherID]: action.payload
    }
  } else {
    return state
  }
}


function byIdSale(state = {}, action) {
  // console.log(action.payload, "=============ByIdsale")
  if (action.type === 'SAVED_SALE_VOUCHER') {
    const voucherID = action.payload.id;
    return {
      ...state,
      [voucherID]: action.payload
    }
  }
  if (action.type === 'EDIT_FETCHED_SALE_VOUCHER') {
    const voucherID = action.payload.id;
    return {
      ...state,
      [voucherID]: action.payload
    }
  } else {
    return state
  }
}

function byListId(state = {}, action) {
  if (action.type === 'SAVED_VOUCHER') {
    console.log(action.payload, "edit payload")
    const voucherID = action.payload.id;
    return {
      ...state,
      [voucherID]: action.payload
    }
  }
  if (action.type === 'EDIT_FETCHED_VOUCHER') {
    const voucherID = action.payload.id;
    return {
      ...state,
      [voucherID]: action.payload
    }
  }
  if (action.type === 'FETCHED_VOUCHER') {
    const normalizedVoucher = normalize(action.payload.data, voucherListSchema);
    console.log('--------data----', action.payload)
    return merge({}, state, normalizedVoucher.entities.vouchers)
  }
  if (action.type === 'UPDATE_LIST_VOUCHER') {
    const voucherID = action.payload.id;
    return {
      ...state,
      [voucherID]: action.payload
    }
  }
  if (action.type === 'DELETED_VOUCHER') {
    if (action.payload.msg.type === "success") {
      const voucherID = action.payload.id;
      //     return {...state, 
      //       [voucherID]: { ...state[voucherID],
      //         status :0
      //       }
      //     }
      //   }else{
      //     return state
      //   }
      // } else {
      //   return state
      // }
      let finalState = { ...state }
      delete finalState[voucherID]
      return finalState
    } else {
      return state
    }
  } else {
    return state
  }
}

