function byListIdSale(state = {}, action) {
  if (action.type === 'SAVED_SALE_VOUCHER') {
    const voucherSaleID = action.payload.id;
    return {
      ...state,
      [voucherSaleID]: action.payload
    }
  }
  if (action.type === 'EDIT_FETCHED_SALE_VOUCHER') {
    const voucherID = action.payload.id;
    return {
      ...state,
      [voucherID]: action.payload
    }
  }
  if (action.type === 'FETCHED_SALE_VOUCHER') {
    const normalizedVoucher = normalize(action.payload.data, voucherListSchema);
    console.log('--------saledata', action.payload)
    return merge({}, state, normalizedVoucher.entities.vouchers)
  }

  if (action.type === 'DELETED_VOUCHER') {
    if (action.payload.msg.type === "success") {
      const voucherID = action.payload.id;
      let finalState = { ...state }
      delete finalState[voucherID]
      return finalState
    } else {
      return state
    }
  } else {
    return state
  }
}

function params(state = defaultVoucherParams, action) {
  if(action.type=== 'FETCH_VOUCHER'){
    return{
      ...state,
      voucherFetched:false,
      isFetching:true
    }
  }

  if (action.type === 'FETCHED_VOUCHER') {
    return {
      ...state,
      voucherFetched: true,
      isFetching: false
    }
  }

  if(action.type=== 'FETCH_SALE_VOUCHER'){
    return{
      ...state,
      voucherFetched:false,
      isFetching:true
    }
  }

  if(action.type=== 'FETCHED_SALE_VOUCHER'){
    return{
      ...state,
      voucherFetched:true,
      isFetching:false
    }
  }

  if (action.type === 'EDIT_FETCHED_VOUCHER') {
    return {
      ...state,
      voucherEditFetched: true,
      getVid: action.payload.id
    }
  }
  if (action.type === 'SEARCH_COST') {
    return {
      ...state,
      searchFetched: false,
      isSearching: true
    }
  }
  if (action.type === 'STORE_SEARCH_COST_RESULT') {
    return {
      ...state,
      searchFetched: true,
      voucherFetched: true,
      isSearching: false
    }
  }
  if (action.type === 'SET_V_FETCH_STATUS') {
    return {
      ...state,
      voucherEditFetched: false,
      fromVoucherList: action.payload
    }
  } else {
    return state
  }
}

function notifications(state = {}, action) {
  if (action.type === 'SAVED_VOUCHER') {
    return {
      ...state,
      [action.payload.id]: {
        ...state[action.payload.id],
        save: {
          status: action.txn.type === "success" ? 'success' : 'error',
          msg: action.txn.msg
        }
      }
    }
  }
  if (action.type === 'SAVED_SALE_VOUCHER') {
    return {
      ...state,
      [action.payload.id]: {
        ...state[action.payload.id],
        save: {
          status: action.txn.type === "success" ? 'success' : 'error',
          msg: action.txn.msg
        }
      }
    }
  }
  if (action.type === 'DELETED_VOUCHER') {
    return {
      ...state,
      [action.payload.id]: {
        ...state[action.payload.id],
        delete: {
          status: action.payload.msg.type === "success" ? 'success' : 'error',
          msg: action.payload.msg.msg
        }
      }
    }
  }
  if (action.type === 'NOTIFICATION_DONE_VOUCHER') {
    const { id, type } = action.payload
    // Remove the 'id' element from state
    const { [id]: idValue, ...restOfState } = state;
    // Remove the 'type' from the 'id' element
    const { [type]: removedValue, ...restOfId } = idValue;
    // Merge back together
    const finalState = { ...restOfState, [id]: restOfId };
    return finalState

  } else {
    return state
  }
}

