import React, { useState } from "react";
import { Container, Header, Form, Dropdown, Button } from "semantic-ui-react";
import { createVtype, editVtype, setNotifyDone, fetchedVtype } from "../data/actions";
import { Formik, Field, Form as FormikForm } from "formik";
import { FormikInputComponent, FormikSelectComponent, FormikDisplayLabelComponent } from "../../../utilities/formUtils";
import { getVtype, getVtypeParams, getNotification, getVtypeList } from "../data/selectors";
import { vTypeSchema } from "../data/model";
import userACL from "../../../store/access";
import { useSelector, useDispatch } from "react-redux";
import VtypeNatureSelect from "./vtypeNatureSelect";
import RlbVtypeSelect from "./rlbVtypeSelect"
import VTypeSelect from "./vtypeSelect";
import Notification from '../../../utilities/notificationUtils'
import v4 from 'uuid'


const VtypeMaster = (props) => {


  const vtype = useSelector((state) => getVtype(state, props));
  const params = useSelector((state) => getVtypeParams(state, props));
  const dispatch = useDispatch();

  const [savedVtypeId, setSavedVtypeId] = useState(false)

  const saveVtype = (values, resetForm) => {
    if (props.match.path === "/vtype/create") {
      values.id = v4();
      userACL.atCreate(values);
      // values.segid = values.cid;
      values.rlb_box_id=values.boxId
      delete values.txnid
      delete values.boxId
      dispatch(createVtype(values));
      setSavedVtypeId(values.id)
      resetForm();
    }

    if (props.match.path === "/vtype/edit/:id") {
      userACL.atUpdate(values);
      // values.segid = values.cid;
      values.rlb_box_id=values.boxId
      delete values.txnid
      delete values.boxId
      dispatch(editVtype(values));
      setSavedVtypeId(values.id)
      setTimeout(function(){
        props.history.push(`/vtype/list`);
      },1000)
    }
  };

  return (
    <Container>
      <Header as="h2">{params.title}</Header>
      <Formik
        initialValues={vtype}
        validationSchema={vTypeSchema}
        onSubmit={(values, { resetForm }) => saveVtype(values, resetForm)}
        render={({ handleSubmit, errors, onChange, values, handleChange, setFieldValue, }) => (
          <Form as={FormikForm} onSubmit={handleSubmit} className="CustomeForm">
            <Form.Group widths={3}>
              <Field name="name" label="Name" component={FormikInputComponent} isMandatory={true} />
            </Form.Group>

            <Form.Group widths={3}>
              {props.match.path === '/vtype/create' ?
                <VtypeNatureSelect name='nature' isTxn={false} isSelection='true' label='Nature' isMandatory={true} />
                :
                <>
                  <Field component={FormikDisplayLabelComponent} isLabel={false} label='Nature' classExtra={"topExtra"} text={vtype ? vtype.nature : ''} isMandatory={true} />
                  <br />
                </>

              }
            </Form.Group>

            <Form.Group widths={3}>
              <RlbVtypeSelect name="rlb_vtype" label="RealBooks Voucher Type" classNameGet="minWtSet10em" isTxn='false' isSelection={true} text="rlb_vtype" index={0} fieldName={''} fromField='' focus={true}  isMandatory={true}/>
              {/* <Field name="rlb_vtype" label="RealBooks Voucher Type" component={FormikInputComponent} isMandatory={true} /> */}
            </Form.Group>
            <Button type="submit" size="medium" color='blue' className="CustomeBTN"> {params.submitButtonText} </Button>
            {savedVtypeId ?
              <Notification id={savedVtypeId} notifySelector={getNotification} notifyDoneAction={setNotifyDone} type='save' />
              :
              null}
          </Form>
        )}
      />
    </Container>
  );
};

export default VtypeMaster
