import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { fetchUnitMaster } from '../data/actions'
import * as select from '../data/selectors'
import { Field } from 'formik'
import { FormikSelectComponent } from '../../../utilities/formUtils'


const UnitSelect = ({ name, isTxn, isLabel, label, isSelection, focus, placeholder, validation, computeValue, index, fieldName, isMandatory, fromField, otherList, passFunct, getValue : callback }, props) => {


    const unitFetched = useSelector(state => select.getIsUnitMasterFetched(state, props))
    const options = useSelector(state => select.selectUnit(state, props))
    const dispatch = useDispatch()


    useEffect(()=>{
        if (!unitFetched)
            dispatch(fetchUnitMaster());
    },[]);  

    
    const getValue = (values) => {
        //console.log("Asset Type Selected", values, name)
        if(passFunct){
            passFunct(values)
        }
    }


    // Loading Logic if tasks not fetched
    if (!unitFetched && options.length > 0 ){
        return <p>Loading</p>
    }  else {        
        return (
            <Field name={name} isLabel={isLabel} isTxn={isTxn} label={label} validation={validation} isSelection={isSelection} component={FormikSelectComponent} computeValue={computeValue} index={index} fieldName={fieldName} fromField={fromField} otherList={otherList} userProps={{ options, getValue }} focus={focus} placeholder={placeholder} isMandatory={isMandatory}> </Field>
        )
    }


}


export default UnitSelect;
