import * as type from "./types"

export function createFile(props) {
    return {
        type : type.CREATE_FILE,
        payload : props,
        // txn : 'initiated',
    };
}

export function savedFile(props) {
    return {
        type : type.SAVED_FILE,
        payload : props,
        // txn : 'success',
        // diff : true
    };
}

export function fetchFileList(props) {
    return {
        type: type.FETCH_FILE_LIST,
        payload: props,
    }
}

export function fetchedFileList(props) {
    return {
        type: type.FETCHED_FILE_LIST,
        payload: props,
    }
}

export function downloadFile(option) {
    return {
        type : type.DOWNLOAD_FILE,
        payload : option
    };
}

export function downloadedFile(dreport) {
    return {
        type : type.DOWNLOADED_FILE,
        payload : dreport
    };
}