import * as type from './types'


export function createDepreciation(depreciation) {
    return {
        type : type.CREATE_DEPRECIATION,
        payload : depreciation,
        txn : 'initiated',
    };
}

export function savedDepreciation(depreciation) {
    return {
        type : type.SAVED_DEPRECIATION,
        payload : depreciation,
        txn : 'success',
        diff : true
    };
}

export function editDepreciation(depreciation) {
    return {
        type : type.EDIT_DEPRECIATION,
        payload : depreciation,
        txn : 'initiated',
    };
}

export function fetchDepreciation(param) {
    return {
        type : type.FETCH_DEPRECIATION,
        payload: param
    };
}

export function fetchedDepreciation(depreciation) {
    return {
        type : type.FETCHED_DEPRECIATION,
        payload : depreciation
    };
}


export function deleteDepreciation(depreciation) {
    return {
        type : type.DELETE_DEPRECIATION,
        payload: depreciation
    };
}

export function deletedDepreciation(msg) {
    return {
        type : type.DELETED_DEPRECIATION,
        payload : msg
    };
}

export function setNotifyDone(props) {
    return {
        type : type.NOTIFICATION_DONE_DEPRECIATION,
        payload: props
    };
}