import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'
import { fetchVtype, setNotifyDone, deleteVtype } from '../data/actions'
import * as select from '../data/selectors'
import { Container, Table, TableHeader, TableBody, Header, Modal, Button } from 'semantic-ui-react'
import userACL from '../../../store/access'
import Notification from '../../../utilities/notificationUtils'


const VtypeRow = props => {

    const dispatch = useDispatch();
    const isNegative = parseInt(props.vtype.status, 10) === 1 ? false : true;
    const [isModalOpen, setModalOpen] = useState(false)
    const [deleteVtypeId, setDeleteVtypeId] = useState(false);

    const deleteVtypeFromList = (values) => {
        const getVlaue = { id: values }
        userACL.atUpdate(getVlaue)
        dispatch(deleteVtype(getVlaue))
        setDeleteVtypeId(values)
        setModalOpen(false)
    }

    const closeModal = () => {
        setModalOpen(false)
    }

    return (
        <Table.Row>
            <Table.Cell>{props.vtype.name}</Table.Cell>
            <Table.Cell>{props.vtype.nature}</Table.Cell>
            <Table.Cell>{props.vtype.rlb_vtype}</Table.Cell>
            <Table.Cell>
                {isNegative === false ?
                    <>
                        <Link to={"/vtype/edit/"+props.vtype.id}>Edit</Link><span>&nbsp;&nbsp;</span>
                        <span style={{ cursor: 'pointer', color: 'red' }} onClick={() => { setModalOpen(true) }} >Delete</span>
                    </>
                    :
                    <span>Deleted</span>
                }
                <>
                    {
                        deleteVtypeId ?
                            <Notification id={deleteVtypeId} notifySelector={select.getNotification} notifyDoneAction={setNotifyDone} type='delete'/>
                            :
                            null
                    }
                </>
                <Modal open={isModalOpen} size="mini">
                    <Modal.Header>Vtype List</Modal.Header>
                    <Modal.Content>
                        <h4>Do you want to delete this row??</h4>
                    </Modal.Content>
                    <Modal.Actions>
                        <Button type="button" positive   onClick={() => closeModal()}>No</Button>
                        <Button type="button" color='red' onClick={() => deleteVtypeFromList(props.vtype.id)}>Yes</Button>

                    </Modal.Actions>
                </Modal>
            </Table.Cell>
        </Table.Row>
    )
}

const VtypeList = (props) => {

    const vtype = useSelector(state => state.vtype.byId)
    const vtypeFetched = useSelector(state => select.getIsVtypeFetched(state, props))
    const dispatch = useDispatch();

    console.log("vtype========",vtype)
    console.log("vtypeFetched========",vtypeFetched)


    useEffect(() => {
        if (!vtypeFetched) 
        dispatch(fetchVtype());
    },[]);

    const tableStyle = {
        header: {
            position: "fixed",
            width: "100%",
            backgroundColor: "#fff"
        },
        tableHead: {
            overflowY: "auto",
            height: '580px',
            backgroundColor: "#fff"
        },
        table: {
            borderCollapse: "collapse",
            width: '100%',
            marginTop:"40px"
        },
        th: {
            position: "sticky",
            backgroundColor: "#fff",
            top: "28px",
            zIndex: "1",
            padding: "25px 0"
        }
    }

    // Loading Logic if tasks not fetched
    if (!vtypeFetched) {
        return <p>Loading ... </p>
    } else {
        return (
            <Container style={tableStyle.tableHead}>
                <Header style={tableStyle.header} as='h1' align='left'>Vtype List</Header>
                <Table selectable basic='very' style={tableStyle.table}>
                    <TableHeader>
                        <Table.Row>
                            <Table.HeaderCell style={tableStyle.th}>Name</Table.HeaderCell>
                            <Table.HeaderCell style={tableStyle.th}>Nature</Table.HeaderCell>
                            <Table.HeaderCell style={tableStyle.th}>RealBooks Voucher Type</Table.HeaderCell>
                            <Table.HeaderCell style={tableStyle.th}>Action</Table.HeaderCell>
                        </Table.Row>
                    </TableHeader>
                    <TableBody>
                        {Object.keys(vtype).map((key) =>
                            <VtypeRow vtype={vtype[key]} key={vtype[key].id} /> )}
                    </TableBody>
                </Table>
            </Container>
        )
    }

}

export default VtypeList;

