import { createSelector } from 'reselect'
import { startsWith, cloneDeep } from 'lodash';
import { year } from './model'

export const getIsFetchingYear = (state, props) => state.year.params.isFetching;
export const getIsYearFetched = (state, props) => state.year.params.yearFetched;
export const getYearList = (state, prop) => state.year.byId
export const getNotification = (state, id) => state.year.notifications[id]
// const orgSelected = (state) => state.org.params.selected;


export const getYear = (state, props) => {
    if(props.match.path==='/year/create'){
        return year()
    }
    if(props.match.path==='/year/edit/:id'){        
        let _id = props.match.params.id
        let obj = cloneDeep(state.year.byId[_id]);
        return obj
    }
}

export const getYearParams = (state, props) => {
    const params =  {}

    if(startsWith(state.router.location.pathname,'/year/create')){ 
        console.log(state.year)
        params.title = state.year.params.createTitle
        params.submitButtonText = state.year.params.createSubmitButtonText        
    };

    if(startsWith(state.router.location.pathname,'/year/edit/')){ 
        params.title = state.year.params.editTitle
        params.submitButtonText = state.year.params.editSubmitButtonText        
    };

    return params
}


export const selectYear = createSelector(
    getYearList,
    year => { 
        const keys = Object.keys(year)
        const obj = keys.map( (key) => { return { key : key,  value : key, text : year[key].name }})
        return obj
    })