import { combineReducers } from "redux";
import { merge } from 'lodash'
import { normalize, schema } from "normalizr";


// Define a depreciations schema
const unitMasterSchema = new schema.Entity('unitmasters', {}, { idAttribute: 'id' });
const unitMasterListSchema = [unitMasterSchema];

const defaultUnitMasterParams = {
  unitMasterFetched: false,
  isFetching: false,
  createTitle: 'Create New UnitMaster',
  createSubmitButtonText: 'Submit',
  editTitle: 'Edit UnitMaster List',
  editSubmitButtonText: 'Submit'
}

function byId(state = {}, action) {
  if (action.type === 'SAVED_UNIT_MASTER') {
    const UnitID = action.payload.id
    return {
      ...state,
      [UnitID]: action.payload
    }
  }

  if (action.type === 'FETCHED_UNIT_MASTER') {
    const normalizedUnitMaster = normalize(action.payload, unitMasterListSchema);
    return merge({}, state, normalizedUnitMaster.entities.unitmasters)
  }

  if (action.type === 'DELETED_UNIT_MASTER') {
    if (action.payload.msg.type === "success") {
      const unitMasterID = action.payload.id;
      let finalState = { ...state }
      delete finalState[unitMasterID]
      return finalState
    } else {
      return state
    }
  } else {
    return state
  }
}


function notifications(state = {}, action) {
  if (action.type === 'SAVED_UNIT_MASTER') {
    return {
      ...state,
      [action.payload.id]: {
        ...state[action.payload.id],
        save: {
          status: action.txn === "success" ? 'success' : 'error',
          msg: action.txn
        }
      }
    }
  }
  if (action.type === 'DELETED_UNIT_MASTER') {
    return {
      ...state,
      [action.payload.id]: {
        ...state[action.payload.id],
        delete: {
          status: action.payload.msg === "Success" ? 'success' : 'error',
          msg: action.payload.msg
        }
      }
    }
  }
  if (action.type === 'NOTIFICATION_DONE_UNIT_MASTER') {
    const { id, type } = action.payload
    // Remove the 'id' element from state
    const { [id]: idValue, ...restOfState } = state;
    // Remove the 'type' from the 'id' element
    const { [type]: removedValue, ...restOfId } = idValue;
    // Merge back together
    const finalState = { ...restOfState, [id]: restOfId };
    return finalState

  } else {
    return state
  }
}

function params(state = defaultUnitMasterParams, action) {
  if (action.type === 'FETCH_UNIT_MASTER') {
    return {
      ...state,
      unitMasterFetched: false,
      isFetching: true
    }
  }
  if (action.type === 'FETCHED_UNIT_MASTER') {
    return {
      ...state,
      unitMasterFetched: true,
      isFetching: false
    }
  } else {
    return state
  }
}


const unitmaster = combineReducers({
  byId,
  notifications,
  params

});

export default unitmaster;