export const bodyScrollRemove = (setHeight) => {
    let menuHeight = document.getElementById("std-menu-container") ? document.getElementById("std-menu-container").clientHeight : 0
    let windowHeight = window.innerHeight
    let setHeightDiv = parseFloat(windowHeight) - (parseFloat(menuHeight) + 2)
    let h2Height = document.getElementById("headerH2") ? document.getElementById("headerH2").clientHeight : 0
    let bottonSubmit = document.getElementById("bottonSubmit") ? document.getElementById("bottonSubmit").clientHeight : 0
    let lastHeight = parseFloat(setHeightDiv) - (parseFloat(h2Height) + parseFloat(bottonSubmit) + 50)
    setHeight(lastHeight)
}
