import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import * as select from '../data/selectors'
import { fetchRlbVtype } from '../data/actions'
import { Field } from 'formik'
import { FormikSelectComponent } from '../../../utilities/formUtils'


const LocationSelect = ({name, isTxn, isLabel, label, isSelection, focus, placeholder, validation, computeValue, index, fieldName, fromField, otherList, passFunct, getValue : callback }, props) => {
    const dispatch = useDispatch()
    const rlbVtypeFetched = useSelector(state => select.getIsRlbVtypeFetched(state, props))
    const options = useSelector(state => select.selectRlbVType(state, props))
   
    
    useEffect(()=>{
        if (!rlbVtypeFetched)
            dispatch(fetchRlbVtype());
    },[]);  

    const getValue = (values) => {
        if(passFunct){
            passFunct(values)
        }
    }


    // if (rlbVtypeFetched && options.length > 0) {
    //     return <p>Loading...</p>
    // } else {
        return (
            <Field name={name} isLabel={isLabel} isTxn={isTxn} label={label} validation={validation} isSelection={isSelection} component={FormikSelectComponent} computeValue={computeValue} index={index} fieldName={fieldName} fromField={fromField} otherList={otherList} userProps={{ options, getValue }} focus={focus} placeholder={placeholder}> </Field>
        )
    // }

}


export default LocationSelect;
