import { searchColumns, voucherListSearchSchema } from "../data/model";
import Notification from "../../../utilities/notificationUtils";
import { searchLedger } from "../../ledger_search/data/actions";
import { fetchItems } from "../../items/data/actions";
import { FilterCell } from "../../../utilities/listUtils";
import { TableListDiv } from "./tableListDiv";
import { heightSet, rowBodyHeightSet } from "./heightSetCommList";
import { StickyTable, Row, Cell } from "react-sticky-table";
import NatureSelect from "./selectNature";
