import * as type from './types'


export function createAssettype(assettype) {
    return {
        type : type.CREATE_ASSETTYPE,
        payload : assettype,
        txn : 'initiated',
    };
}

export function savedAssettype(assettype) {
    return {
        type : type.SAVED_ASSETTYPE,
        payload : assettype,
        txn : 'success',
        diff : true
    };
}

export function editAssettype(assettype) {
    return {
        type : type.EDIT_ASSETTYPE,
        payload : assettype,
        txn : 'initiated',
    };
}

export function fetchAssettype() {
    return {
        type : type.FETCH_ASSETTYPE
    };
}

export function setFetchFalse() {
    return {
        type : type.SET_FETCH_FALSE
    };
}

export function fetchedAssettype(assettype) {
    return {
        type : type.FETCHED_ASSETTYPE,
        payload : assettype
    };
}


export function deleteAssettype(assettype) {
    return {
        type : type.DELETE_ASSETTYPE,
        payload: assettype
    };
}

export function deletedAssettype(msg) {
    return {
        type : type.DELETED_ASSETTYPE,
        payload : msg
    };
}

export function searchSegList() {
    return {
        type: type.SEARCH_SEG_LIST,
        
    }
}

export function storeSearchedSegList(props) {
    return {
        type: type.STORE_SEARCHED_SEG_LIST,
        payload: props
    }
}

export function setNotifyDone(props) {
    return {
        type : type.NOTIFICATION_DONE_ASSETTYPE,
        payload: props
    };
}