import { combineReducers } from 'redux';
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr';

const importSchema = new schema.Entity('files',{}, { idAttribute: 'id' });
const importListSchema = [importSchema];


function byId(state = {}, action) {
    if (action.type === 'SAVED_FILE') { //SAVED_FILE
      const fileID = action.payload.id;
      return {...state, 
        [fileID]: action.payload
      }
    }
    if (action.type === 'FETCHED_FILE_LIST') {
      const normalizedUnitMaster = normalize(action.payload, importListSchema);
      console.log('action.payload.data FETCHED_FILE_LIST ', action.payload)
      return merge({}, state, normalizedUnitMaster.entities.files)
    }

    if(action.type ==='DOWNLOADED_FILE'){
      // console.log("downloadedFile",action.payload.id)
      // return {...state,
      // [action.payload.type]:action.payload.data
      const fileID = action.payload.id;
      console.log("fileId",action.payload)
      return {...state, 
        [fileID]: action.payload
      }
    }
    
  //   if (action.type === 'FETCHED_FILE_LIST') {
  //     // const normalizeMenuList = normalize(action.payload.data, menuListFetchedSchema)
  //     // return merge({}, state, normalizeMenuList.entities.mapping)
  //     // console.log('action.payload.data FETCHED_MENU_LIST ', action.payload)
  //     if(action.payload.type.toString().toLowerCase() === "success"){
  //         if(action.payload.msg.toString().toLowerCase() === "no menu found"){
  //             return []
  //         }else{
  //             return action.payload.data
  //         }
  //     }else{
  //         return []
  //     }
      
  // }

  
  
    else {
      return state
    }
  }

 

  const file=combineReducers({
    byId
  })

