import { call, takeEvery, takeLatest, all, put, select } from 'redux-saga/effects'
import { getIsDreportFetched } from './selectors'
import {savedDreport,fetchedDreport, storeSearchDreportResult,downloadedDreport } from './actions'
import axios from 'axios';
import { handlError, parseError } from '../../app/serverError'
import { ASSET_URL } from '../../../store/path'
import userACL from '../../../store/access' 
import { merge } from 'lodash'


// const delay = (ms) => new Promise(res => setTimeout(res, ms))

//#region Saga for Create Dreport 


//#region Saga for List Tasks 

function* fetchDreport() {

  yield takeEvery('FETCH_DREPORT',requestDreport)

}
  
function* requestDreport(action){
    let { data } = yield call(requestDreportAPI, action.payload)
    yield put (fetchedDreport({data, type: action.payload.depreciation}))    
}

function requestDreportAPI(data) {
  data = merge({}, data, userACL.atFetch())
  data.rlb_box_id=data.boxId
  delete data.boxId
  return axios.post(ASSET_URL+'/depreciation/list', data, {crossDomain: true})
}

//#region Saga for Download Tasks 

function* downloadDreport() {
  yield takeEvery('DOWNLOAD_DREPORT',requestDownloadDreport)
}
function* requestDownloadDreport(action){
  let { response } = yield call(requestDownloadDreportAPI, action.payload)
  // let { data } = yield call(downloadFileApi, action.payload)
  if (response) {
    console.log("response download",action.payload)
    const url = window.URL.createObjectURL(new Blob([response.data]));
    const link = document.createElement("a");
    link.href = url;
    link.setAttribute("download", action.payload.depreciation+"year.xlsx");
    // link.setAttribute("download", action.payload.depreciation+action.payload.fy_id+".xlsx");
    document.body.appendChild(link);
    link.click();
    yield put(downloadedDreport(response))
}  
}

function requestDownloadDreportAPI(data) {
  data=merge({}, data, userACL.atFetch())
  data.rlb_box_id=data.boxId
  delete data.boxId
  const request_config = {
    method: "POST",
    url: ASSET_URL + '/depreciation/download',
    data: data,
    responseType: "blob",
    headers: { "Content-Type": "application/json" },
};
return axios(request_config, { crossDomain: true })
.then(response => ({ response }),
(error) => {console.log(error);}
);
}

//#endregion


const sagaErrorMessage = (error, action) => {
    console.group("Saga Error:"+action.type)
    console.log(error)
    console.groupEnd()
}

export default function* dreportSaga() {
    yield all([
        fetchDreport(),
        downloadDreport()      
    ])
  }  