import axios from 'axios';
import { merge } from 'lodash';
import { all, call, put, select, takeEvery } from 'redux-saga/effects';
import userACL from '../../../store/access';
import { ASSET_URL } from '../../../store/path';
import { handlError, parseError } from '../../app/serverError';
import { fetchedYear, savedYear } from './actions';
import { getIsYearFetched } from './selectors';

// const delay = (ms) => new Promise(res => setTimeout(res, ms))

//#region Saga for Create Year 

function* createYear() {
  yield takeEvery('CREATE_YEAR',saveYear)
}

function* saveYear(action){
const { response, error } = yield call(saveYearAPI, action.payload)
console.log("response",response)
if(response){
  if (response.data.type !== "error"){ 
    yield put (savedYear(action.payload))
  }
}
else {
  yield put (handlError(action, parseError(error)))
  sagaErrorMessage(error,action)  
}
}

function saveYearAPI(data) {
  return axios.post(ASSET_URL+'/year/add', data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}


//#endregion


//#region Saga for Edited Year 

function* editYear() {
  yield takeEvery('EDIT_YEAR',saveEditedYear)
}

function* saveEditedYear(action){
  const { response, error } = yield call(editYearAPI, action.payload)  
  if (response) {
    if(response.data.type!=='error')
    yield put (savedYear(action.payload))
    // yield put(fetchedYear(action.payload))
  // console.log("response==========",response.data)
  }
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function editYearAPI(data) {
  return axios.post(ASSET_URL+'/year/update', data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
    .catch(error => ( console.log({error})))
}

//#endregion


//#region Saga for List Tasks 

function* fetchYear() {

  yield takeEvery('FETCH_YEAR',requestYear)

}
  
function* requestYear(){

  const isYearFetched = yield select(getIsYearFetched);
  if(!isYearFetched){
    let { data } = yield call(requestYearAPI)
    yield put (fetchedYear(data.data))
  }

}
// TODO
// http://43.204.150.85:5061/year/list
function requestYearAPI() {
  let data = merge({}, userACL.atFetch())
  // data.segid = data.cid
  data.rlb_box_id = data.boxId
  delete data.txnid
  delete data.boxId
  return axios.post(ASSET_URL+'/year/list', data, {crossDomain: true})
}

//#endregion


const sagaErrorMessage = (error, action) => {
    console.group("Saga Error:"+action.type)
    console.log(error)
    console.groupEnd()
}

export default function* yearSaga() {
    yield all([
        createYear(),
        fetchYear(),
        editYear()
    ])
  }  