import { call, takeEvery, all, put, select } from "redux-saga/effects";
import { getIsVtypeFetched,getIsRlbVtypeFetched } from "./selectors";
import { savedVtype, fetchedVtype,fetchedRlbVtype, deletedVtype } from "./actions";
import axios from "axios";
import { handlError, parseError } from "../../app/serverError";
import { ASSET_URL } from "../../../store/path";
import userACL from "../../../store/access";
import { merge } from "lodash";


//#region saga for Create Vtype

function* createVtype() {
    yield takeEvery("CREATE_VTYPE", saveVtype);
}

function* saveVtype(action) {
    const { response, error } = yield call(saveVtypeAPI, action.payload);
    if (response) {
        if(response.data.type!=="error"){
        yield put(savedVtype(action.payload));
    }
    }else {
        yield put(handlError(action, parseError(error)));
        sagaErrorMessage(error, action);
    }
}


function saveVtypeAPI(data) {
    // data.rlb_box_id=data.boxId
    // delete data.boxId
    return axios.post(ASSET_URL + "/vtype/add", data)
        .then((response) => ({ response }))
        .catch((error) => ({ error }));
}

//#endregion

//#region Saga for Edited Vtype

function* editVtype() {
    yield takeEvery("EDIT_VTYPE", saveEditedVtype);
}

function* saveEditedVtype(action) {
    const { response, error } = yield call(editVtypeAPI, action.payload);
    if (response){
        if(response.data.type!=="error"){
     yield put(savedVtype(action.payload));
    }
    }else {
        yield put(handlError(action, parseError(error)));
        sagaErrorMessage(error.action);
    }
}

function editVtypeAPI(data) {
    let vtype_id = data.id;
    // data.rlb_box_id=data.boxId
    // delete data.boxId
    return axios.post(ASSET_URL + "/vtype/update", data, {crossDomain: true})
        .then((response) => ({ response }))
        .catch((error) => ({ error }));

}

//#endregion

//#region Saga for Delete Vtype

function* deleteVtype() {
    yield takeEvery("DELETE_VTYPE", saveDeletedVtype);
}

function* saveDeletedVtype(action) {
    const { response, error } = yield call(deleteVtypeAPI, action.payload);
    if (response)
        yield put(deletedVtype({ id: action.payload.id, msg: response.data }));
        
    else {
        yield put(handlError(action, parseError(error)));
        sagaErrorMessage(error, action);
    }
}

function deleteVtypeAPI(data) {
    let vTypeId = data.id;
    data.rlb_box_id=data.boxId
    delete data.boxId
    return axios.post(ASSET_URL + "/vtype/delete/" + vTypeId, data, {crossDomain: true})
        .then((response) => ({ response }))
        .catch((error) => ({ error }));
}

//#endregion

//#region Saga for List Tasks

function* fetchVtype() {
    yield takeEvery("FETCH_VTYPE", requestVtype);
}

function* requestVtype() {
    const isVtypeFetched = yield select(getIsVtypeFetched);
    if (!isVtypeFetched) {
        let { data } = yield call(requestVtypeAPI);
       
        yield put(fetchedVtype(data));
    }
}

function requestVtypeAPI() {
    let data = merge({}, userACL.atFetch());
    data.rlb_box_id=data.boxId
    delete data.boxId
    return axios.post(ASSET_URL + "/vtype/list", data, { crossDomain: true });
}


//region Saga for rlbvtyes

function* fetchrlbVtype() {
    yield takeEvery("FETCH_RLBVTYPE", requestrlbVtype);
}

function* requestrlbVtype() {
    const isVtypeFetched = yield select(getIsRlbVtypeFetched);
    if (!isVtypeFetched) {
        let { data } = yield call(requestrlbVtypeAPI);
       
        yield put(fetchedRlbVtype(data));
    }
}

function requestrlbVtypeAPI() {
    let data = merge({}, userACL.atFetch());
    data.rlb_box_id=data.boxId
    delete data.boxId
    return axios.post(ASSET_URL + "/vtype/rlbvtypes", data, { crossDomain: true });
}

//#endregion

const sagaErrorMessage = (error, action) => {
    console.group("Saga Error:" + action.type);
    console.log(error);
    console.groupEnd();
};

export default function* vtypeSaga() {
    yield all([
        createVtype(),
        deleteVtype(),
        fetchVtype(),
        fetchrlbVtype(),
        editVtype()
    ])
}  