import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'
import { fetchAssettype, deleteAssettype, setNotifyDone } from '../data/actions'
import { Container, Table, TableHeader, TableBody, Header, Modal, Grid, Button, Form } from 'semantic-ui-react'
import { Formik, Field, Form as FormikForm } from 'formik';
import userACL from '../../../store/access'
import Notification from '../../../utilities/notificationUtils'
import { displayAmtInLakh } from '../../../utilities/listUtils'
import moment from 'moment'
import { StickyTable, Row, Cell } from 'react-sticky-table'
import { heightSet, rowBodyHeightSet } from './heightSetAssetList'
import { TableListDiv } from './tableListDiv';
import { searchColumns } from '../data/model';
import { getFilteredAssetType, getIsAssettypeFetched, getNotification } from '../data/selectors';



const AssettypeList = (props) => {

    const assettype = useSelector(state => getFilteredAssetType(state))
    const assetFetched = useSelector(state => getIsAssettypeFetched(state, props))
    const [assetId, setAssetTypeId] = useState('')
    const [deleteAssetTypeId, setDeleteAssetTypeId] = useState(false);
    const [isModalOpen, setModalOpen] = useState(false)
    const [heights, setHeight] = useState('')
    const [getBodyHeight, setGetBodyHeight] = useState('')

    const dispatch = useDispatch();

    useEffect(() => {
        callHeightFunc()
        window.addEventListener('resize', function () {
            callHeightFunc()
        }, false);
    }, [])

    useEffect(() => {
        if (!assetFetched)
            dispatch(fetchAssettype())
    }, [])

    const deleteassetTypeFromList = (values) => {
        const getVlaue = { id: values }
        userACL.atUpdate(getVlaue)
        dispatch(deleteAssettype(getVlaue))
        setDeleteAssetTypeId(values.id)
        setModalOpen(false)
    }

    const callHeightFunc = () => {
        heightSet(setHeight)
        rowBodyHeightSet(setGetBodyHeight)
    }
    const closeModal = () => {
        setModalOpen(false)
    }

    const AssetTypeListActions = ({ object: assetobj }) => {
        return (
            <>
                <Link to={"/assettype/edit/" + assetobj.id}>Edit</Link><span>&nbsp;&nbsp;</span>
                <span style={{ cursor: 'pointer', color: 'red' }} onClick={() => {
                    setModalOpen(true)
                    setAssetTypeId(assetobj.id)
                }}>Delete</span>
            </>

        )
    }

    return (
        <Container>

            <div className='' style={{ height: heights + 'px' - 10, width: "100%" }}>
                <StickyTable className='tableLayout' stickyHeaderCount={1} stickyColumnCount={0} style={{ width: '100%' }}>
                    <TableListDiv columns={searchColumns} data={assettype} Actions={AssetTypeListActions} setHeight={parseFloat(heights) - 70} getIndex={15} filterBoxNone={''} rightBox={''} centerBox={' '} callHeightFunc={callHeightFunc} paddingShort={2} getWidth={'101%'} actionClass={"actionTdDR"} />
                </StickyTable>
            </div>

            {
                deleteAssetTypeId ?
                    <Notification id={deleteAssetTypeId} notifySelector={getNotification} notifyDoneAction={setNotifyDone} type='delete' />
                    :
                    null
            }


            <Modal open={isModalOpen} size="mini">
                <Modal.Header>AssettypeList List</Modal.Header>
                <Modal.Content>
                    <h4>Do you want to delete this row??</h4>
                </Modal.Content>
                <Modal.Actions>
                    <Button type="button" positive onClick={() => closeModal()}>No</Button>
                    <Button type="button" color='red' onClick={() => deleteassetTypeFromList(assetId)}>Yes</Button>
                </Modal.Actions>
            </Modal>
        </Container>
    )
}

export default AssettypeList
