import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import {
  fetchUnitMaster,
  setNotifyDone,
  deleteUnitMaster,
} from "../data/actions";
import * as select from "../data/selectors";
import {
  Container,
  Table,
  TableHeader,
  TableBody,
  Header,
  Button,
  Modal,
} from "semantic-ui-react";
import userACL from "../../../store/access";
import Notification from "../../../utilities/notificationUtils";

const UnitList = (props) => {
  const dispatch = useDispatch();
  const isNegative = props.unit.status === 1 ? false : true;
  const [isModalOpen, setModalOpen] = useState(false);
  const [unitList,setUnitList]=useState([])
  const [deleteUnitmasterId, setDeleteUnitmasterId] = useState(false);
  console.log(props.unit.name,"==========unit1")

  const deleteUnitmasterFromList = (values) => {
    const getVlaue = { id: values };
    userACL.atUpdate(getVlaue);
    dispatch(deleteUnitMaster(getVlaue));
    setDeleteUnitmasterId(values);
    setModalOpen(false);
  };

  // useEffect(()=>{
  //     dispatch(fetchUnitMaster())
  // },[])

  const closeModal = () => {
    setModalOpen(false);
  };

  return (
    <Table.Row>
      <Table.Cell>{props.unit.name}</Table.Cell>
      <Table.Cell>{props.unit.sname}</Table.Cell>
      <Table.Cell>{props.unit.display_name}</Table.Cell>
      <Table.Cell>{props.unit.decimal}</Table.Cell>
      <Table.Cell>
        {isNegative === false ? (
          <>
            <Link to={"/unitmaster/edit/" + props.unit.id}>Edit</Link>
            <span>&nbsp;&nbsp;</span>
            <span
              style={{ cursor: "pointer", color: "red" }}
              onClick={() => {
                setModalOpen(true);
              }}
            >
              Delete
            </span>
          </>
        ) : (
          <span>Deleted</span>
        )}
        <>
          {deleteUnitmasterId ? (
            <Notification
              id={deleteUnitmasterId}
              notifySelector={select.getNotification}
              notifyDoneAction={setNotifyDone}
              type="delete"
            />
          ) : null}
        </>
        <Modal open={isModalOpen} size="mini">
          <Modal.Header>UnitMaster List</Modal.Header>
          <Modal.Content>
            <h4>Do you want to delete this unit??</h4>
          </Modal.Content>
          <Modal.Actions>
            <Button type="button" positive onClick={() => closeModal()}>
              No
            </Button>
            <Button
              type="button"
              color="red"
              onClick={() => deleteUnitmasterFromList(props.unit.id)}
            >
              Yes
            </Button>
          </Modal.Actions>
        </Modal>
      </Table.Cell>
    </Table.Row>
  );
};

const UnitMasterList = (props) => {
  const unit = useSelector((state) => state.unitmaster.byId);
  console.log(unit, "===================unit");
  const unitFetched = useSelector((state) =>
    select.getIsUnitMasterFetched(state, props)
  );
  console.log(unitFetched, "===================unitFetched");

  const dispatch = useDispatch();

  useEffect(() => {
    if (!unitFetched) dispatch(fetchUnitMaster());
  }, []);

  const tableStyle = {
    header: {
      position: "fixed",
      width: "100%",
      backgroundColor: "#fff",
    },
    tableHead: {
      overflowY: "auto",
      // overflow:"hidden",
      height: "580px",
      backgroundColor: "#fff",
    },
    table: {
      borderCollapse: "collapse",
      width: "100%",
      marginTop: "40px",
    },
    th: {
      position: "sticky",
      backgroundColor: "#fff",
      top: "28px",
      zIndex: "1",
      padding: "25px 0",
    },
  };

  // Loading Logic if tasks not fetched
  if (unit.length > 0 || !unitFetched) {
    return <p>Loading ... </p>;
  } else {
    return (
      <Container style={tableStyle.tableHead}>
        <Header style={tableStyle.header} as="h1" align="left">
          UnitMaster List
        </Header>
        <Table selectable basic="very" style={tableStyle.table}>
          <TableHeader>
            <Table.Row>
              <Table.HeaderCell style={tableStyle.th}>
                Unit Name
              </Table.HeaderCell>
              <Table.HeaderCell style={tableStyle.th}>
                Short Name
              </Table.HeaderCell>
              <Table.HeaderCell style={tableStyle.th}>
                Display Name
              </Table.HeaderCell>
              <Table.HeaderCell style={tableStyle.th}>Decimal</Table.HeaderCell>
              <Table.HeaderCell style={tableStyle.th}>Action</Table.HeaderCell>
            </Table.Row>
          </TableHeader>
          <TableBody>
            {Object.keys(unit).map((key) => (
              <UnitList unit={unit[key]} key={unit[key].id} />
            ))}
          </TableBody>
        </Table>
      </Container>
    );
  }
};

export default UnitMasterList;
