import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { createItems } from '../data/actions';
import { itemsQuickSchema, duplicateCheckItemCode } from '../data/model'
import { getItemDepreciation, getQuickItems } from '../data/selectors';
import { Form, Button, Modal } from 'semantic-ui-react';
import { Formik, Field, Form as FormikForm, FieldArray } from 'formik';
import { FormikInputComponent, FormikVNumCodeComponent, FormikAmountComponent, FormikTextAreaComponent, FormikDateComponent } from '../../../utilities/formUtils';
import ItemgroupSelect from '../../itemgroup/components/itemgroupSelect'
import EmployeeSelect from '../../employee/components/employeeSelect'
import LocationSelect from '../../location/components/locationSelect'
import userACL from '../../../store/access'
import GstRateSelect from './gstRateSelect';
import CessRateSelect from './cessRateSelect';
import { items } from '../data/model'
import ItemsDepreciationArrayQuick from './itemsDepreciationArrayQuick'
import DepartmentSelect from '../../department/components/departmentSelect'
import AssetTypeSelect from '../../assettype/components/asetTypeSelect'
import { getAssettypeList } from '../../assettype/data/selectors'
import UnitSelect from '../../unitmaster/components/unitMasterSelect'
import { cloneDeep } from 'lodash'
import { editAssettype } from '../../assettype/data/actions';
import SubLocationSelect from '../../location/components/subLocationSelect';

// import UserDropdown from '../../users/components/userDropdown'

const ItemQuickForm = ({ setItemObject, index, name, assetGroupArray, ...props }) => {
    const [displayStatus, setDisplayStatus] = useState(false);
    //const items = useSelector(state => getItems(state, props))
    const itemDepreciation = useSelector(state => getItemDepreciation(state, items ? items.id : 0))
    const items = useSelector(state => getQuickItems(state, props))
    const createTitle = useSelector(state => state.items.params.createTitle)
    const asetTypeList = useSelector(state => getAssettypeList(state))
    const options = useSelector(state => getAssettypeList(state, props))
    const createSubmitButtonText = useSelector(state => state.items.params.createSubmitButtonText)
    const dispatch = useDispatch()
    const [selectedLocation, setSelectedLocation] = useState(items ? items.location : null)
    const [assetType, setAssetType] = useState('')
    const [assetTypeObj, setAssetTypeObj] = useState(options ? assetType ? options[assetType] : null : null);
    const [prefix, setPrefix] = useState(options ? assetType ? options[assetType] ? options[assetType].number_prefix : "" : "" : "");
    const [suffix, setSuffix] = useState(options ? assetType ? options[assetType] ? options[assetType].number_suffix : "" : "" : "");
    const [codeNo, setCodeNo] = useState(options ? assetType ? options[assetType] ? options[assetType].next_number : "" : "" : "");
    const [codeLength, setCodeLength] = useState(options ? assetType ? options[assetType] ? options[assetType].number_count : "" : "" : "");
    const [isEditVnum, setIsEditVnum] = useState(options ? assetType ? options[assetType] ? options[assetType].is_editable : "no" : "no" : "no");
    const [groupArray, setGroupArray] = useState(assetGroupArray)
    let height = (typeof InstallTrigger !== 'undefined') ? 5 : 6

    const saveItems = (values, resetForm) => {
        userACL.atCreate(values)
        values.op_dep_dt = values.op_dep_dt === "" ? null : values.op_dep_dt
        values.invc_dt = values.invc_dt === "" ? null : values.invc_dt
        values.warranty_end_dt = values.warranty_end_dt === "" ? null : values.warranty_end_dt
        values.insurance_exp_dt = values.insurance_exp_dt === "" ? null : values.insurance_exp_dt
        values.op_depreciation_val = values.op_depreciation_val === "" ? null : values.op_depreciation_val
        values.capitalisation_date = values.capitalisation_date === "" ? null : values.capitalisation_date
        values.gst_prcnt =  values.gst_prcnt === "" ? null : values.gst_prcnt
        values.cess_prcnt =  values.cess_prcnt === "" ? null : values.cess_prcnt
        dispatch(createItems(values))
        setItemObject(index, values, name)
        let getObject = cloneDeep(asetTypeList[assetType]),
            newCodeNo = parseInt(codeNo, 10) + 1;
        getObject.next_number = newCodeNo
        userACL.atUpdate(getObject)
        getObject.segid = getObject.cid
        delete getObject.txnid
        dispatch(editAssettype(getObject))
        setAssetType(0)
        setAssetTypeObj(null)
        setPrefix("")
        setSuffix("")
        setCodeLength(0)
        setIsEditVnum("no")
        setCodeNo("")
        resetForm()
        setDisplayStatus(false)
    }
    const vnumOnBlurFunction = (next_number, number_count) => {
        let numCount = number_count,
            nextNumber = next_number,
            nextNumCount = nextNumber ? nextNumber.toString().length : 0,
            needCountOfZero = parseInt(numCount, 10) - parseInt(nextNumCount, 10),
            zeros = "";
        if (parseInt(needCountOfZero, 10) > 0) {
            for (let i = 0; i < needCountOfZero; i++) {
                zeros += "0"
            }
        }
        setCodeNo(zeros + next_number)
    }

    useEffect(() => {
        console.log("assetType==========", assetType)
        if (options[assetType]) {
            setAssetTypeObj(options[assetType])
            setPrefix(options[assetType].number_prefix)
            setSuffix(options[assetType].number_suffix)
            vnumOnBlurFunction(options[assetType].next_number, options[assetType].number_count)
            setCodeLength(options[assetType].number_count)
            setIsEditVnum(options[assetType].is_editable)
        }
    }, [assetType])

    useEffect(() => {
        setGroupArray(assetGroupArray)
    }, [assetGroupArray])



    return (
        <Modal size='fullscreen' open={displayStatus} trigger={<Button type="button" size='mini' onClick={() => setDisplayStatus(true)}>New Asset</Button>} onClose={() => setDisplayStatus(false)}>
            <Modal.Header>Create Asset</Modal.Header>
            <Modal.Content>
                <Formik id="itemsForm" size="large" width={5}
                    initialValues={items}
                    validationSchema={itemsQuickSchema}
                    onSubmit={(values, { resetForm }) => saveItems(values, resetForm)}
                    render={({ handleSubmit, onChange, values, handleChange, errors }) => (
                        <Form as={FormikForm} size="small" className="popupCustomeForm" width={5} onSubmit={handleSubmit}>
                            <Form.Group widths={3}>
                                <AssetTypeSelect name="atype_id" label="Asset Type" isTxn='false' isSelection={true} index={0} fieldName={''} fromField='group' passFunct={setAssetType} focus={true} />
                                {assetTypeObj && assetTypeObj.numbering && assetTypeObj.numbering === "auto" ?
                                    <Field name="code" component={FormikVNumCodeComponent} prefix={prefix} suffix={suffix} maxlength={codeLength} userProps={{ setFieldManually: codeNo }} onBlurFunc={isEditVnum === "yes" ? vnumOnBlurFunction : null} isEditVnum={isEditVnum}></Field>
                                    :
                                    <Field name="code" component={FormikInputComponent} validate={(val) => duplicateCheckItemCode(val, values)}></Field>
                                }
                                <Field name="sl_no" label='Serial No.' component={FormikInputComponent}></Field>
                            </Form.Group>
                            <Form.Group widths={3}>
                                <Field name="name" label='Asset Description' component={FormikInputComponent}></Field>
                                <ItemgroupSelect name="group" isTxn='false' isSelection={true} onSelectOf={groupArray} index={0} fieldName={''} fromField='group' otherList={values.id} />
                                <UnitSelect name="uom_id" label="Unit" isTxn='false' isSelection={true} index={0} />
                            </Form.Group>
                            <Form.Group widths={3}>
                                <GstRateSelect name="gst_prcnt" isTxn={false} isSelection='true' isLabel={false} label='GST Rate' index={0} />
                                <CessRateSelect name="cess_prcnt" isTxn='false' isSelection={true} isLabel={false} label={'Cess Rate'}  index={0} />
                            </Form.Group>
                            <Form.Group widths={3}>
                                <Field name="warranty_end_dt" label='Warranty End Date' component={FormikDateComponent}></Field>
                                <Field name="capitalisation_date" component={FormikDateComponent}></Field>
                                <Field name="org_useful_life" label={'Life of Asset'} component={FormikAmountComponent}></Field>
                            </Form.Group>
                            <Form.Group widths={3}>
                                <LocationSelect name="location" isTxn='false' isSelection={true} />
                                <SubLocationSelect name="sub_loc_id" label="Sub-Location" isTxn='false' isSelection={true} depends={values.location} />
                            </Form.Group>
                            <Form.Group widths={3} className="setHeight marginRemove">
                                <EmployeeSelect name="emp_id" label="Employee" isTxn='false' isSelection={true} />
                                <DepartmentSelect name="dept_id" label="Department" isTxn='false' isSelection={true} />
                                <Field classField="relativeGet" className="paddingSet" name="description" component={FormikTextAreaComponent} userProps={{ maxLength: 300, displayCount: true, height: height }} label='Item Description' focus={true} />
                            </Form.Group>
                            <Form.Group widths={3}>
                                <Field name="insurance_status" label='Insurance Status' component={FormikInputComponent}></Field>
                                <Field name="insurance_num" label='Insurance No.' component={FormikInputComponent}></Field>
                                <Field name="insurance_exp_dt" label='Insurance Expiry Date' component={FormikDateComponent}></Field>
                            </Form.Group>
                            <Button type='button' onClick={handleSubmit} size="medium" color='blue' className="CustomeBTN"> {createSubmitButtonText} </Button>
                        </Form>
                    )}
                />
            </Modal.Content>
        </Modal>
    )
}

export default ItemQuickForm;