import axios from 'axios';
import { merge } from 'lodash';
import { all, call, put, select, takeEvery } from 'redux-saga/effects';
import userACL from '../../../store/access';
import { ASSET_URL } from '../../../store/path';
import { handlError, parseError } from '../../app/serverError';
import { deletedArea, fetchedArea, savedArea } from './actions';
import { getIsAreaFetched } from './selectors';

// const delay = (ms) => new Promise(res => setTimeout(res, ms))

//#region Saga for Create Area 

function* createArea() {
    yield takeEvery('CREATE_AREA',saveArea)
  }

function* saveArea(action){
  const { response, error } = yield call(saveAreaAPI, action.payload)
  console.log("Saga",action.payload)
  if (response) yield put (savedArea(action.payload))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function saveAreaAPI(data) {
    return axios.post(ASSET_URL+'/area/add', data, {crossDomain: true})
      .then(response => ({ response }))
      .catch(error => ({ error }))
  }

//#endregion


//#region Saga for Edited Area 

function* editArea() {
  yield takeEvery('EDIT_AREA',saveEditedArea)
}

function* saveEditedArea(action){
  const { response, error } = yield call(editAreaAPI, action.payload)  
  if (response) yield put (savedArea(action.payload))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function editAreaAPI(data) {
  let user_id = data._id
  return axios.post(ASSET_URL+'/area/update/'+ user_id, data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion

//#region Saga for Delete Area 

function* deleteArea() {
  yield takeEvery('DELETE_AREA',saveDeleteArea)
}

function* saveDeleteArea(action){
  const { response, error } = yield call(deleteAreaAPI, action.payload)
  if (response) yield put (deletedArea({id: action.payload.id, msg : response.data}))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function deleteAreaAPI(data) {
  let areaId = data.id
  return axios.post(ASSET_URL+'/area/delete/'+ areaId, data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for List Tasks 

function* fetchArea() {

  yield takeEvery('FETCH_AREA',requestArea)

}
  
function* requestArea(){

  const isAreaFetched = yield select(getIsAreaFetched);
  if(!isAreaFetched){
    let { data } = yield call(requestAreaAPI)
    yield put (fetchedArea(data.data))

  }

}

function requestAreaAPI() {
  let data = merge({}, userACL.atFetch())  
  return axios.post(ASSET_URL+'/area/list', data, {crossDomain: true})
}

//#endregion


const sagaErrorMessage = (error, action) => {
    console.group("Saga Error:"+action.type)
    console.log(error)
    console.groupEnd()
}

export default function* areaSaga() {
    yield all([
        createArea(),
        deleteArea(),
        fetchArea(),
        editArea()
    ])
  }  