import { combineReducers } from 'redux';
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr';

// Define a itemss schema
const itemsSchema = new schema.Entity('items', {}, { idAttribute: 'id' });
const itemsListSchema = [itemsSchema];

const depSchema = new schema.Entity('depreciation', {}, { idAttribute: 'id' });
const depListSchema = [depSchema];

const defaultItemsParams = {
  itemsFetched: false,
  isFetching: false,
  isSearching: false,
  searchFetched: false,
  createTitle: 'Create Asset For Prior Period',
  createSubmitButtonText: 'Submit',
  editTitle: 'Edit Asset',
  editSubmitButtonText: 'Submit'
}


function byDepListId(state = {}, action) {
  if (action.type === 'SAVED_DEPRECIATION_FOR_ITEM') {
    const depID = action.payload.id;
    return {
      ...state,
      [depID]: action.payload
    }
  }
  if (action.type === 'FETCHED_DEPRECIATION_FOR_ITEM') {
    const normalizedDep = normalize(action.payload, depListSchema);
    return merge({}, normalizedDep.entities.depreciation)
  }
  if (action.type === 'DELETED_DEPRECIATION_FOR_ITEM') {
    if (action.payload.msg === "Success") {
      const depID = action.payload.id;
      let finalState = { ...state }
      delete finalState[depID]
      return finalState
    } else {
      return state
    }
  } else {
    return state
  }
}

function byId(state = {}, action) {
  if (action.type === 'SAVED_ITEMS') {
    const itemsID = action.payload.id;
    return {
      ...state,
      [itemsID]: action.payload
    }
  }
  if (action.type === "FETCHED_ITEMS_FROM_VOUCHER") {
    const normalizedItems = normalize(action.payload, itemsListSchema);
    return merge({}, state, normalizedItems.entities.items)
  }

  if (action.type === 'FETCHED_ITEM') {
    const normalizedItems = normalize(action.payload, itemsListSchema);
    return merge({}, state, normalizedItems.entities.items)
  }

  if (action.type === "STORE_SEARCH_ITEMS_RESULT_SALE") {
    const normalizedItems = normalize(action.payload, itemsListSchema);
    return merge({}, state, normalizedItems.entities.items)
  }

  if (action.type === "STORE_SEARCH_ITEMS_RESULT") {
    const normalizedItems = normalize(action.payload, itemsListSchema);
    return merge({}, state, normalizedItems.entities.items)
  }
  if (action.type === 'DELETED_ITEMS') {
    console.log("action.payload======",action.payload)
    if (action.payload.msg.type === "success") {
      const itemsID = action.payload.id;
      let finalState = { ...state }
      delete finalState[itemsID]
      return finalState
    } else {
      return state
    }
  }
  else {
    return state
  }
}


function searchResults(state = {}, action) {
  if (action.type === 'STORE_SEARCH_ITEMS_RESULT') {
    const normalizedItems = normalize(action.payload, itemsListSchema)
    return merge({}, normalizedItems.entities.items)
  } else {
    return state
  }
}
function searchResultsSale(state = {}, action) {
  if (action.type === 'STORE_SEARCH_ITEMS_RESULT_SALE') {
    const normalizedItems = normalize(action.payload, itemsListSchema)
    return merge({}, normalizedItems.entities.items)
  } else {
    return state
  }
}

function byListId(state = {}, action) {
  if (action.type === 'SAVED_ITEMS') {
    const itemsID = action.payload.id;
    return {
      ...state,
      [itemsID]: action.payload
    }
  }
  if (action.type === 'FETCHED_ITEMS') {
    const normalizedItems = normalize(action.payload, itemsListSchema);
    return merge({}, normalizedItems.entities.items)
  }
  if (action.type === 'FETCHED_ITEM') {
    const normalizedItems = normalize(action.payload, itemsListSchema);
    return merge({}, normalizedItems.entities.items)
  }
  if (action.type === 'DELETED_ITEMS') {
    if (action.payload.msg.type === "success") {
      const itemsID = action.payload.id;
      let finalState = { ...state }
      delete finalState[itemsID]
      return finalState
    } else {
      return state
    }
  } else {
    return state
  }
}



function filterResults(state = {}, action) {
  if (action.type === 'FILTER_ITEMS') {
    const col = action.payload.col;
    return {
      ...state,
      [col]: action.payload
    }
  } else {
    return state
  }
}


function params(state = defaultItemsParams, action) {
  if (action.type === 'FETCH_ITEM') {
    return {
      ...state,
      itemsFetched: false,
      isFetching: true
    }
  }
  if (action.type === 'FETCHED_ITEM') {
    return {
      ...state,
      itemsFetched: true,
      isFetching: false
    }
  }
  if (action.type === 'FETCH_ITEMS') {
    return {
      ...state,
      itemsFetched: false,
      isFetching: true
    }
  }
  if (action.type === 'FETCHED_ITEMS') {
    return {
      ...state,
      itemsFetched: true,
      isFetching: false
    }
  }
  if (action.type === 'SEARCH_ITEMS') {
    return {
      ...state,
      searchFetched: false,
      isSearching: true
    }
  }
  if (action.type === 'SEARCH_ITEMS_SALE') {
    return {
      ...state,
      searchFetched: false,
      isSearching: true
    }
  }
  if (action.type === 'STORE_SEARCH_ITEMS_RESULT_SALE') {
    return {
      ...state,
      searchFetched: true,
      itemsFetched: true,
      isSearching: false
    }
  }
  if (action.type === 'STORE_SEARCH_ITEMS_RESULT') {
    return {
      ...state,
      searchFetched: true,
      itemsFetched: true,
      isSearching: false
    }
  } else {
    return state
  }
}


function notifications(state = {}, action) {
  if (action.type === 'SAVED_ITEMS') {
    return {
      ...state,
      [action.payload.id]: {
        ...state[action.payload.id],
        save: {
          status: action.txn === "success" ? 'success' : 'error',
          msg: action.txn
        }
      }
    }
  }
  if (action.type === 'DELETED_ITEMS') {
    return {
      ...state,
      [action.payload.id]: {
        ...state[action.payload.id],
        delete: {
          status: action.payload.msg.type === ("Success" || "success") ? 'success' : 'error',
          msg: action.payload.msg.msg
        }
      }
    }
  }
  if (action.type === 'NOTIFICATION_DONE_ITEMS') {
    const { id, type } = action.payload
    // Remove the 'id' element from state
    const { [id]: idValue, ...restOfState } = state;
    // Remove the 'type' from the 'id' element
    const { [type]: removedValue, ...restOfId } = idValue;
    // Merge back together
    const finalState = { ...restOfState, [id]: restOfId };
    return finalState

  } else {
    return state
  }
}

const items = combineReducers({
  byDepListId,
  byId,
  notifications,
  searchResults,
  searchResultsSale,
  byListId,
  filterResults,
  params
});

export default items;

