import { connectRouter } from 'connected-react-router'
import { combineReducers } from 'redux'
import { txns } from './store/txnMiddleware'
import year from './modules/year/data/reducers'
import assettype from './modules/assettype/data/reducers'
import itemgroup from './modules/itemgroup/data/reducers'
import unitmaster from './modules/unitmaster/data/reducers'
import vtype from './modules/vtype/data/reducers'
import employee from './modules/employee/data/reducers'
import department from './modules/department/data/reducers'
import location from './modules/location/data/reducers'
import voucher from './modules/voucher/data/reducers'
import items from './modules/items/data/reducers'
import ledgers from './modules/ledger_search/data/reducers'
import vdetails from './modules/vdetails/data/reducers'
import area from './modules/area/data/reducers'
import depreciation from './modules/depreciation/data/reducers'
import dreport from './modules/dreport/data/reducers'
import file from './modules/importCreate/data/reducers'


const rootReducer = (history) => combineReducers({
    router: connectRouter(history),
    txns,
    year,
    assettype,
    itemgroup,
    unitmaster,
    vtype,
    employee,
    department,
    location,
    voucher,
    items,
    ledgers,
    vdetails,
    area,
    depreciation,
    dreport,
    file
})

export default rootReducer