import { startsWith, cloneDeep, at } from 'lodash';
import { createSelector } from 'reselect';
import userACL from '../../../store/access';
import { assettype, assetTypeNumbering } from './model'




export const getIsFetchingAssettype = (state, props) => state.assettype.params.isFetching;
export const getIsAssettypeFetched = (state, props) => state.assettype.params.assettypeFetched;
export const getSearchSegListResults = (state, props) => state.assettype.storeSearchedSegList
export const getAssettypeList = (state, prop) => state.assettype.byListId
export const getAssettypeObj = (state, id) => state.assettype.byListId[id]

export const getNotification = (state, id) => state.assettype.notifications[id]

const orgSelected = (state) => state.org.params.selected;

export const getAssettype = (state, props) => {
    if (props.match.path === '/assettype/create') {
        return assettype()
    }
    if (props.match.path === '/assettype/edit/:id') {
        let _id = props.match.params.id
        let obj = cloneDeep(state.assettype.byListId[_id]);
        if(obj.numDetails.length === 0){
            obj.numDetails = [assetTypeNumbering()]
        }
        // console.log(obj, '------------------edit-obj2')
        return obj
    }

}


export const getAssetTypeNumbering = (state, assetId) => {
    let assetNum = at(state.assettype.ById, assetId)
    if (assetNum[0] === undefined)
        assetNum = [assetTypeNumbering()]
    return assetNum
}


export const getAssettypeParams = (state, props) => {
    const params = {}

    if (startsWith(state.router.location.pathname, '/assettype/create')) {
        params.title = state.assettype.params.createTitle
        params.submitButtonText = state.assettype.params.createSubmitButtonText
    };

    if (startsWith(state.router.location.pathname, '/assettype/edit/')) {
        params.title = state.assettype.params.editTitle
        params.submitButtonText = state.assettype.params.editSubmitButtonText
    };

    return params
}



export const selectAssetType = (state, props) => {
    const assettype = state.assettype.byListId
    const keys = Object.keys(assettype)
    const obj = keys.map((key) => {
        return { key: key, value: key, text: assettype[key].tname }
    });
    return obj

}

