export const getFilteredAssetType = createSelector(
    getAssettypeList,
    (assettype) => {
        // console.log(assettype, '----asetType')
        let filteredAssetType = Object.keys(assettype).map(function (key) {        
            return assettype[key]
        })
        // console.log(filteredAssetType, '----asetType')
        return filteredAssetType
    }
)


    export const selectSegment = (state, props) => {
        const segment = state.assettype.storeSearchedSegList   
        const keys = Object.keys(segment)
        const obj = keys.map((key) => {
            segment[key].key = (segment[key].segid).toString()
            segment[key].value = (segment[key].segid).toString()
            segment[key].text = segment[key].segment
            // console.log(segment[key])
            return segment[key]
        });
        // console.log(obj,'==obj')
        return obj
    
    }
