import React from 'react';
import { Field } from 'formik';
import { FormikSelectComponent } from '../../../utilities/formUtils';

const NatureSelect = ({ name, isTxn, label, isLabel, isSelection, placeholder, getSelectedDDval, computeValue, index, fieldName, formField, otherList, className }, props) => {

    const getSelectedValue = (values) => {
        // console.log("Type Selected", values, name)
    }
    const opt = [
        { key: "purchase", value: "purchase", text: "Purchase" },
        { key: "sale", value: "sale", text: "Sale" },
    ]

    return (
        <Field name={name} isTxn={isTxn} label={label} isLabel={isLabel} placeholder={placeholder} isSelection={isSelection} classNameGet={className} component={FormikSelectComponent} computeValue={computeValue} index={index} fieldName={fieldName} formField={formField} otherList={otherList} userProps={{ options: opt, getValue: getSelectedDDval ? getSelectedDDval : getSelectedValue }} />
    )
}

export default NatureSelect;
