import * as type from './types'


export function createItems(items) {
    return {
        type : type.CREATE_ITEMS,
        payload : items,
        txn : 'initiated',
    };
}

export function savedItems(items) {
    return {
        type : type.SAVED_ITEMS,
        payload : items,
        txn : 'success',
        diff : true
    };
}

export function editItems(items, group_name) {
    console.log("call")
    return {
        type : type.EDIT_ITEMS,
        payload : items,
        payload1: group_name,
        txn : 'initiated',
    };
}

export function fetchItem(param) {
    return {
        type : type.FETCH_ITEM,
        payload:param,
        txn:'initiated'
    };
}

export function fetchedItem(items) {
    return {
        type : type.FETCHED_ITEM,
        payload : items
    };
}

export function fetchItems(param) {
    return {
        type : type.FETCH_ITEMS,
        payload: param,
        txn:'initiated'
    };
}

export function fetchedItems(items) {
    return {
        type : type.FETCHED_ITEMS,
        payload : items
    };
}

export function fetchDepreciationForItem(param) {
    return {
        type : type.FETCH_DEPRECIATION_FOR_ITEM,
        payload: param
    };
}

export function fetchedDepreciationForItem(items) {
    console.log("Call 1")
    return {
        type : type.FETCHED_DEPRECIATION_FOR_ITEM,
        payload : items
    };
}

export function savedDepreciationForItem(items) {
    console.log("Call 2")
    return {
        type : type.SAVED_DEPRECIATION_FOR_ITEM,
        payload : items
    };
}
export function deletedDepreciationForItem(items) {
    console.log("Call 3")
    return {
        type : type.DELETED_DEPRECIATION_FOR_ITEM,
        payload : items
    };
}

export function fetchedItemsFromVoucher(items) {
    return {
        type : type.FETCHED_ITEMS_FROM_VOUCHER,
        payload : items
    };
}


export function filterItems(options) {
    return {
        type : type.FILTER_ITEMS,
        payload: options
    };
}


export function searchItemsByName(items) {
    return {
        type : type.SEARCH_ITEMS,
        payload : {value: items, 'type' : 'searchName'} 
    };
}


export function searchItemsByCode(items) {
    return {
        type : type.SEARCH_ITEMS,
        payload : {value: items, 'type' : 'searchCode'} 
    };
}

export function searchItemsByCodeSale(items) {
    return {
        type : type.SEARCH_ITEMS_SALE,
        payload : {str: items} 
    };
}


export function storeSearchItemsResult(items) {
    return {
        type : type.STORE_SEARCH_ITEMS_RESULT,
        payload : items
    };
}

export function storeSearchItemsResultSale(items) {
    return {
        type : type.STORE_SEARCH_ITEMS_RESULT_SALE,
        payload : items
    };
}

export function deleteItems(item) {
    return {
        type : type.DELETE_ITEMS,
        payload: item
    };
}

export function deletedItems(msg) {
    return {
        type : type.DELETED_ITEMS,
        payload : msg
    };
}

export function setNotifyDone(props) {
    return {
        type : type.NOTIFICATION_DONE_ITEMS,
        payload: props
    };
}