import { createSelector } from 'reselect'
import { startsWith, cloneDeep } from 'lodash';
import { area } from './model'


export const getIsFetchingArea = (state, props) => state.area.params.isFetching;
export const getIsAreaFetched = (state, props) => state.area.params.areaFetched;
export const getAreaList = (state, prop) => state.area.byId

export const getNotification = (state, id) => state.area.notifications[id]

const orgSelected = (state) => state.org.params.selected;

export const getArea = (state, props) => {
    if(props.match.path==='/area/create'){
        return area()
    }
    if(props.match.path==='/area/edit/:id'){
        let _id = props.match.params.id
        let obj = cloneDeep(state.area.byId[_id]);
        return obj        
    }
}

export const getAreaParams = (state, props) => {
    const params =  {}

    if(startsWith(state.router.location.pathname,'/area/create')){ 
        params.title = state.area.params.createTitle
        params.submitButtonText = state.area.params.createSubmitButtonText        
    };

    if(startsWith(state.router.location.pathname,'/area/edit/')){ 
        params.title = state.area.params.editTitle
        params.submitButtonText = state.area.params.editSubmitButtonText        
    };

    return params
}

export const selectArea = createSelector(
    getAreaList,
    area => { 
        const keys = Object.keys(area)
        // const obj = keys.map( (key) => { return { value : key, label : area[key].name }})
        const obj = keys.map( (key) => { return { key : key,  value : key, text : area[key].name }})
        return obj
    })
