import { call, takeEvery, all, put, select } from "redux-saga/effects";
import { getIsUnitMasterFetched } from "./selectors";
import {savedUnitMaster, fetchedUnitMaster, deletedUnitMaster} from "./actions";
import axios from "axios";
import { handlError, parseError } from "../../app/serverError";
import { ASSET_URL } from "../../../store/path";
import userACL from "../../../store/access";
import { merge } from "lodash";


//#region saga for Create UnitMaster

function* createUnitMaster() {
  yield takeEvery("CREATE_UNIT_MASTER", saveUnitMaster);
}

function* saveUnitMaster(action) {
  const { response, error } = yield call(saveUnitMasterAPI, action.payload);
  console.log("saga", action.payload);
  if (response){ 
    if(response.data.type!=="error"){yield put(savedUnitMaster(action.payload))};
  }else {
    yield put(handlError(action, parseError(error)));
    sagaErrorMessage(error, action);
  }
}


function saveUnitMasterAPI(data) {
  // userACL.atCreate(data);
  // data.segid = data.cid;
  // data.rlb_box_id=data.boxId
  // delete data.txnid
  // delete data.boxId
  return axios
    .post(ASSET_URL + "/unit/add", data)
    .then((response) => ({ response }))
    .catch((error) => ({ error }));
}

//#endregion

//#region Saga for Edited unitmaster

function* editUnitMaster() {
  yield takeEvery("EDIT_UNIT_MASTER", saveEditedUnitMaster);
}

function* saveEditedUnitMaster(action) {
  const { response, error } = yield call(editUnitMasterAPI, action.payload);
  console.log(action.payload, '=================save')
  if (response){ 
    if(response.data.type!=="error"){
    yield put(savedUnitMaster(action.payload))}
  }
  else {
    yield put(handlError(action, parseError(error.action)));
    sagaErrorMessage(error.action);
  }
}

function editUnitMasterAPI(data) {
  let unit_id = data.id;
  // data.rlb_box_id=data.boxId
  // delete data.boxId
  return axios.post(ASSET_URL + "/unit/update", data, { crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion

//#region Saga for Delete UnitMaster

function* deleteUnitMaster() {
  yield takeEvery("DELETE_UNIT_MASTER", saveDeletedUnitMaster);
}

function* saveDeletedUnitMaster(action) {
  const { response, error } = yield call(deleteUnitMasterAPI, action.payload);
  if (response)
    yield put(deletedUnitMaster({ id: action.payload.id, msg: response.data }));
  else {
    yield put(handlError(action, parseError(error)));
    sagaErrorMessage(error, action);
  }
}

function deleteUnitMasterAPI(data) {
  data.rlb_box_id=data.boxId
  delete data.boxId
  let unitMasterId = data.id;
  return axios
    .post(ASSET_URL + "/unit/delete/" + unitMasterId, data, {
      crossDomain: true,
    })
    .then((response) => ({ response }))
    .catch((error) => ({ error }));
}

//#endregion

//#region Saga for List Tasks

function* fetchUnitMaster() {
  yield takeEvery("FETCH_UNIT_MASTER", requestUnitMaster);
}

function* requestUnitMaster() {
  const isUnitMasterFetched = yield select(getIsUnitMasterFetched);
  if (!isUnitMasterFetched) {
    let { data } = yield call(requestUnitMasterAPI);
    console.log(data.data, '=================data1')
    yield put(fetchedUnitMaster(data.data));
  }
}

function requestUnitMasterAPI() {
  let data = merge({}, userACL.atFetch());
  data.rlb_box_id=data.boxId
  delete data.boxId
  return axios.post(ASSET_URL + "/unit/list", data, { crossDomain: true, });
}

//#endregion

const sagaErrorMessage = (error, action) => {
  console.group("Saga Error:" + action.type);
  console.log(error);
  console.groupEnd();
};

export default function* unitMasterSaga() {
  yield all([
    createUnitMaster(),
    deleteUnitMaster(),
    fetchUnitMaster(),
    editUnitMaster()
  ])
}