import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import * as select from '../data/selectors'
import { Field } from 'formik'
import { FormikSelectComponent } from '../../../utilities/formUtils'


const LocationSelect = ({ name, isTxn, isLabel, label, isSelection, isMandatory, setState, getSubLoc, getValue: callback }, props) => {

    const locationFetched = useSelector(state => select.getIsLocationDepsOnItemListFetched(state, props))
    const options = useSelector(state => select.selectLocation(state, props))
    console.log("locationFetched----------> ", locationFetched)

    console.log('options', options)

    const getValue = (values, val, setFieldValue) => {
        let getSub = options.filter((item) => {
            console.log("Location Selected======", item)
            return item.value === values
        })
        console.log("getSub==========",getSub)
        if(Object.keys(getSub).length > 0){
            if(getSubLoc){
                getSubLoc(getSub[0].subLocation, setFieldValue, values)
            }
        }
        console.log("getSubLoc====> ", getSub)
    }


    if (locationFetched && options.length > 0) {
        return <p>Loading...</p>
    } else {
        return (
            <Field name={name} isLabel={isLabel} isTxn={isTxn} label={label} isMandatory={isMandatory} isSelection={isSelection} component={FormikSelectComponent} userProps={{ options, getValue }}> </Field>
        )
    }

}


export default LocationSelect;
