import {
  getItems,
  getItemsParams,
  getItemDepreciation,
  getNotification,
} from "../data/selectors";
import {
  getAssettypeObj,
  getAssettypeList,
} from "../../assettype/data/selectors";
import { createItems, editItems, setNotifyDone } from "../data/actions";
import { editAssettype, fetchAssettype } from "../../assettype/data/actions";
import { itemsSchema, duplicateCheckItemCode } from "../data/model";
import { Container, Header, Form, Button } from "semantic-ui-react";
import { Formik, Field, Form as FormikForm, FieldArray } from "formik";
import GstRateSelect from "./gstRateSelect";
import CessRateSelect from "./cessRateSelect";
import {
  FormikInputComponent,
  FormikDateComponent,
  FormikAmountComponent,
  FormikTextAreaComponent,
  FormikVNumCodeComponent,
  FormikDisplayLabelComponent,
  FormikDisableInputComponent,
  FormikDisableDateComponent,
  FormikDisableAmountComponent
} from "../../../utilities/formUtils";
import ItemgroupSelect from "../../itemgroup/components/itemgroupSelect";
import AssetTypeSelect from "../../assettype/components/asetTypeSelect";
import LocationSelect from "../../location/components/locationSelect";
import EmployeeSelect from "../../employee/components/employeeSelect";
import DepartmentSelect from "../../department/components/departmentSelect";
import UnitSelect from "../../unitmaster/components/unitMasterSelect";
import userACL from "../../../store/access";
import Notification from "../../../utilities/notificationUtils";
import { cloneDeep } from "lodash";
import SubLocationSelect from "../../location/components/subLocationSelect";
