import { parse } from "uuid";

// import UserDropdown from '../../users/components/userDropdown'

const ItemsForm = (props) => {
  const items = useSelector((state) => getItems(state, props));
  const itemDepreciation = useSelector((state) =>
    getItemDepreciation(state, items ? items.id : 0)
  );
  const asetType = useSelector((state) =>
    getAssettypeObj(state, items ? items.atype_id : 0)
  );
  const asetTypeList = useSelector((state) => getAssettypeList(state));
  const params = useSelector((state) => getItemsParams(state, props));
  const options = useSelector((state) => getAssettypeList(state, props));

  console.log("options========",options)
  console.log("asset========",asetTypeList)
  const dispatch = useDispatch();

  const [savedItemId, setSavedItemId] = useState(false);
  const [subLocationList, setSubLocationList] = useState([]);
  const [assetType, setAssetType] = useState(items ? items.atype_id : null);
  const [assetTypeObj, setAssetTypeObj] = useState(
    options ? (assetType ? options[assetType] : null) : null
  );
  const [prefix, setPrefix] = useState(
    options
      ? assetType
        ? options[assetType]
          ? options[assetType].number_prefix
          : ""
        : ""
      : ""
  );
  const [suffix, setSuffix] = useState(
    options
      ? assetType
        ? options[assetType]
          ? options[assetType].number_suffix
          : ""
        : ""
      : ""
  );
  const [codeNo, setCodeNo] = useState(
    options
      ? assetType
        ? options[assetType]
          ? options[assetType].next_number
          : ""
        : ""
      : ""
  );

  console.log(codeNo,"===========codeNo")
  const [codeLength, setCodeLength] = useState(
    options
      ? assetType
        ? options[assetType]
          ? options[assetType].number_count
          : ""
        : ""
      : ""
  );
  const [isEditVnum, setIsEditVnum] = useState(
    options
      ? assetType
        ? options[assetType]
          ? options[assetType].is_editable
          : "no"
        : "no"
      : "no"
  );
  let height = typeof InstallTrigger !== "undefined" ? 5 : 6;

  const saveItems = (values, resetForm) => {
    if (props.match.path === "/items/create") {
      userACL.atCreate(values);
      values.op_dep_dt = values.op_dep_dt === "" ? null : values.op_dep_dt;
      values.invc_dt = values.invc_dt === "" ? null : values.invc_dt;
      values.warranty_end_dt =
        values.warranty_end_dt === "" ? null : values.warranty_end_dt;
      values.insurance_exp_dt =
        values.insurance_exp_dt === "" ? null : values.insurance_exp_dt;
      values.op_depreciation_val =
        values.op_depreciation_val === "" ? null : values.op_depreciation_val;
      values.gst_prcnt = values.gst_prcnt === "" ? null : values.gst_prcnt;
      values.cess_prcnt = values.cess_prcnt === "" ? null : values.cess_prcnt;
      values.shift_count =
        values.shift_count === "" ? null : values.shift_count;
      values.residual_value =
        values.residual_value === "" ? null : values.residual_value;
      values.group_name = values.group_name === "" ? null : values.group_name;
      // let group_name= values.group_name
      // delete values.group_name
      // dispatch(createItems(values,group_name))
      dispatch(createItems(values));
      let getObject = cloneDeep(asetTypeList[assetType]),
        newCodeNo = parseInt(codeNo, 10) + 1;
      getObject.next_number = newCodeNo;
      userACL.atUpdate(getObject);
      // getObject.segid = getObject.cid
      delete getObject.txnid;
      // dispatch(editAssettype(getObject))
      setSavedItemId(values.id);
      setAssetType(0);
      setAssetTypeObj(null);
      setPrefix("");
      setSuffix("");
      setCodeLength(0);
      setIsEditVnum("no");
      setCodeNo("");
      resetForm();
    }
    if (props.match.path === "/items/edit/:id") {
      userACL.atUpdate(values);
      values.op_dep_dt = values.op_dep_dt === "" ? null : values.op_dep_dt;
      values.invc_dt = values.invc_dt === "" ? null : values.invc_dt;
      values.warranty_end_dt =
        values.warranty_end_dt === "" ? null : values.warranty_end_dt;
      values.insurance_exp_dt =
        values.insurance_exp_dt === "" ? null : values.insurance_exp_dt;
      values.op_depreciation_val =
        values.op_depreciation_val === "" ? null : values.op_depreciation_val;
      values.gst_prcnt = values.gst_prcnt === "" ? null : values.gst_prcnt;
      values.cess_prcnt = values.cess_prcnt === "" ? null : values.cess_prcnt;
      // values.group_name=values.group_name=== "" ? null :values.group_name
      // values.shift_count= values.shift_count === "" ? null : values.shift_count
      values.residual_value =
        values.residual_value === "" ? null : values.residual_value;
      values.rlb_box_id = values.boxId;
      delete values.boxId;
      let group_name = values.group_name;
      delete values.group_name;
      dispatch(editItems(values, group_name));
      // dispatch(editItems(values))
      setSavedItemId(values.id);
      setTimeout(function () {
        props.history.push(`/items/list`);
      }, 1000);
    }
  };

  const vnumOnBlurFunction = (next_number, number_count) => {
    let numCount = number_count,
      nextNumber = next_number,
      nextNumCount = nextNumber ? nextNumber.toString().length : 0,
      needCountOfZero = parseInt(numCount, 10) - parseInt(nextNumCount, 10),
      zeros = "";
    if (parseInt(needCountOfZero, 10) > 0) {
      for (let i = 0; i < needCountOfZero; i++) {
        zeros += "0";
      }
    }
    setCodeNo(zeros + next_number);
    console.log("codeNo=============", codeNo)
  };

  useEffect(() => {
    // if (!assetFetched)
    dispatch(fetchAssettype());
  }, []);

  useEffect(() => {
    if (options[assetType]) {
      setAssetTypeObj(options[assetType]);
      setPrefix(options[assetType].number_prefix);
      setSuffix(options[assetType].number_suffix);
      vnumOnBlurFunction(
        options[assetType].next_number,
        options[assetType].number_count
      );
      setCodeLength(options[assetType].number_count);
      setIsEditVnum(options[assetType].is_editable);
    }
  }, [assetType]);

  const getSubLocation = (subLoc, setFieldValue, values) => {
    setSubLocationList(subLoc);
  };

  return (
    <Container>
      <Header as="h1">{params.title}</Header>
      <Formik
        id="itemsForm"
        size="large"
        width={5}
        initialValues={items}
        validationSchema={itemsSchema}
        onSubmit={(values, { resetForm }) => saveItems(values, resetForm)}
        render={({ handleSubmit, onChange, values, handleChange, errors }) => (
          <Form
            as={FormikForm}
            size="small"
            className="CustomeForm"
            width={5}
            onSubmit={handleSubmit}
          >
            <Form.Group widths={4}>
              {props.match.path === "/items/create" ? (
                <AssetTypeSelect
                  name="atype_id"
                  label="Asset Type"
                  isTxn="false"
                  isSelection={true}
                  index={0}
                  fieldName={""}
                  fromField="group"
                  passFunct={setAssetType}
                  focus={true}
                />
              ) : (
                <Field
                  component={FormikDisplayLabelComponent}
                  isLabel={false}
                  label="AssetType"
                  text={asetType ? asetType.tname : ""}
                />
              )}
              {console.log(asetType)}
              {props.match.path === "/items/create" ? (
                assetTypeObj &&
                assetTypeObj.numbering &&
                assetTypeObj.num3bering === "auto" ? (
                  <Field
                    name="code"
                    component={FormikVNumCodeComponent}
                    prefix={prefix}
                    suffix={suffix}
                    maxlength={codeLength}
                    userProps={{ setFieldManually: codeNo }}
                    onBlurFunc={
                      isEditVnum === "yes" ? vnumOnBlurFunction : null
                    }
                    isEditVnum={isEditVnum}
                  ></Field>
                ) : (
                  // validate={(val) => duplicateCheckItemCode(val, values)}
                  <Field
                    name="code"
                    component={FormikInputComponent}
                    validate={(val) => duplicateCheckItemCode(val, values)}
                  ></Field>
                )
              ) : (
                <Field
                  component={FormikDisplayLabelComponent}
                  isLabel={false}
                  label="Code"
                  text={items.code}
                />
              )}
              <Field
                name="sl_no"
                label="Serial No."
                component={FormikInputComponent}
              ></Field>
            </Form.Group>

            {/* <Form.Group widths={3} className="setHeight">
                            <EmployeeSelect name="emp_id" label="Employee" isTxn='false' isSelection={true}/>
                            <DepartmentSelect name="dept_id" label="Department" isTxn='false' isSelection={true}/>
                            <Field classField="relativeGet" className="paddingSet" name="description" component={FormikTextAreaComponent} userProps={{maxLength: 300, displayCount: true, height: height}} label='Item Description' focus={true}/>
                        </Form.Group> */}
            {/* <Form.Group widths={3}> 
                            <Field name="capitalisation_date" label="Put to use" component={FormikDateComponent}></Field>
                            <LocationSelect name="location" isTxn='false' isSelection={true} getSubLoc={getSubLocation}/>
                            <SubLocationSelect name="sub_loc_id" label="Sub-Location" isTxn='false' isSelection={true} optionsGet={subLocationList}/>
                        </Form.Group> 
                        <Form.Group widths={3}>
                            <Field name="op_depreciation_val" label="Accrued Depreciation Value" component={FormikAmountComponent}></Field>
                            <Field name="op_dep_dt" label="Accrued Depreciation Date" component={FormikDateComponent}></Field>
                        </Form.Group>  */}
            {/* <Form.Group widths={3} className="setHeight">
                            <EmployeeSelect name="emp_id" label="Employee" isTxn='false' isSelection={true}/>
                            <DepartmentSelect name="dept_id" label="Department" isTxn='false' isSelection={true}/>
                            <Field classField="relativeGet" className="paddingSet" name="description" component={FormikTextAreaComponent} userProps={{maxLength: 300, displayCount: true, height: height}} label='Item Description' focus={true}/>
                        </Form.Group>  */}
            <Form.Group widths={4} className="setHeight">
              
              <ItemgroupSelect
                name="group"
                isTxn="false"
                isSelection={true}
                index={0}
                fieldName={""}
                fromField="group"
                otherList={values.id}
              />
              <Field
                name="name"
                label="Asset Description"
                component={FormikInputComponent}
              ></Field>
              <Field
                name="slab"
                label="Slab"
                component={FormikInputComponent}
              ></Field>
              <Field
                classField="relativeGet"
                className="paddingSet"
                name="description"
                component={FormikTextAreaComponent}
                userProps={{
                  maxLength: 300,
                  displayCount: true,
                  height: height,
                }}
                label="Item Description"
                focus={true}
              />
              {console.log("qwet==========", values.id)}
            </Form.Group>
            <Form.Group widths={3}>
              <Field
                name="purchase_date"
                component={FormikDateComponent}
              ></Field>
              <Field
                name="capitalisation_date"
                label="Put to use"
                component={FormikDateComponent}
              ></Field>
              <Field name="party_name" component={props.match.path === "/items/create"?FormikDisableInputComponent:FormikInputComponent}></Field>
              <Field
                name="invc_num"
                label="Invoice No."
                component={FormikInputComponent}
              ></Field>
            </Form.Group>
            {/* <Form.Group widths={3}>
              
            </Form.Group> */}
            <Form.Group widths={3}>
              <Field
                name="invc_dt"
                label="Invoice Date"
                component={FormikDateComponent}
              ></Field>
              
              <Field  name="org_pur_val" label={'Original Purchase Price'} component={FormikAmountComponent}  index={0} fieldName={'original'}></Field>
              <Field
                name="residual_value"
                label="Residual Value"
                component={FormikInputComponent}
              />
              <Field
                name="op_depreciation_val"
                label="Accrued Depreciation Value"
                component={FormikAmountComponent}
              ></Field>
              
            </Form.Group>
            {/* <Form.Group widths={3}></Form.Group> */}


            {/* <Form.Group widths={3}>
                            <Field classField="relativeTB" name="org_pur_val" label={'Original Purchase Price'} component={FormikAmountComponent}  index={0} fieldName={'original'}></Field>
                            <Field name="org_useful_life" label={'Life of Asset/Remaining Life of Asset (For Assets Purchased before 31.03.2014)'} component={FormikAmountComponent}></Field>
            </Form.Group> */}

            <Form.Group widths={3}>
              
              <Field
                name="op_dep_dt"
                label="Accrued Depreciation Date"
                component={props.match.path === "/items/create"?FormikDisableDateComponent:FormikDateComponent}
              ></Field>
              <Field
                name="initial_value"
                label={"Depreciable Value"}
                component={FormikAmountComponent}
                index={0}
                fieldName={"initial"}
              ></Field>
              <Field
                name="estimated_use_life"
                label="Estimated useful life as per schedule II"
                component={FormikInputComponent}
              />
              <ShiftOpt
                name="shift_count"
                isTxn="false"
                isSelection={true}
                isLabel={false}
                label={"Shift"}
                path={props.match.path}
                index={0}
              />
              {/* <ShiftOpt
                name="shift_count"
                path={props.match.path}
                isTxn="false"
                isSelection={true}
                isLabel={false}
                label="shift_count"
                index={0}
              /> */}
            </Form.Group>
            <Form.Group widths={3}>
              <Field
                classField=""
                name="asset_qty"
                label="Quantity"
                component={props.match.path === "/items/create"?FormikDisableAmountComponent:FormikAmountComponent}
              ></Field>
              <UnitSelect
                name="uom_id"
                label="Unit"
                isTxn="false"
                isSelection={true}
                index={0}
              />
              <GstRateSelect
                name="gst_prcnt"
                path={props.match.path}
                isTxn="false"
                isSelection={true}
                isLabel={false}
                label="GST Rate"
                index={0}
              />
              <CessRateSelect
                name="cess_prcnt"
                isTxn="false"
                path={props.match.path}
                isSelection={true}
                isLabel={false}
                label={"Cess Rate"}
                index={0}
              />
            </Form.Group>
            <Form.Group widths={3} className="setHeight">
              <LocationSelect
                name="location"
                isTxn="false"
                isSelection={true}
                getSubLoc={getSubLocation}
              />
              <SubLocationSelect
                name="sub_loc_id"
                label="Sub-Location"
                isTxn="false"
                isSelection={true}
                optionsGet={subLocationList}
              />
              <DepartmentSelect
                name="dept_id"
                label="Department"
                isTxn="false"
                isSelection={true}
              />
              <EmployeeSelect
                name="emp_id"
                label="Employee"
                isTxn="false"
                isSelection={true}
              />
            </Form.Group>

            <Form.Group widths={3}>
              <Field
                name="insurance_num"
                label="Insurance No."
                component={FormikInputComponent}
              ></Field>
              <Field
                name="insurance_exp_dt"
                label="Insurance Expiry Date"
                component={FormikDateComponent}
              ></Field>
              <Field
                name="insurance_status"
                label="Insurance Status"
                component={FormikInputComponent}
              ></Field>
              <Field
                name="warranty_end_dt"
                label="Warranty End Date"
                component={FormikDateComponent}
              ></Field>
            </Form.Group>

            <Button
              type="submit"
              size="medium"
              color="blue"
              className="CustomeBTN"
            >
              {" "}
              {params.submitButtonText}{" "}
            </Button>
            {savedItemId ? (
              <Notification
                id={savedItemId}
                notifySelector={getNotification}
                notifyDoneAction={setNotifyDone}
                type="save"
              />
            ) : null}
          </Form>
        )}
      />
    </Container>
  );
};

export default ItemsForm;
