import * as type from './types'


export function createDepartment(department) {
    return {
        type : type.CREATE_DEPARTMENT,
        payload : department,
        txn : 'initiated',
    };
}

export function savedDepartment(department) {
    return {
        type : type.SAVED_DEPARTMENT,
        payload : department,
        txn : 'success',
        diff : true
    };
}

export function editDepartment(department) {
    return {
        type : type.EDIT_DEPARTMENT,
        payload : department,
        txn : 'initiated',
    };
}

export function fetchDepartment() {
    return {
        type : type.FETCH_DEPARTMENT,
    };
}

export function fetchedDepartment(department) {
    return {
        type : type.FETCHED_DEPARTMENT,
        payload : department
    };
}


export function deleteDepartment(department) {
    return {
        type : type.DELETE_DEPARTMENT,
        payload: department
    };
}

export function deletedDepartment(msg) {
    return {
        type : type.DELETED_DEPARTMENT,
        payload : msg
    };
}

export function setNotifyDone(props) {
    return {
        type : type.NOTIFICATION_DONE_DEPARTMENT,
        payload: props
    };
}