const ImportBodyList=(props)=>{
    // console.log("props.file",props.file)

    const dispatch=useDispatch()

    const fileDownload=(values)=>{
      dispatch(downloadFile(values))
    }
    return(<Table.Row>
        <Table.Cell onClick={()=>{fileDownload(props.file.id)}} style={{color:"#0070C2",cursor:"pointer"}}>{props.file.org_name}</Table.Cell>
        <Table.Cell>{displayDate(props.file.dt_update)}</Table.Cell>
        <Table.Cell>{props.file.file_status}</Table.Cell>
        <Table.Cell>Delete</Table.Cell>
    </Table.Row>)
}

