const itemsSchema = new schema.Entity('items', {}, { idAttribute: 'id' });
const vdetailsSchema = new schema.Entity('tdetails', { items: itemsSchema }, { idAttribute: 'id' });
const voucherSchema = new schema.Entity('vouchers', { tdetails: [vdetailsSchema] }, { idAttribute: 'id' });
const voucherListSchema = [voucherSchema];

