//#region Saga for Create Voucher 

function* createVoucher() {
  yield takeEvery('CREATE_VOUCHER', saveVoucher)
}

function* saveVoucher(action) {
  const { response, error } = yield call(saveVoucherAPI, action.payload)
  console.log("Saga", action.payload)
  if (response) yield put(savedVoucher(action.payload, response.data))
  else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
  }
}

function saveVoucherAPI(data) {
  console.log("call")
  data.rlb_box_id=data.boxId
  delete data.boxId
  return axios.post(ASSET_URL + '/voucher/add', data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


