//#region Saga for Delete Voucher 

function* deleteVoucher() {
  yield takeEvery('DELETE_VOUCHER', saveDeleteVoucher)
}

function* saveDeleteVoucher(action) {
  const { response, error } = yield call(deleteVoucherAPI, action.payload)
  console.log(response, ' del-res')
  console.log(response.data, ' del-res-data')
  console.log(action.payload, 'del-actpayload')
  if (response) yield put(deletedVoucher({ id: action.payload.id, msg: response.data }))
  else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
  }
}

function deleteVoucherAPI(data) {
  let voucherId = data.id
  return axios.post(ASSET_URL + '/voucher/delete/' + voucherId, data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion

//#region Saga for List Tasks 

function* fetchVoucher() {

  yield takeEvery('FETCH_VOUCHER', requestVoucher)

}

function* requestVoucher(action) {
  let { data } = yield call(requestVoucherAPI, action.payload)
  yield put(fetchedVoucher(data))
}

function requestVoucherAPI(data) {
  data = merge({}, data, userACL.atFetch())
  console.log('==============fdata', data)
  return axios.post(ASSET_URL + '/voucher/list', data, { crossDomain: true })
}

