const sagaErrorMessage = (error, action) => {
  console.group("Saga Error:" + action.type)
  console.log(error)
  console.groupEnd()
}

export default function* voucherSaga() {
  yield all([
    createVoucher(),
    deleteVoucher(),
    fetchVoucher(),
    editVoucher(),
    createSaleVoucher(),
    editSaleVoucher(),
    fetchSaleVoucher(),
    // getDepreciationCost()

  ])
}  