import React from 'react';
import { Route, withRouter } from 'react-router';
import AssettypeList from "./components/assettypeList";
import AssettypeForm from "./components/assettypeForm";
import { Container } from 'semantic-ui-react'


const Assettype = () => {
    return (
        <Container>
            <Route path="/assettype/list" component={withRouter(AssettypeList)} />
            <Route path="/assettype/edit/:id" component={AssettypeForm} />
            <Route path="/assettype/create" component={AssettypeForm} />
        </Container>
    )
};

export default Assettype;

