import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  getDepartment,
  getDepartmentParams,
  getNotification,
} from "../data/selectors";
import {
  createDepartment,
  editDepartment,
  fetchDepartment,
  setNotifyDone,
} from "../data/actions";
import { departmentSchema, duplicateCheckDepartmentName } from "../data/model";
import { Container, Header, Form, Button } from "semantic-ui-react";
import { Formik, Field, Form as FormikForm } from "formik";
import { FormikInputComponent } from "../../../utilities/formUtils";
import userACL from "../../../store/access";
import Notification from "../../../utilities/notificationUtils";
// import LocationSelect from '../../location/components/locationSelect'
import v4 from "uuid";

// import UserDropdown from '../../users/components/userDropdown'

const DepartmentForm = (props) => {
  const department = useSelector((state) => getDepartment(state, props));
  const params = useSelector((state) => getDepartmentParams(state, props));
  const departmentList = useSelector((state) => state.department.byId);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetchDepartment());
  }, []);

  let x = Object.keys(departmentList).map((item) => {
    return departmentList[item].dept_name;
  });

  console.log("departList=======",x)

  const checkIfValueInUse = (valueToCheck) => {
    const existingValues = x;
    return existingValues.includes(valueToCheck);
  };

  const validate = (values) => {
    const errors = {};
    if (checkIfValueInUse(values.dept_name)) {
      errors.dept_name = "This Name Already Existing";
    }
    return errors;
  };

  const [savedDepartmentId, setSavedDepartmentId] = useState(false);

  const saveDepartment = (values, resetForm) => {
    if (props.match.path === "/department/create") {
      values.id = v4();
      userACL.atCreate(values);
      // values.segid = values.cid
      values.rlb_box_id = values.boxId;
      delete values.txnid;
      delete values.boxId;
      dispatch(createDepartment(values));
      setSavedDepartmentId(values.id);
      resetForm();
    }
    if (props.match.path === "/department/edit/:id") {
      userACL.atUpdate(values);
      // values.segid = values.cid
      values.rlb_box_id = values.boxId;
      delete values.txnid;
      delete values.boxId;
      dispatch(editDepartment(values));
      setSavedDepartmentId(values.id);

      props.history.push(`/department/list`);
    }
  };

  console.log("savedDepartmentId=======", savedDepartmentId);
  return (
    <Container>
      <Header as="h1">{params.title}</Header>
      <Formik
        id="departmentForm"
        size="large"
        width={5}
        initialValues={department}
        validationSchema={departmentSchema}
        validate={validate}
        onSubmit={(values, { resetForm }) => saveDepartment(values, resetForm)}
        render={({ handleSubmit, onChange, values, handleChange }) => (
          <Form
            as={FormikForm}
            size="small"
            className="CustomeForm"
            width={5}
            onSubmit={handleSubmit}
          >
            <Form.Group widths={3}>
              <Field
                name="dept_name"
                label="Name"
                component={FormikInputComponent}
                isMandatory={true}
                onChange={handleChange}
                // validate={(val) => duplicateCheckDepartmentName(val, values)}
              ></Field>
            </Form.Group>
            <Button
              type="submit"
              size="medium"
              color="blue"
              className="CustomeBTN"
            >
              {" "}
              {params.submitButtonText}{" "}
            </Button>
            {savedDepartmentId ? (
              <Notification
                id={savedDepartmentId}
                notifySelector={getNotification}
                notifyDoneAction={setNotifyDone}
                type="save"
              />
            ) : null}
          </Form>
        )}
      />
    </Container>
  );
};

export default DepartmentForm;
