import { combineReducers } from 'redux';
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr';

// Define a departments schema
const departmentSchema = new schema.Entity('departments',{}, { idAttribute: 'id' });
const departmentListSchema = [departmentSchema];

const defaultDepartmentParams = {
  departmentFetched: false,
  isFetching:false,
  departmentDeleted:false,
  // isDeleting:true,
  createTitle: 'Create New Department',
  createSubmitButtonText: 'Submit',
  editTitle: 'Edit Depreciation Department',
  editSubmitButtonText: 'Submit' 
}

const defaultMessages = {
  id: {
    save: {
      status: "",
      msg: "",
      isSaving: false
    },
    edit: {
      status: "",
      msg: "",
      isSaving: false
    },
    delete: {
      status: "",
      msg: "",
      isSaving: false
    }
  }
}

function byId(state = {}, action) {
    if (action.type === 'SAVED_DEPARTMENT') {
      const departmentID = action.payload.id;
      return {...state, 
        [departmentID]: action.payload
      }
    }
    if (action.type === 'FETCHED_DEPARTMENT') { 
        const normalizedDepartment = normalize(action.payload, departmentListSchema);
        return merge({},state,normalizedDepartment.entities.departments)
    }
    if (action.type === 'DELETED_DEPARTMENT') {
      if(action.payload.msg.type === "success"){
        const departmentID = action.payload.id;
        let finalState = {...state}
        delete finalState[departmentID]
        return finalState
      }else{
        return state
      }
    } else {
      return state
    }
  }

function params(state = defaultDepartmentParams, action) {
  if (action.type === 'FETCH_DEPARTMENT') { 
    return {...state, 
      departmentFetched: false,
      isFetching:true
    }
}
  if (action.type === 'FETCHED_DEPARTMENT') { 
      return {...state, 
        departmentFetched: true,
        isFetching:false
      }
  }

// if (action.type === 'DELETED_DEPARTMENT') { 
//   return {...state, 
//     departmentDeleted: true,
//   }
// }
   else {
    return state
  }
}


function notifications(state = {}, action) {
  if (action.type === 'SAVED_DEPARTMENT') {
    return {...state, 
      [action.payload.id]: { ...state[action.payload.id],
        save: { 
          status : action.txn === "success" ? 'success' : 'error',
          msg : action.txn
        }
      }
    }
  }
  if (action.type === 'DELETED_DEPARTMENT') { 
    return {...state, 
      [action.payload.id]: { ...state[action.payload.id],
        delete: { 
          status : action.payload.msg === "Success" ? 'success' : 'error',
          msg : action.payload.msg
        }
      }
    }
  } 
  if(action.type === 'NOTIFICATION_DONE_DEPARTMENT'){
    const {id, type} = action.payload
    // Remove the 'id' element from state
    const { [id]: idValue, ...restOfState } = state;
    // Remove the 'type' from the 'id' element
    const { [type]: removedValue, ...restOfId } = idValue;
    // Merge back together
    const finalState = { ...restOfState, [id]: restOfId };     
    return finalState

  } else {
    return state
  }
}

const department = combineReducers({
    byId,
    notifications,
    params
});

export default department;

