import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { fetchVtype } from '../data/actions'
import * as select from '../data/selectors'
import { Field } from 'formik'
import { FormikSelectComponent } from '../../../utilities/formUtils'
import { fetchItem } from '../../items/data/actions';
import { merge} from "lodash";
import userACL from "../../../store/access";

const VTypeSelect = ({ name, isTxn, isLabel, label, isSelection, isMandatory, focus, placeholder, validation, computeValue, index, fieldName, fromField, otherList, passFunct, classNameGet, getValue : callback }, props) => {


    const vTypeFetched = useSelector(state => select.getIsVtypeFetched(state, props))
    const options = useSelector(state => select.selectVType(state, props))
    const dispatch = useDispatch()
    console.log("vTypeFetched=========",options)


    useEffect(()=>{
        if (!vTypeFetched)
            dispatch(fetchVtype());
    },[]);  

    // useEffect(()=>{
    //     let values = merge({}, userACL.atFetch());
    //     values.nature=options.nature
    //     if(vTypeFetched){
    //         dispatch(fetchItem(values))
    //     }
    // },[])

    const getValue = (values) => {
        console.log("V Type Selected", values)
        if(passFunct){
            passFunct(values)
        }
    }


    // Loading Logic if tasks not fetched
    if (!vTypeFetched && options.length > 0 ){
        return <p>Loading</p>
    }  else {        
        return (
            <Field name={name} isLabel={isLabel} isTxn={isTxn} isMandatory={isMandatory} label={label} classNameGet={classNameGet} validation={validation} isSelection={isSelection} component={FormikSelectComponent} computeValue={computeValue} index={index} fieldName={fieldName} fromField={fromField} otherList={otherList} userProps={{ options, getValue }} focus={focus} placeholder={placeholder}> </Field>
        )
    }


}


export default VTypeSelect;
