import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'
import { fetchDepartment, deleteDepartment, setNotifyDone } from '../data/actions'
import * as select from '../data/selectors'
import { Container, Table, TableHeader, TableBody, Header, Modal, Button, Loader } from 'semantic-ui-react'
import userACL from '../../../store/access'
import Notification from '../../../utilities/notificationUtils'



const Department = props => {
    const dispatch = useDispatch();
    const isNegative = props.department.status === 1 ? false : true;
    const [isModalOpen, setModalOpen] = useState(false)
    const location = useSelector(state => state.location.byId)

    const [deleteDepartmentId, setDeleteDepartmentId] = useState(false);

    const deleteDepartmentFromList = (values) => {
        const getVlaue = { id: values }
        userACL.atUpdate(getVlaue)
        dispatch(deleteDepartment(getVlaue))
        setDeleteDepartmentId(values.id)
        setModalOpen(false)
    }

    const closeModal = () => {
        setModalOpen(false)
    }

    return (
        <Table.Row negative={isNegative}>
            <Table.Cell width={14}>{props.department.dept_name}</Table.Cell>
            {/* <Table.Cell>{props.department.loc_id !== null && props.department.loc_id !== undefined && props.department.loc_id !== "" ? location[props.department.loc_id].location : ""}</Table.Cell> */}
            <Table.Cell>
                {isNegative === false ?
                    <>
                        <Link to={"/department/edit/" + props.department.id}>Edit</Link><span>&nbsp;&nbsp;</span>
                        <span style={{ cursor: 'pointer', color: 'red' }} onClick={() => setModalOpen(true)} >Delete</span>
                    </>
                    :
                    <span>Deleted</span>
                }
                <>
                    {
                        !deleteDepartmentId ?
                            <Notification id={deleteDepartmentId} notifySelector={select.getNotification} notifyDoneAction={setNotifyDone} type='delete' />
                            :
                            <h5>Deleted</h5>

                    }
                </>
                <Modal open={isModalOpen} size='mini'>
                    <Modal.Header>Department List</Modal.Header>
                    <Modal.Content>
                        <h4>Do you want to delete this row??</h4>
                    </Modal.Content>
                    <Modal.Actions>
                        <Button type="button" positive onClick={() => closeModal()}>No</Button>
                        <Button type="button" color='red' onClick={() => deleteDepartmentFromList(props.department.id)}>Yes</Button>
                    </Modal.Actions>
                </Modal>

            </Table.Cell>
        </Table.Row>
    )
}

const DepartmentList = (props) => {

    const department = useSelector(state => state.department.byId)
    const departmentFiltered = useSelector(state => select.getFilteredDepartment(state))
    const fetchedDepartment = useSelector(state => state.department.params.departmentFetched)
    // const DepartmentDeleted = useSelector(state => state.department.params.departmentDeleted)
    const isFetching = useSelector(state => state.department.params.isFetching)
    const departmentFetched = useSelector(state => select.getIsDepartmentFetched(state, props))
    const dispatch = useDispatch();

    useEffect(() => {
        if(!departmentFetched)
        dispatch(fetchDepartment());
    }, []);

    const tableStyle = {
        // header: {
        //     position: "fixed",
        //     width: "100%",
        //     backgroundColor: "#fff"
        // },
        // tableHead: {
        //     overflowY: "auto",
        //     height: '580px',
        //     backgroundColor: "#fff"
        // },
        // table: {
        //     borderCollapse: "collapse",
        //     width: '100%',

        // },
        // th: {
        //     position: "sticky",
        //     backgroundColor: "#fff",
        //     top: "28px",
        //     zIndex: "1",
        //     padding: "25px 0"
        // }
    }

    // Loading Logic if tasks not fetched
    if (!departmentFetched) {
        return <Loader active inline="centered" style={{ marginTop: '150px' }}>Loading ... </Loader>
    } else {
        return (
            <Container style={tableStyle.tableHead}>
                <Header style={tableStyle.header} as='h1' align='left'>Department List</Header>
                <Table style={tableStyle.table} selectable basic='very'>
                    <TableHeader>
                        <Table.Row>
                            <Table.HeaderCell width={14} style={tableStyle.th}>Department Name</Table.HeaderCell>
                            <Table.HeaderCell style={tableStyle.th}>Action</Table.HeaderCell>
                        </Table.Row>
                    </TableHeader>
                    <TableBody>
                        {Object.keys(department).map((key) =>
                            <Department department={department[key]} key={department[key].id} />)}
                    </TableBody>
                </Table>
            </Container>
        )
    }
}

export default DepartmentList;
