import React, { useState, useEffect } from "react";
import { Container, Header, Form, Button } from "semantic-ui-react";
import { Formik, Field, Form as FormikForm } from "formik";
import {
  FormikInputComponent,
  FormikDateComponent,
} from "../../../utilities/formUtils";
import Notification from "../../../utilities/notificationUtils";
import { useSelector, useDispatch } from "react-redux";
import { getYear, getYearParams, getNotification } from "../data/selectors";
import {
  createYear,
  editYear,
  setNotifyDone,
  fetchYear,
} from "../data/actions";
import { yearSchema } from "../data/model";
import userACL from "../../../store/access";
import Depriciation from "./depriciation";

import v4 from "uuid";

const YearForm = (props) => {
  const year = useSelector((state) => getYear(state, props));
  const yearList = useSelector((state) => state.year.byId)
  const params = useSelector((state) => getYearParams(state, props));
  const [savedYearId, setSavedYearId] = useState(false);
  const [depStatus,setDepStatus]=useState("")
  const dispatch = useDispatch();
  // console.log("depStatus======",depStatus,savedYearId)

console.log("yearList========",yearList)


  const saveYear = (values, resetForm) => {
    console.log(values);
    if (props.match.path === "/year/create") {
      values.id = v4();
      userACL.atCreate(values);
      // values.segid = values.cid
      values.rlb_box_id = values.boxId;
      delete values.boxId;
      delete values.txnid;
      dispatch(createYear(values));
      setSavedYearId(values.id);
      resetForm();
    }
    if (props.match.path === "/year/edit/:id") {
      console.log(values.id);
      userACL.atUpdate(values);
      // values.segid = values.cid
      values.rlb_box_id = values.boxId;
      delete values.boxId;
      delete values.txnid;
      dispatch(editYear(values));
      setSavedYearId(values.id);
      setDepStatus(year.book_dep_status)
      console.log("depStatus==========",depStatus)
      setTimeout(function () {
        props.history.push("/year/list");
      }, 1000);
    }
  };

  useEffect(()=>{
    dispatch(fetchYear())
  },[])

  console.log("savedYearId======", savedYearId);

  return (
    <div>
      <Container>
        {console.log("year=======",year)}
        <Header as="h1">{params.title}</Header>
        <Formik
          id="yearForm"
          size="large"
          width={5}
          initialValues={year}
          validationSchema={yearSchema}
          onSubmit={(values, { resetForm }) => saveYear(values, resetForm)}
          render={({
            handleSubmit,
            onChange,
            values,
            handleChange,
            errors,
            touched,
          }) => (
            <Form
              as={FormikForm}
              size="small"
              className="CustomeForm"
              width={5}
              onSubmit={handleSubmit}
            >
              <Form.Group widths={3}>
                <Field
                  name="name"
                  component={FormikInputComponent}
                  isMandatory={true}
                ></Field>
                <Field
                  name="sname"
                  label="Short Name (Max 6 Characters)"
                  component={FormikInputComponent}
                  isMandatory={true}
                ></Field>
              </Form.Group>

              <Form.Group widths={3}>
                <Depriciation
                  name="book_dep"
                  isTxn="false"
                  isSelection="true"
                  label="Book Depreciation"
                  path={props.match.path}
                  isMandatory={true}
                  depStatus={year.book_dep_status}
                />
                <Depriciation
                  name="it_dep"
                  isTxn="false"
                  isSelection="true"
                  label="It Depreciation"
                  path={props.match.path}
                  isMandatory={true}
                  depStatus={year.it_dep_status}
                />
              </Form.Group>

              <Form.Group widths={3}>
                <Field
                  name="from_dt"
                  label="From Date"
                  component={FormikDateComponent}
                  isMandatory={true}
                ></Field>
                <Field
                  name="to_dt"
                  label="To Date"
                  component={FormikDateComponent}
                  isMandatory={true}
                ></Field>
              </Form.Group>

              {console.log(errors)}
              <Button
                type="submit"
                size="medium"
                color="blue"
                className="CustomeBTN"
              >
                {" "}
                {params.submitButtonText}
              </Button>
              {savedYearId !== false ? (
                <>
                  <Notification
                    id={savedYearId}
                    notifySelector={getNotification}
                    notifyDoneAction={setNotifyDone}
                    type="edit"
                  />
                </>
              ) : null}
              {console.log("Notification=====", setNotifyDone)}
            </Form>
          )}
        />
      </Container>
    </div>
  );
};

export default YearForm;
