import * as type from './types'


export function createYear(year) {
    return {
        type : type.CREATE_YEAR,
        payload : year,
        txn : 'initiated',
    };
}

export function savedYear(year) {
    return {
        type : type.SAVED_YEAR,
        payload : year,
        txn : 'success',
        diff : true
    };
}

export function editYear(year) {
    return {
        type : type.EDIT_YEAR,
        payload : year,
        txn : 'initiated',
    };
}

export function fetchYear() {
    return {
        type : type.FETCH_YEAR,
    };
}

export function fetchedYear(year) {
    return {
        type : type.FETCHED_YEAR,
        payload : year
    };
}

export function setNotifyDone(props) {
    return {
        type : type.NOTIFICATION_DONE_YEAR,
        payload: props
    };
}